<template>
  <div class="bugDetail wrapper pbt">
    <van-nav-bar title="设置" left-arrow @click-left="onClickLeft" fixed @click-right="onClickRight" right-text="保存" />
    <van-cell-group title="兼容设置">
      <van-cell title="ios适配" label="目前仅对焦点问题进行适配" center>
        <template #value>
          <div style="display:flex;justify-content:flex-end;">
            <van-switch v-model="checked" />
          </div>
        </template>
      </van-cell>
    </van-cell-group>
  </div>
</template>
<script>
import { Dialog, Toast } from 'vant';
export default {
  name: 'BugDetail',
  inject: ['actionShow', 'avatarGet'],
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    onClickRight() {
      Dialog.confirm({
        title: '保存设置',
      })
        .then(() => {
          Toast.loading({
            message: '保存中...',
            overlay: true,
          })
          if (!this.$store.state.setup)
            this.$store.state.setup = {};
          this.$store.state.setup.system = this.checked;
          this.$store.commit('setSetUp', this.$store.state.setup)
          Toast.clear();
        })
        .catch(() => {
        });
    }
  },
  mounted() {
    Toast.loading({ message: '加载设置中...', overlay: true, });
    this.checked = (this.$store.state.setup && this.$store.state.setup.system) ? true : false
    this.$nextTick(() => {
      Toast.clear();
    })
  },
  // beforeRouteLeave(to,from,next) {
  //   if (to.path === '/') {
  //     console.log(to.meta.needRefresh);
  //     to.meta.needRefresh = true;
  //     console.log(to.meta.needRefresh);
  //     next();
  //   }
  // }
}
</script>

