<template>
  <div class="bugDetail wrapper pbt">
    <van-nav-bar title="问题详情" left-arrow @click-left="onClickLeft" fixed />
    <van-loading size="24px" vertical color="#0094ff" v-show="loading" style="margin-top: 1rem;">加载中...</van-loading>
    <van-skeleton title avatar :row="4" v-show="loading" v-for="el in 3" :key="el" />
    <div class="bugItem bulk" v-if="!loading">
      <div class="content">
        <div class="top">
          <van-image class="avatar" width="1rem" height="1rem" fit="cover" radius="5px"
            :src="avatarGet(bugDetail.avatar)" />
          <div class="nameDateReport">
            <div nameDate>
              <div class="nickname p4b">{{bugDetail.nickname}}</div>
              <div class="time p5">{{before(bugDetail.createdAt)}}</div>
            </div>
            <div class="report">
              <van-button size="mini" color="transparent" @click="actionShow()">
                <template #icon>
                  <van-icon name="ellipsis" color="#aaa" size="0.5rem"></van-icon>
                </template>
              </van-button>
            </div>
          </div>
        </div>
        <div class="mid">
          <div class="text p3 van-multi-ellipsis--l3">
            {{bugDetail.text}}
          </div>
        </div>
        <div class="imgBox" v-if="bugDetail.imgs.length">
          <div class="square" v-for="(item,index) in bugDetail.imgs" :key="item">
            <van-image width="100%" height="100%" fit="cover" radius="5px"
              @click.stop='imgPreview(bugDetail.imgs,index)' lazy-load
              :src="this.$store.state.URL+'upload/bugImgs/'+item" />
          </div>
        </div>
        <van-divider />
      </div>
      <div class="commentBox">
        <p class="p3b" style="padding-bottom:.5rem">评论&nbsp;{{bugDetail.comment.length}}</p>
        <van-empty image="https://fastly.jsdelivr.net/npm/@vant/assets/custom-empty-image.png" image-size="80"
          description="谁会成为第一个评论的人呢" v-if="!bugDetail.comment.length" />
        <div class="content" v-for="el in bugDetail.comment" :key="el">
          <div class="top">
            <van-image class="avatar" width="1rem" height="1rem" fit="cover" radius="5px"
              :src="avatarGet(el.avatar)" />
            <div class="nameDateReport">
              <div nameDate>
                <div class="nickname p4b">{{el.nickname}}</div>
                <div class="time p5">{{before(el.date)}}</div>
              </div>
              <div class="report">
                <van-button size="mini" color="transparent" @click="actionShow()">
                  <template #icon>
                    <van-icon name="ellipsis" color="#aaa" size="0.5rem"></van-icon>
                  </template>
                </van-button>
              </div>
            </div>
          </div>
          <div class="mid">
            <div class="text p4 van-multi-ellipsis--l3 " @click="focus(el.nickname,el.userId)">
              <span v-if="el.targetName">回复<span class="p4b">{{el.targetName+':'}}</span></span>
              {{el.message}}
            </div>
          </div>
          <van-divider />
        </div>
      </div>
      
    </div>
    <div class="bottomFrame">
      <van-field left-icon="edit" placeholder="我来说说" @click="focus" disabled />
    </div>
    <van-popup v-model:show="show" position="top">
      <van-form @submit="onSubmit">
        <van-cell-group inset>
          <van-field v-model="message" rows="3" autosize type="textarea" :maxlength="word_limit" :placeholder='placeholder' 
          @update:model-value="inputChange" ref="commentInput"></van-field>
        </van-cell-group>
        <van-divider />
        <div class="btnBox">
          <van-button color="black" native-type="submit" size="small">
            发送
          </van-button>
        </div>
      </van-form>
    </van-popup>
  </div>
</template>
<script>

import { ImagePreview, Toast } from 'vant';
import dayjs from '@/assets/util/public'
export default {
  name: 'BugDetail',
  inject: ['actionShow','avatarGet'],
  data() {
    return {
      id: 0,
      bugDetail: [],
      loading: true,
      show: false,//弹出层
      message: '',
      word_limit: 200,
      isReply: false,
      targetId: 0,//被回复的用户id
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
    imgPreview(arr, index) {//图片预览
      arr = arr.map(el => {
        return this.$store.state.URL + 'upload/bugImgs/' + el;
      })
      ImagePreview({
        images: arr,
        startPosition: index,
      });
    },
    before(date) {//时间显示
      return dayjs(date).format('YYYY[年]MM[月]DD[日] HH:mm')
    },
    focus(name, id) {
      if (id) {
        this.isReply = true;
        this.placeholder = '回复 ' + name;
        this.targetId = id;
      }
      else {
        this.isReply = false;
        this.placeholder = '走心的评论更容易被看到哦'
        this.targetId = 0;
      }
      this.show = true;
      this.$nextTick(() => {
        this.$refs.commentInput.focus()
      })
    },
    onSubmit() {
      console.log(this.message);
      if (this.message.trim().length === 0) {
        Toast.fail('无意义内容！')
        return
      }
      Toast.loading({
        message: '提交中...',
        overlay: true,
        duration:0
      });
      const params = { userId: this.$store.state.user.userId, message: this.message,targetId:this.targetId,id:this.id}
      this.$axios.get('addCommentInBug', { params }).then(rs => {
        this.bugDetail.comment.unshift(rs)
        this.show = false;
        this.message = '';
        Toast.clear()
      })
    },
    inputChange(e) {
      if (e.length >= this.word_limit)
        Toast('字数已达上限，超出内容已隐藏')
    },
    async dataGet() {
      this.bugDetailGet();
    },
    async bugDetailGet() {
      this.$axios.get('bugListGet', { params: { id: this.id } }).then(rs => {
        this.bugDetail = rs;
        this.$nextTick(() => {
          setTimeout(() => {
            this.loading = false;
          }, 100)
        })
      })
    },
  },
  mounted() {
    const arr = this.$route.path.split('/')
    this.id = arr[arr.length - 1]
    this.dataGet();
  },

}
</script>

<style scoped lang="less">
.bugDetail {
  background: #f8f8f8;

  .btnBox {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 1rem;
    padding-bottom: 10px;
  }

  .bugItem {
    display: flex;
    flex-direction: column;

    .content {
      padding: .5rem;
      background: white;

      >div {
        padding: 2px 0;

      }

      .top {
        display: flex;
        align-items: center;

        div.nameDateReport {
          padding-left: .3rem;
          flex: auto;
          display: flex;
          justify-content: space-between;

          div.nameDate {
            display: flex;
            flex-flow: column;
          }

          div.report {
            display: flex;
            align-items: center;
          }
        }

      }


      .mid {
        padding-left: 1.3rem;
      }

      .imgBox {
        padding-left: 1.3rem;
        display: flex;

        .square {
          width: 2rem;
          height: 2rem;
          margin-right: 2px;
        }
      }
    }

    .commentBox {
      flex:auto;
      padding: .5rem;
      margin-top: 20px;
      background: white;

      .content {
        padding: 0;
      }
    }
  }

  .bottomFrame {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    bottom: 0;
    width: 100%;
    box-shadow: 0 0 2px 1px grey;

    :deep(input) {
      background: #f8f8f8;
    }
  }

}
</style>