<template>
    <div class="login wrapper pt">
        <van-nav-bar title="登录" fixed>
            <template #left>
                <van-button type="primary" icon="cross" to="/" color="white"></van-button>
            </template>
        </van-nav-bar>
        <van-form @submit="onSubmit">
            <van-cell-group inset>
                <van-field v-model="username" name="phoneNumber" label="手机号" placeholder="11位手机号"
                    :rules="[{ pattern, message: '请填写正确的11位手机号' }]" />
                <van-field v-model="password" type="password" name="password" label="密码" placeholder="密码"
                    :rules="[{ pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/, message: '密码必须包括字母和数字，且在6到18位之间' }]" />
            </van-cell-group>
            <div style="margin: 16px;">
                <van-button round block color="black" native-type="submit">
                    登录
                </van-button>
            </div>
        </van-form>
        <div style="display:flex;justify-content:space-between;padding:0 1rem;align-items:center;">
            <router-link to="" class="p6"><u @click="forget">忘记密码?</u></router-link>
            <router-link to="/register" class="p6"><u>没有账号? 去注册</u></router-link>
        </div>
    </div>
</template>

<script>
import qs from 'qs'
import { Toast } from 'vant';
export default {
    name: 'LoginView',

    data() {
        return {
            username: "",
            password: "",
            pattern: /(^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89]))|(886)\d{8}$/,
            activeNames: [],
        }
    },
    methods: {
        onSubmit() {
            Toast.loading({
                message: '尝试登录中',
                overlay: true,
                duration: 0,
            })
            let data = { phoneNumber: this.username, password: this.password }
            const params = qs.stringify(data)
            this.$axios.post('userLogin', params).then((rs) => {
                if (typeof (rs) === 'object') {
                    this.$store.commit('setUser', rs)
                    Toast.success({
                        message: '登录成功！',
                        onClose: this.onClose,
                        overlay: true,
                    })
                }
                else if (rs === 'error') {
                    Toast.fail({
                        message: '账号或密码错误！',
                    })
                }
                else if (rs === 'notExist') {
                    Toast.fail({
                        message: '用户不存在！'
                    })
                }
            })
            return false;
        },
        onClose() {
            if (this.$store.state.user.channels.channel.length === 0)
                this.$router.replace('/channelSelect')
            else {
                this.$router.replace('/')
            }
        },
        forget() {
            Toast({
                message: '还是联系一下管理员吧',
                position: 'bottom',
            })
        }

    },
    mounted() {

    },
}
</script>

<style scoped lang="less">
.van-form {
    margin-top: 100px;
}
</style>