<template>
  <div class="answerList wrapper pt">
    <van-nav-bar title="我的问题" left-arrow @click-left="onClickLeft" fixed />
    <div class="commentList bulk">
      <van-empty description="暂无回答" v-show="!list.length" />
      <div class="commentItem" v-for="el in list" :key="el.id">
        <router-link :to="'/questionDetail/' + el.id">
          <div class="author" style="padding-left:5px;">
            <van-image round width="0.8rem" height="0.8rem" fit="cover"
              :src="this.$store.state.URL + '/images/avatar/cat.jpeg'" />
            <div class="info">
              <span class="p5">一只猫</span>
              <span class="p7g">{{ before(el.date - 0) }}&nbsp;回答了你的问题</span>
            </div>
          </div>
          <div style="padding:5px">
            <p class="p6 van-multi-ellipsis--l2">
              {{ el.answer }}
            </p>
          </div>
          <div class="bottom" :style="{ 'padding': el.listenerName ? '5px' : '0' }">
            <div style="padding:5px" v-if="el.desc">
              <p class="p5 repliedMessage van-multi-ellipsis--l3">问题详情:<span class="p5g">{{
                  el.desc
              }}</span></p>
            </div>
            <div class="article" style="padding:5px">
              <div>
                <van-image width="1rem" height="1rem" fit="cover" round :src="avatarGet(this.$store.state.user.avatar)"
                  style='background:rgb(234, 234, 234)' />
              </div>
              <div class="articleInfo van-ellipsis">
                <span class="p6g">{{ this.$store.state.user.nickname }}</span>
                <span class="p6 van-ellipsis">{{ el.question}}</span>
              </div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AnswerList',
  inject: ['getId', 'before', 'avatarGet'],
  data() {
    return {
      list: []
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    }
  },
  mounted() {
    const userId = this.getId(this.$route.path);
    const params = { userId, type: 'answer' }
    this.$axios.get('getMessageNotice', { params }).then(rs => {
      console.log(rs);
      this.list = rs
    })
  },

}
</script>

<style scoped lang="less">
div.commentItem {
  padding: 10px;
  background: white;
  margin-top: 10px;

  >a>div {
    padding: 5px 0;
  }

  div.author {
    display: flex;
    align-items: center;

    div.info {
      display: flex;
      flex-direction: column;
      padding-left: 5px;
    }
  }

  div.bottom {
    display: flex;
    flex-direction: column;
    background: rgb(243, 243, 243);
    padding: 5px;

    .repliedMessage {
      box-sizing: border-box;
    }

    div.article {
      background: rgb(253, 253, 253);
      display: flex;
      align-items: center;

      div.articleInfo {
        display: flex;
        flex-direction: column;
        padding-left: 5px;
      }
    }
  }
}
</style>