import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Axios from 'axios'
import { Lazyload } from 'vant';
// import fastClick from "fastclick"
// fastClick.attach(document.body)
//方法
import util from './assets/util/public'

//样式
import './assets/css/base.css'
import './assets/css/public.css'
import './assets/css/font_3548490_l2ew7l5zof/iconfont.css'
//vant
import Vant from 'vant';
import 'vant/lib/index.css';
import 'amfe-flexible'

const app = createApp(App)

//axios1
const instance = Axios.create({
    // baseURL: 'http://192.168.1.107:1337/users/',
    baseURL: 'http://47.102.115.96:1337/users/',
    timeout: 15000
})

instance.interceptors.request.use(config => {
    //对config进行处理 请求拦截器
    return config
});

instance.interceptors.response.use(res => { //响应拦截
    // console.log(res);
    return res.data;
});

//axios2
const instance2 = Axios.create({
    // baseURL: 'http://192.168.1.107:1337/admins/',
    baseURL: 'http://47.102.115.96:1337/admins/',
    timeout: 15000
})

instance2.interceptors.request.use(config => {
    //对config进行处理 请求拦截器
    return config
});

instance2.interceptors.response.use(res => { //响应拦截
    // console.log(res);
    return res.data;
});
app.config.globalProperties.$axios = instance
app.config.globalProperties.$axios2 = instance2
app.config.globalProperties.$util = util
app.use(store).use(router).use(Vant).use(Lazyload).mount('#app')