<template>
    <div class="communityList " ref="communityListBulk">
        <!-- this.$refs.communityListBulk -->
        <van-pull-refresh v-model="refreshing" :head-height="80" :style="minHeight($refs.communityListBulk,true)" 
            @refresh="onRefresh">
            <van-list v-model:loading="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                <PostList :ArticleList="list"></PostList>
            </van-list>
        </van-pull-refresh>
    </div>
</template>
<script>
import PostList from '@/components/PostList.vue'
import { Toast } from 'vant';
export default {
    name: 'CommunityList',
    inject:['minHeight'],
    props: {
        params: {
            type: Object,
        }
    },
    components: {
        PostList,
    },
    data() {
        return {
            loading: false,
            finished: false,
            size: this.$store.state.size,
            // size: 1,
            skip: 0,
            list: [],
            refreshing: false,
            loaderArr:[],
        }
    },
    methods: {
        onRefresh() {
            if (this.loading === true) {
                Toast.allowMultiple()
                Toast.fail({
                    overlay: true,
                    message: '不要在加载时刷新!'
                })
                this.refreshing = false;
                return
            }
            this.loadedArr = this.list.map(el => {
                return el.articleId;
            })
            this.loading = true;
            this.finished = false;
            this.onLoad();
        },
        onLoad() {
            let params;
            if (this.refreshing)
                params = {
                    ...this.params, skip: this.skip, size: this.size, URL: this.$store.state.URL, loadedArr: this.loadedArr
                };
            else
                params = { ...this.params, skip: this.skip, size: this.size, URL: this.$store.state.URL };
            this.$axios.get('articlesList', { params }).then(rs => {
                setTimeout(() => {
                    if (this.refreshing) {
                        this.list = [...rs, ...this.list];
                        this.loading = false;
                        this.refreshing = false;
                        Toast({
                            position: 'bottom',
                            message:rs.length? '新增了' + rs.length + '条内容':'已经空了',
                        });
                    }
                    else {
                        this.list = [...this.list, ...rs];
                        this.loading = false;
                    }
                    if (rs.length < this.size) {
                        this.finished = true;
                    }
                    else {
                        this.skip = this.skip + this.size
                    }
                }, 500);
            })
        }
    },
    mounted() {
    },
}
</script>

