<template>
    <div class="history wrapper pt">
        <van-nav-bar title="我的历史" left-arrow @click-left="onClickLeft" fixed />
        <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">加载中...</van-loading>
        <div v-show="!loading" class="bulk" id="bulk" ref="bulk">
            <van-empty description="暂无浏览记录" v-if='!list.length' />
            <van-cell-group :title="item" v-for="item in time" :key="item" v-else>
                <div v-for="el in list" :key="el.articleId">
                    <van-cell center clickable v-if="show(item, el.date)" :to="'/articleHome/' + el.articleId">
                        <template #icon>
                            <van-button :type="el.isSubmit ? 'primary' : 'success'" size="mini">{{ el.isSubmit ? '头条' :
                                    '社区'
                            }}
                            </van-button>
                        </template>
                        <template #value>
                            <p class="p5 van-ellipsis" style="padding-left:5px">
                                {{ el.title }}
                            </p>
                        </template>
                    </van-cell>
                </div>
            </van-cell-group>
        </div>
    </div>
</template>
<script>
import dayjs from '@/assets/util/public'
export default {
    name: 'HistoryView',
    data() {
        return {
            myHistory: [],
            aught: 0,
            now: 0,
            DayBefore: 0,
            yesterdayAught: 0,
            list: [],
            time: ['今天', '昨天', '前天'],
            loading: true,
            scrollTop: 0,
        }
    },
    methods: {
        show(item, date) {
            if (item === '今天' && date > this.aught && date < this.now)
                return true
            else if (item === '昨天' && date > this.yesterdayAught && date < this.aught)
                return true
            else if (item === '前天' && date > this.DayBefore && date < this.yesterdayAught)
                return true
            else
                return false
        },
        onClickLeft() {
            this.$router.back()
        },
        async historyGet() {
            this.aught = dayjs(dayjs().format('YYYY/MM/DD')).valueOf()//今天零点
            this.now = dayjs().valueOf()//现在
            this.DayBefore = this.aught - 1000 * 3600 * 24 * 2 //前天零点的时间戳
            this.yesterdayAught = this.aught - 1000 * 3600 * 24  //昨天零点
            const params = { userId: this.$store.state.user.userId }
            await this.$axios.get('historyGet', { params }).then(rs => {//获取历史
                this.myHistory = rs[0];
                this.list = rs[1].filter(el => { return el });
            })
            // 删除三天前的历史
            this.myHistory = this.myHistory.filter(el => {
                return el.date > this.yesterdayAught
            })
            const params1 = { userId: this.$store.state.user.userId, arr: this.myHistory };
            await this.$axios.get('historyUpdate', { params: params1 })
            this.loaded()
        },
        loaded() {
            if (this.loading === true)
                setTimeout(() => {
                    this.loading = false;
                }, 500)
        },
        // push() {
        //     if (!history.state.title) {//有了就不要再push了!
        //         let state1 = {
        //             title: "title",
        //             url: 'http://www.maomao365.com',
        //         }
        //         history.pushState(state1, 'title')
        //     }
        // },
    },
    mounted() {
        this.historyGet();
    },
    activated() {
        if (this.$route.meta.isBack !== true) {//回到你原来的模样
            Object.assign(this.$data, this.$options.data())
            this.historyGet();
        }
        else {
            this.$refs.bulk.scrollTop = this.scrollTop;
        }
    },
    beforeRouteEnter(to, from, next) {
        if (from.path.indexOf('/article') !== -1) { to.meta.isBack = true; }
        next()
    },
    beforeRouteLeave() {
        this.scrollTop = document.querySelector("#bulk").scrollTop
    }
}
</script>

