<template>
    <div class="wangEditor wrapper">
        <div class="progress" v-show="upLoadProgress !== 0">
            <van-loading type="spinner" color="#1989fa" size="24px" v-if="upLoadProgress < 100"><span
                    class="p5">图片上传中</span></van-loading>
            <div v-else class="icon">
                <van-icon name="checked" color="green" size="24px" />
                <span class="p5">上传完成{{ fileCount ? '(' + fileCount + ')' : null }}</span>
            </div>
            <van-progress :percentage="upLoadProgress" style="width:100%;" />
        </div>
        <div>
            <Editor v-model="html" :defaultConfig="editorConfig" :mode="mode" class="editor p4" @onCreated="onCreated"
                @onFocus="onFocus" @onBlur='onBlur' />
        </div>
        <van-popup v-model:show="show" :overlay="false" :lockScroll="false"
            :position="(this.$store.state.setup && this.$store.state.setup.system) ? 'top' : 'bottom'">
            <Toolbar class="toolbar" :editor="editor" :defaultConfig="toolbarConfig" v-show="focusing" :mode="mode" />
        </van-popup>
    </div>
</template>

<script>
import '@wangeditor/editor/dist/css/style.css'
import { Editor, Toolbar } from '@wangeditor/editor-for-vue'
import axios from 'axios'
import {Toast} from 'vant'

export default ({
    components: { Editor, Toolbar },
    props: {
        submitOrPost: {
            type: String,
        }
    },
    data() {
        return {
            focusing: true,
            windowHeight: window.innerHeight,
            originHeight: window.innerHeight,
            editor: null,
            html: "",
            toolbarConfig: {
                // "insertLink",
                toolbarKeys: ["emotion", 'uploadImage', "header3", 'bold', "blockquote", "undo", "redo", "bulletedList"],
            },
            editorConfig: {
                placeholder: this.submitOrPost === 'submit' ? '投稿的内容审核后可见' : '发布到合适的社区，会有更多曝光机会，错误选择社区会被处理', autoFocus: false, scroll: false, hoverbarKeys: {
                    'link': {
                        menuKeys: ['editLink', 'viewLink'],
                    },
                    'image': {
                        menuKeys: ["imageWidth100", "deleteImage"],
                    }
                }, MENU_CONF: {
                    insertLink: {
                        checkLink: this.customCheckLinkFn,
                    },
                    uploadImage: {
                        customUpload: this.uploadImage
                    }
                }
            },
            mode: 'simple', // or 'simple',
            upLoadProgress: 0,
            fileCount: 0,
            show: false,
        }
    },
    methods: {
        onCreated(editor) {
            this.editor = Object.seal(editor) // 一定要用 Object.seal() ，否则会报错
            // console.log('所有key:', editor.getAllMenuKeys());
            // console.log('单个配置:', editor.getMenuConfig('bulletedList'));
            // console.log('编辑器配置:', editor.getConfig());
        },
        onFocus() {
            this.show = true;
        },
        onBlur() {
            this.show = false;
        },
        //链接开头校验
        customCheckLinkFn(text, url) {
            if (url.indexOf('http') !== 0) {
                alert('链接必须以 http/https 开头!')
                return '链接必须以 http/https 开头'
            }
            return true
        },
        //上传图片
        async uploadImage(file, insertFn) {
            if (this.$store.state.imageType.indexOf(file.type)=== -1) {
                Toast.fail('格式不支持!')
                return
            }
            this.fileCount++;
            const key = this.submitOrPost
            let data = new FormData()
            data.append(key, file)
            let src = '';
            await axios({
                url: this.$store.state.URL + "users/" + key + 'Upload',
                method: 'post',
                data,
                onUploadProgress: (progressEvent) => { //原生获取上传进度的事件
                    if (progressEvent.lengthComputable) {
                        //属性lengthComputable主要表明总共需要完成的工作量和已经完成的工作是否可以被测量
                        //如果lengthComputable为false，就获取不到progressEvent.total和progressEvent.loaded
                        this.upLoadProgress = parseInt(progressEvent.loaded / progressEvent.total * 100) //实时获取上传进度
                    }
                }
            }
            ).then(rs => {
                src = rs
                let url = this.$store.state.URL + 'upload/' + key + '/' + src.data
                // console.log('即将插入图片:',url);
                insertFn(url)
            })
            // console.log('上传成功,图片名为:', src);
        },
    },
    mounted() {
    },
    beforeUnmount() {
        const editor = this.editor
        if (editor == null) return
        editor.destroy() // 组件销毁时，及时销毁编辑器
    },
})
</script>
<style scoped lang="less">
.wangEditor {
    .progress {
        display: flex;
        align-items: center;
        height: 30px;
        flex-flow: column;
        padding: 10px 0;

        .icon {
            display: flex;
            align-items: center;
        }
    }
}

div.toolbar {
    height: 2rem;

    :deep(.w-e-toolbar) {}

    :deep(.w-e-bar-item button::after) {
        content: none !important;
    }

    :deep(.w-e-bar-item button::before) {
        content: none !important;
    }
}


:deep(.w-e-bar-item .w-e-drop-panel) {
    height: 1rem !important;
    margin-top: 1rem !important;
    overflow: auto;
}
</style>
