<template>
    <div class="submit pt wrapper">
        <van-nav-bar title="文章投稿" left-arrow fixed @click-left="onClickLeft">
            <template #right>
                <span style="margin-left: 1rem;" @click.stop="uploadSend">{{ this.$route.query.articleId ? '确认编辑' :
                        '发送'
                }}</span>
            </template>
        </van-nav-bar>
        <div class="bulk" ref="bulk" id="bulk">
            <van-cell-group>
                <van-cell>
                    <template #title>
                        <div v-for="el in selectedCom" :key="el.channelId">
                            <van-button type="primary" size="mini" @click="showPop">{{ el.channelName }}</van-button>
                        </div>
                        <van-button icon="plus" type="primary" size="mini" @click="showPop"
                            v-show="selectedCom.length === 0">
                            选择频道</van-button>
                    </template>
                </van-cell>
                <van-popup v-model:show="show" position="right" :style="{ width: '100%', height: '100%' }" class="pop">
                    <van-nav-bar title="选择频道" left-arrow @click-left="hidPop">
                        <template #left>
                            <van-icon name="cross" size="18" />
                        </template>
                    </van-nav-bar>
                    <ChannelEdit :my="myChannels" :isSelect="true" ref="headerChild" @hidPop="hidPop"></ChannelEdit>
                </van-popup>
                <van-cell>
                    <template #value>
                        <van-field v-model="title" placeholder="标题" />
                    </template>
                </van-cell>
            </van-cell-group>
            <van-cell title="请选择封面:" clickable size="large" ref="test">
                <template #value>
                    <van-uploader v-model="fileList" :max-count="1" :after-read="afterRead" name="avatar"
                        :before-read="beforeRead" :preview-size="[160, 90]" :max-size="8000 * 1024"
                        @oversize="onOversize" />
                </template>
            </van-cell>
            <WangEditor ref="editor" submitOrPost="submit"></WangEditor>
        </div>
        <van-popup v-model:show="cutShow" position="right" :style="{ width: '100%', height: '100%' }">
            <ImageCut :type="type" :src="src" @cutConfirm="cutConfirm" @cutCancel="cutCancel" :key="key"
                @loaded="cutLoaded"></ImageCut>
        </van-popup>
        <van-dialog v-model:show="dialogShow" title="标题" show-cancel-button @cancel="allowExit = false"
            @confirm="allowExit = true;">
            <img src="https://fastly.jsdelivr.net/npm/@vant/assets/apple-3.jpeg" />
        </van-dialog>
    </div>
</template>
<script>
import { Toast } from 'vant';
import { Dialog } from 'vant';
import ChannelEdit from '@/components/ChannelEdit.vue';
import WangEditor from '@/components/WangEditor.vue';
import ImageCut from '@/components/ImageCut.vue'
import qs from 'qs';
export default {
    name: 'SubmitView',
    inject: ['reload'],
    components: {
        ChannelEdit,
        WangEditor,
        ImageCut
    },
    data() {
        return {
            cutShow: false,//图片剪切的弹出
            show: false,//频道选择的弹出
            src: '',//传给子组件的src
            title: '',
            fileList: [{ url: '' },],
            myChannels: [],
            selectedCom: [],
            key: 1,//子组件的key
            cover: ' ',
            dialogShow: false,
            allowExit: false,
            saved: false,
            draftCreatedAt: 0,
        }
    },
    methods: {
        hidPop() {
            this.show = false;
            this.selectedCom = this.$refs.headerChild.selectedCom
        },
        showPop() {
            this.show = true;
        },
        cutLoaded() {
            Toast.clear();
        },
        cutConfirm(data) {//剪切确认
            this.fileList[0].content = data;
            this.src = data;
            this.cutShow = false;
        },
        cutCancel() {//剪切取消
            this.cutShow = false;
            this.fileList.length = 0
        },
        onOversize() {
            Toast('文件大小不能超过 8M!');
        },
        onClickLeft() {
            this.$router.back();
        },
        afterRead(file) {
            this.key = this.reload();//组件重渲染
            let arr = file.content.split(",");
            this.type = arr[0].match(/:(.*?);/)[1];
            this.src = file.content;
            this.cutShow = true;
        },
        beforeRead(file) {
            if (this.$store.state.imageType.indexOf(file.type) !== -1) {
                Toast.loading({
                    message: '处理中...',
                    overlay: true,
                    duration: 0
                });
                return true;
            }
            else {
                Toast('您选择的格式不支持或选择了多张图片！');
                return false;
            }
        },
        base64toFile(dataurl, filename) {
            let arr = dataurl.split(",");
            let mime = arr[0].match(/:(.*?);/)[1];
            let suffix = mime.split("/")[1];
            let bstr = atob(arr[1]);
            let n = bstr.length;
            let u8arr = new Uint8Array(n);
            while (n--) {
                u8arr[n] = bstr.charCodeAt(n);
            }
            return new File([u8arr], `${filename}.${suffix}`, {
                type: mime
            });
        },
        async uploadSend() {
            if (this.selectedCom.length === 0 || this.title === '' || this.$refs.editor.html === '' || this.$refs.editor.html === `<p><br></p>`)
                Toast.fail('存在未填写字段！');
            else {
                Toast.loading({
                    message: '提交中...',
                    forbidClick: true,
                    duration: 0,
                    overlay: true,
                });
                if (this.fileList.length !== 0) {
                    if (this.src !== 'str') {
                        const data = new FormData();
                        data.append('cover', this.base64toFile(this.src, 'file'));
                        await this.$axios.post('coverUpload', data).then(rs => {
                            this.cover = rs
                        })
                    }
                }
                else
                    this.cover = ' ';
                const authorId = this.$store.state.user.userId;
                const channelId = this.selectedCom[0].channelId
                const title = this.title
                const content = this.$refs.editor.html
                let params = { authorId, channelId, title, cover: this.cover, content }
                let message = '投稿成功，等待审核';
                if (this.$route.query.articleId) {
                    message = '编辑成功！'
                    params = { ...params, articleId: this.$route.query.articleId }
                    params = qs.stringify(params);
                    await this.$axios.post('articleEdit', params)
                }
                else {
                    params = qs.stringify(params);
                    await this.$axios.post('articleSubmit', params)
                }
                this.saved = true;//投稿成功表示保存了
                Toast.clear();
                Toast({
                    message,
                    forbidClick: true,
                    onClose: () => {
                        this.$router.back()
                    }
                })
            }
        },
        dataGet() {
            this.fileList = [];
            const params = { userId: this.$store.state.user.userId }
            this.$axios.get('chaListShow', { params }).then(rs => {
                this.myChannels = rs[0];
            });
        },
    },
    mounted() {
        this.dataGet()
        if (this.$route.query.articleId) {//编辑
            Toast.loading({
                message: '数据获取中',
                overlay: true,
                duration: 0
            })
            const params = { articleId: this.$route.query.articleId, URL: this.$store.state.URL }
            this.$axios.get('articleItemGet', { params }).then(rs => {
                this.title = rs[0].title;
                this.selectedCom[0] = rs[2];
                this.$refs.editor.html = rs[0].content;
                if (rs[0].cover !== ' ') {
                    this.fileList[0] =
                        { url: this.$store.state.URL + 'upload/cover/' + rs[0].cover };
                    this.cover = rs[0].cover;
                    this.src = 'str';
                }
                Toast.clear()
            })
        }
        if (this.$route.query.createdAt) {
            Toast.loading({
                message: '数据获取中',
                overlay: true,
                duration: 0
            })
            const params = { createdAt: this.$route.query.createdAt, URL: this.$store.state.URL, userId: this.$store.state.user.userId }
            this.$axios.get('draftListGet', { params }).then(rs => {
                this.draftCreatedAt = rs.createdAt;
                this.title = rs.title;
                if (rs.channelId) {
                    this.selectedCom= rs.channelId;
                }
                this.$refs.editor.html = rs.content;
                if (rs.cover !== ' ') {
                    this.fileList[0] =
                        { url: this.$store.state.URL + 'upload/cover/' + rs.cover };
                    this.cover = rs.cover;
                    this.src = 'str';
                }
                Toast.clear()
            })
        }
    },
    beforeRouteLeave(to, from, next) {
        if (!this.$route.query.articleId && this.$refs.editor.html !== '' && this.$refs.editor.html !== `<p><br></p>` && !this.saved) {
            setTimeout(() => {
                Dialog.confirm({
                    title: '是否保存到草稿箱',
                })
                    .then(async () => {
                        Toast.loading({
                            message: '请求中',
                            overlay: true,
                            duration: 0,
                        })
                        if (this.fileList.length !== 0) {
                            if (this.src !== 'str') {
                                const data = new FormData();
                                data.append('cover', this.base64toFile(this.src, 'file'));
                                await this.$axios.post('coverUpload', data).then(rs => {
                                    this.cover = rs
                                })
                            }
                        }
                        else
                            this.cover = ' ';
                        const authorId = this.$store.state.user.userId;
                        const channelId = this.selectedCom
                        const title = this.title
                        const content = this.$refs.editor.html
                        let params = { authorId, channelId, title, cover: this.cover, content, createdAt: this.draftCreatedAt }
                        params = qs.stringify(params);
                        await this.$axios.post('draftSave', params).then(res => {
                            console.log(res);
                            Toast.clear();
                            Toast.success({
                                message: '保存成功!',
                                onClose: next
                            })
                        })

                    })
                    .catch(() => {
                        // on cancel
                        next(true)
                    });

            }, 1)
        }
        else
            next(true)
    }
}
</script>

<style scoped lang="less">
.submit {
    // padding-bottom: 40px;

    :deep(.van-cell__title, van-cell__value) {
        display: flex;
        align-items: center;
    }

    .van-cell {
        padding: 5px;
    }
}
</style>