<template>
    <div class="profileEdit wrapper pt">
        <van-nav-bar title="资料修改" left-arrow fixed @click-left="onClickLeft">
            <template #right>
                <van-icon name="success" size="18" @click="save" />
            </template>
        </van-nav-bar>
        <van-cell-group>
            <van-cell title="头像" clickable size="large" center>
                <template #value>
                    <van-uploader v-model="fileList" :max-count="1" :after-read="afterRead" name="avatar"
                        :before-read="beforeRead" />
                </template>
            </van-cell>
            <van-field v-model="nickname" label="设置昵称" placeholder="请输入昵称" input-align="right" maxlength='12' />
            <van-field v-model="sex" label="设置性别" placeholder="请设置性别" input-align="right" readonly @click="showPopup" />
            <van-field v-model="signature" label="设置签名" placeholder="请输入签名" rows="1" autosize type="textarea" />
            <van-popup v-model:show="show" position="bottom" round>
                <van-picker title="性别修改" :columns="columns" @confirm="onConfirm" @cancel="hidden" item-height=".7rem"
                    visible-item-count='5' />
            </van-popup>
        </van-cell-group>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    name: 'ProfileEdit',
    inject: ['avatarGet'],
    data() {
        return {
            src: '',
            sex: '',
            signature: '',
            nickname: '',
            show: false,
            columns: ['男', '女'],
            fileList: [{ url: this.$store.state.URL + '/images/avatar/cat.jpeg' },],
            avatar: 0,
            avatarChanged: false,
            avatarPath: '',
        }
    },
    methods: {
        onClickLeft() {
            this.$router.back();
        },
        showPopup() {
            this.show = true
        },
        hidden() {
            this.show = false
        },
        onConfirm(value) {
            this.sex = value;
            this.hidden();
        },
        afterRead(file) {
            this.avatar = file;
            this.avatarChanged = true;
        },
        beforeRead(file) {
            if (file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/webp') {
                return true;
            }
            else {
                Toast('格式不支持');
                return false;
            }
        },
        async save() {
            if (this.fileList.length === 0 || this.nickname === '') {
                Toast.fail('信息不全');
            }
            else {
                const toast1 = Toast.loading({
                    message: '提交中...',
                    forbidClick: true,
                    duration: 0,
                    overlay: true,

                });
                if (this.avatarChanged === true) {
                    const data = new FormData()
                    console.log(this.avatar.file);
                    data.append('avatar', this.avatar.file);
                    await this.$axios.post('avatarUpload', data).then(rs => {
                        this.avatarPath = rs;
                    })
                }
                const params = { avatarPath: this.avatarPath, nickname: this.nickname, signature: this.signature, sex: this.sex, userId: this.$store.state.user.userId }
                this.$axios.get('userDataUpdate', { params }).then(rs => {
                    console.log('修改了' + rs);
                    toast1.clear()
                    Toast.success({
                        message: '提交成功',
                        forbidClick: true,
                        onClose: () => {
                            this.$router.replace('/my')
                        }
                    })
                })

            }

        }
    },
    mounted() {
        const params = { userId: this.$store.state.user.userId }
        this.$axios.get('userDataShow', { params }).then(rs => {
            this.fileList[0].url = this.avatarGet(rs.avatar);
            this.nickname = rs.nickname;
            this.signature = rs.signature;
            this.sex = rs.sex;
        })

    }
}
</script>

