<template>
    <div class="article wrapper pt">
        <van-nav-bar title="文章详情" left-text="返回" left-arrow @click-left="onClickLeft" fixed :right-text="managing?'':'审核'"
            @click-right="show = true" />
        <van-action-sheet v-model:show="show" :actions="actions" @select="onSelect" cancel-text="取消"
            close-on-click-action />
        <div>
            <ArticleItem :ArticleId="articleId" v-if="articleId !== 0" ref="item" @articleState="articleState">
            </ArticleItem>
        </div>
    </div>
</template>

<script>
import ArticleItem from '@/components/ArticleItem.vue'
import { Toast } from 'vant';
import { Dialog } from 'vant';
export default {
    name: 'ArticleView',
    components: {
        ArticleItem,
    },
    data() {
        return {
            articleId: 0,
            show: false,
            actions: [
                { name: '打回' },
                { name: '通过' },
            ],
            managing:true,
        }
    },
    methods: {
        articleState(data){
            this.managing=data;
        },
        onClickLeft() {
            this.$router.replace({path:'/articlesList',query:{managing:this.managing}})
        },
        onSelect(item) {
            if (item.name === '打回')
                Dialog.confirm({
                    title: '打回投稿',
                    message:
                        '确定打回此投稿吗?',
                })
                    .then(() => {
                        const params = { articleId: this.articleId, action: 'No' }
                        this.$axios2.get('auditAct', { params }).then(res => {
                            console.log(res);
                            this.$router.back()
                            Toast(item.name);
                        })
                    })
                    .catch(() => {
                    });
            else
                Dialog.confirm({
                    title: '审核通过',
                    message:
                        '确定此投稿合格给予通过吗?',
                })
                    .then(() => {
                        const params = { articleId: this.articleId, action: 'Yes' }
                        console.log(params);
                        this.$axios2.get('auditAct', { params }).then(res => {
                            console.log(res);
                            // this.$router.back({ path: '/articlesList', query: { managing: this.managing } })
                            this.$router.back()
                            Toast(item.name);
                        })
                    })
                    .catch(() => {
                    });
        }
    },
    mounted() {
        const arr = this.$route.path.split('/')
        this.articleId = parseInt(arr[arr.length - 1])
    },
}
</script>

