<template>
    <div class="tabBar ">
        <van-nav-bar fixed>
            <template #right>
                <van-button icon="search" to="/search" color="transparent" />
                <!-- <van-button icon="chat-o" @click="developing" color="transparent" /> -->
            </template>
        </van-nav-bar>
        <van-tabbar v-model="active" route>
            <van-tabbar-item icon="wap-home" to="/">首页</van-tabbar-item>
            <van-tabbar-item icon="friends" to="/community">社区</van-tabbar-item>
            <van-tabbar-item icon="add" @click="showPopup" class="add" v-if="!isAdmin"></van-tabbar-item>
            <van-tabbar-item icon="add" to="/admin" v-else>管理</van-tabbar-item>
            <van-tabbar-item icon="chat" to="/messageNotice">
                <span>消息</span>
                <template #icon>
                    <van-icon name="chat" :dot="newNotice" />
                </template>
            </van-tabbar-item>
            <van-tabbar-item icon="manager" to="/my">我的</van-tabbar-item>
        </van-tabbar>
        <van-action-sheet v-model:show="show" cancel-text="取消" title="发布" close-on-click-action>
            <template #default>
                <van-grid clickable :column-num="3" :border="false">
                    <van-grid-item icon="notes-o" text="文章投稿" to="/submit" @click="show = false" />
                    <van-grid-item icon="tv-o" text="视频投稿" @click="developing" />
                    <van-grid-item icon="description" text="帖子/动态" to="/post" @click="show = false" />
                </van-grid>
            </template>
        </van-action-sheet>
    </div>
</template>
<script>
export default {
    name: 'TabBar',
    inject: ['developing', 'toLogin'],
    data() {
        return {
            active: -1,
            show: false,
            isAdmin: false,
            newNotice: false,
        }
    },
    methods: {
        showPopup() {
            if (this.$store.state.user)
                this.show = true
            else
                this.toLogin()
        },
        hidden() {
            this.show = false
        },
        init() {
            if (this.$store.state.user) {
                // 检查并更新userDone
                this.$axios.get('userDoneUpdate', { params: { userId: this.$store.state.user.userId } })
                this.$axios.get('getMessageNotice', { params: { userId: this.$store.state.user.userId } }).then(rs => {
                    if (rs.newNotice===true)
                        this.newNotice = rs.newNotice
                })
                if (this.$store.state.user.userId === 1000)
                    this.isAdmin = true
            }
        }
    },
    mounted() {//每次tab切换都会执行  缓存后变成每个各执行一次
        // console.log('用户信息:', this.$store.state.user);
    },
    activated() {
        this.init()
    },
    deactivated() {
        this.newNotice = false;
    },

}
</script>
<style scoped lang="less">
:deep(.add i) {
    font-size: 40px;
    color: red;
}
</style>