<template>
    <div class="register wrapper pt">
        <van-nav-bar title="注册" fixed @click-left="onClickLeft" left-arrow>
        </van-nav-bar>
        <van-form @submit="onSubmit">
            <van-cell-group inset>
                <van-field v-model="phoneNumber" name="phoneNumber" label="手机号" placeholder="11位手机号"
                    :rules="[{ pattern, message: '请填写正确的11位手机号' }]" />
                <van-field v-model="password" type="password" name="password" label="密码" placeholder="密码"
                    :rules="[{ pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/, message: '密码必须包括字母和数字，且在6到18位之间' }]" />
            </van-cell-group>
            <div style="margin: 16px;">
                <van-button round block type="primary" native-type="submit">
                    提交
                </van-button>
            </div>
        </van-form>
        <div style="display:flex;justify-content:space-between;padding:0 1rem;align-items:center;">
            <router-link to="/login" class="p6" style="color:#1989fa;"><u>已有账号? 去登录</u></router-link>
        </div>
    </div>
</template>

<script>
import qs from 'qs'
import { Toast } from 'vant';
export default {

    name: 'LoginView',
    data() {
        return {
            phoneNumber: "",
            password: "",
            pattern: /(^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89]))|(886)\d{8}$/,
        }
    },
    methods: {
        onClickLeft() {
            this.$router.back()
        },
        onSubmit(value) {
            let params = qs.stringify(value);
            this.$axios.post('userRegister', params).then(rs => {
                if (rs === 1) {
                    Toast.fail('用户已存在！请直接登录');
                }
                else if (rs === 2) {
                    Toast.success({
                        duration: 2000,
                        message: '注册成功！',
                        onClose: this.onClose
                    })
                }
            })
            return false;
        },
        onClose() {
            this.$router.replace('/login')
        }

    },
    mounted() {

    }

}
</script>

<style scoped lang="less">
.van-form {
    margin-top: 100px;
}
</style>