<template>
    <div class="search wrapper">
        <van-search v-model="value" placeholder="请输入搜索关键词" @search="onSearch(value)"  clear-trigger="always"
            @clear="isResultShow=false" onfocus="isResultShow=false">
            <template #left>
            <van-icon name="arrow-left" @click="back"/>
            </template>
        </van-search>
        <div class="result" v-if="isResultShow">
            <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">加载中...</van-loading>
            <van-tabs v-model:active="active" swipeable shrink v-else>
                <van-tab title="内容">
                    <van-empty image="error" description="没有搜索结果" v-if="!articleList.length" />
                    <PostList :ArticleList="articleList" v-else ></PostList>
                </van-tab>
                <van-tab  title="用户" >
                    <van-empty image="error" description="没有搜索结果" v-if="!userList.length"/>
                    <div v-else>
                        <PersonalInfo :userInfo="el"  v-for="el in userList" :key="el.userId"></PersonalInfo>
                    </div>
                </van-tab>
            </van-tabs>
        </div>
        <div class="suggestion" v-else-if="value!==''">
            <van-cell icon="search" v-for="el in searchSuggestion" :key="el" clickable @click.stop="onSearch(el)">
                <template #title>
                    <p class="van-ellipsis" style="width:8rem;">{{el}}</p>
                </template>
            </van-cell>
        </div>
        <div class="history" v-else>
            <van-cell title="搜索历史" v-if="searchHistory.length">
                <van-icon name="delete" @click.stop="cleanHistory">清空</van-icon>
            </van-cell>
            <div>
                <van-button color="#777" size="mini" v-for="el in searchHistory" :key="el" @click.stop="onSearch(el)">
                    {{el}}</van-button>
            </div>
        </div>
    </div>
</template>

<script>
import PersonalInfo from '@/components/PersonalInfo.vue'
import PostList from '@/components/PostList.vue'
export default {
    name: 'SearchView',
    components: {
        PersonalInfo, PostList,
    },
    data() {
        return {
            loading: true,
            active: 0,
            value: '',
            isResultShow: false,
            searchHistory: [],
            searchSuggestion: [],
            articleList:[],
            userList:[]
        }
    },
    methods: {
        back() {
            this.$router.back()
        },
        onSearch(value) {
            this.loading = true;
            this.value = value;
            if (value !== '') {
                if (this.searchHistory.indexOf(value) === -1) {
                    this.searchHistory.push(value)
                    this.$store.commit('setHistory', this.searchHistory)
                }
                this.isResultShow = true;
                const params = { value ,URL:this.$store.state.URL}
                this.$axios.get('search', { params }).then(rs => {
                    console.log(rs);
                    this.articleList = rs[1];
                    this.userList = rs[2];
                    setTimeout(() => {
                        this.loading=false
                    },1000)
                })
            }
        },
        cleanHistory() {//清空其实记录
            this.searchHistory = [];
            this.$store.commit('setHistory', this.searchHistory)
        },
    },
    mounted() {
        if (!this.$store.state.history)
            this.$store.commit('setHistory', this.searchHistory)
        else
            this.searchHistory = this.$store.state.history
    },
    watch: {
        value(newValue) {
            if (newValue === '')
                this.isResultShow = false;
            else {
                const params = { value: newValue }
                this.$axios.get('search', { params }).then(rs => {
                    this.searchSuggestion = rs[0];
                })
            }
        }
    }
}
</script>

<style scoped lang="less">
.search {
    min-height: calc(100vh - 46px);

    .history>div {
        padding: 10px;
    }
}
</style>