<template>
  <div class="ActionSheet wrapper">
    <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">加载中...</van-loading>
    <van-grid clickable :column-num="4" :border="false" icon-size="1rem" v-else>
      <van-grid-item icon="warning" text="举报" icon-color="grey" @click='report' />
      <van-grid-item icon="delete" text="删除" icon-color="grey" @click="delArticle(articleId)" v-if="delShow" />
      <van-grid-item icon="edit" text="编辑" icon-color="grey" @click="editArticle(articleId)" v-if="editShow" />
    </van-grid>
  </div>
</template>

<script>
import { Toast, Dialog } from 'vant';
export default ({
  props: {
    articleId: {
      type: Number,
    }
  },
  inject: ['report'],
  data() {
    return {
      loading: true,
      authorId: -1,
      type: null,
      delShow: false,
      editShow: false,
    }
  },
  methods: {
    delArticle(articleId) {
      Dialog.confirm({
        title: '确定吗',
        message:
          '关于此文章的一切都会消失，仍旧如此吗',
      })
        .then(() => {
          // on confirm
          Toast.loading({
            message: '删除中...',
            overlay: true,
            loadingType: 'spinner',
            duration: 0,
          });
          const params = { articleId }
          this.$axios.get('delArticle', { params }).then(rs => {
            console.log(rs);
            Toast.clear();
            Toast.success({
              message: '删除成功！',
              overlay: true,
              onClose: () => {
                this.$router.back()
              }
            });
          })
        })
        .catch(() => {
          // on cancel
        });
    },
    editArticle() {
      this.isSubmit ? this.$router.push({ path: '/submit', query: { articleId: this.articleId } })
        : this.$router.push({ path: '/post', query: { articleId: this.articleId } })
      this.$emit('closeAction')
    },
    init() {
      if (this.articleId && this.$store.state.user) {
        const params = { articleId: this.articleId, }
        this.$axios.get('articleItemGet', { params }).then(rs => {
          this.isSubmit = rs[0].isSubmit
          if (rs[0].authorId === this.$store.state.user.userId) {
            this.delShow = true;
            this.editShow = true;
          }
          this.type = rs[0].isSubmit;
          this.loading = false;
        })
      }
      else
        this.loading = false
    }
  },
  mounted() {
    this.init()
  },
})
</script>
