<template>
  <div class="questionDetail wrapper pt">
    <van-nav-bar title="问题详情" left-arrow @click-left="onClickLeft" fixed />
    <div class="top" v-if="detail.isHot===false">
      <van-image class="avatar" width="1rem" height="1rem" fit="cover" radius="5px"
        :src="avatarGet(detail.avatar)" />
      <div class="nameDateReport">
        <div nameDate>
          <div class="nickname p4b">{{detail.nickname}}</div>
          <div class="time p5">{{dateShow(detail.createdAt)}}</div>
        </div>
        <div class="report">
          <van-button size="mini" color="transparent">
            <template #icon>
              <van-icon name="ellipsis" color="#aaa" size="0.5rem" @click="actionShow()"></van-icon>
            </template>
          </van-button>
        </div>
      </div>
    </div>
    <p class="p3b title" style="color:black">问:</p>
    <p class="p3b text-center">{{detail.question}}</p>
    <van-divider />
    <div class="qDetail " v-if="detail.desc!==''">
      <p class="p3b title" style="color:black">问题详情:</p>
      <p class="normal p5">{{detail.desc}}</p>
      <van-divider />
    </div>
    <p class="p3b title" style="color:black">答:</p>
    <p :class="detail.answer==='暂无回答'?'text-center p3b':'normal p5'">{{detail.answer}}</p>
    <van-divider />
    <!-- &&detail.answer==='暂无回答' -->
    <div class="btnBox" v-show="this.$store.state.user.userId===1000">
      <van-button  color="black" @click="show=true">我来回答</van-button>
    </div>
    <van-popup v-model:show="show" position="top">
      <van-form @submit="onSubmit">
        <van-cell-group inset>
          <van-field v-model="answer" rows="3" autosize type="textarea"  placeholder='填写你要向Ta回复的内容'
             ></van-field>
        </van-cell-group>
        <van-divider />
        <div class="sendBtn">
          <van-button color="black" native-type="submit">
            发送
          </van-button>
        </div>
      </van-form>
    </van-popup>
  </div>
</template>
<script>
import dayjs from '@/assets/util/public'
import qs from 'qs'
import { Dialog,Toast } from 'vant';
export default {
  name: 'ContactView',
  inject: ['avatarGet','actionShow'],
  data() {
    return {
      id: 0,
      detail: {},
      show: false,
      answer:'',
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
    dateShow(date) {//时间显示
      return dayjs(date).format('YYYY[年]MM[月]DD[日] HH:mm')
    },
    reset() {//数据重置
      this.show = false;
      this.detail.answer = this.answer;
      this.answer = '';
    },
    onSubmit() {//回答提交
      Dialog.confirm({
        title: '确定吗',
        message:
          '回答后不可修改，确定这样回答吗?',
      })
        .then(() => {
          // on confirm
          let toast1 = Toast.loading({
            message: '提交中',
            overlay: true,
            duration: 0,
          });
          const params = { answer: this.answer, id: this.id };
          this.$axios.post('answerPost', qs.stringify(params)).then(rs => {
            console.log(rs);
            toast1.clear();
            Toast.success({
              message: '回答成功！',
              overlay: true,
              onClose: this.reset,
            })
          })
        })
        .catch(() => {
          // on cancel
        });
    },
  },
  mounted() {
    const arr = this.$route.path.split('/');
    this.id = arr[arr.length - 1];
    const params = { id: this.id }
    this.$axios.get('questionDetailGet', { params }).then(rs => {
      this.detail = rs;
      console.log(this.detail);
    })
  },

}
</script>

<style scoped lang="less">
.btnBox {
  position:fixed;
  bottom:100px;
  display: flex;
  width:100%;
  justify-content: center;
}
 .sendBtn {
   display: flex;
   justify-content: flex-end;
   align-items: center;
   padding-right: 1rem;
   padding-bottom: 10px;
 }
.title {
  padding-left: 1rem;
}

.normal {
  padding-left: 1rem;
}

.top {
  display: flex;
  align-items: center;
  padding: .5rem 1rem;

  div.nameDateReport {
    padding-left: .3rem;
    flex: auto;
    display: flex;
    justify-content: space-between;

    div.nameDate {
      display: flex;
      flex-flow: column;
    }

    div.report {
      display: flex;
      align-items: center;
    }
  }

}
</style>