<template>
    <div class="submitList ">
        <van-cell v-for="item in ArticleList" :key="item.articleId" title-style="flex:2"
            :to="'/articleHome/' + item.articleId" style="height:2.5rem">
            <template #title>
                <div class="van-multi-ellipsis--l2 ">
                    <p class="van-multi-ellipsis--l2 lineClamp2" style="100%;">{{ item.title}}</p>
                </div>
            </template>
            <template #value>
                <van-image width="100%" height="100%" fit="cover" :src="item.cover" radius="0.08rem" />
            </template>
            <template #label>
                {{ before(item.createdAt) }}{{ item.isAll ? '-[' + item.channelName + ']' : ' by--' +
                        item.authorName
                }}
                <div style="float: right;padding-right:0.4rem">
                    <van-icon name="comment-o" /> {{ item.commentCount }}
                </div>
            </template>
        </van-cell>
    </div>
</template>
<script>
import dayjs from '@/assets/util/public'
export default {
    name: 'submitList',
    props: {
        ArticleList: {
            type: Array,
        }
    },
    data() {
        return {
        }
    },
    methods: {
        before(date) {
            const createdAt = dayjs(date)
            const now = dayjs().valueOf()
            const diff = createdAt.diff(now, 'day', true)
            if (diff <= -7)
                return dayjs(date).format('YYYY/MM/DD')
            return dayjs(date).fromNow()
        },
    },
    mounted() {
    },
}
</script>

<style scoped lang="less">

.lineClamp2 {
    height: 48px;
}
</style>