<template>
    <div class="collectBox ">
        <van-action-sheet v-model:show="actionsShow" cancel-text="取消" title="收藏内容" :actions="actions"
            close-on-click-action @select="onSelect">
        </van-action-sheet>
        <van-dialog v-model:show="dialogShow" :title="createCollect" show-cancel-button theme="round-button"
            confirm-button-color="#000" confirm-button-text="创建" cancel-button-color="grey" @confirm="createConfirm">
            <template #default>
                <van-field v-model="cbTitle" placeholder="收藏夹名字(8字内)" required size="large" maxlength='8'
                    input-align="center" />
            </template>
        </van-dialog>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    name: 'postList',
    props: {
        articleId: {
            type: Number,
        },
        // checkedArr:{
        //     type:Array
        // }
    },
    data() {
        return {
            actionsShow: false,
            createCollect: '创建收藏夹',
            dialogShow: false,//收藏夹名的确认与取消
            collectBox: [],//后台存储的收藏夹列表
            cbTitle: '',//收藏夹名
        }
    },
    methods: {
        async createConfirm() {//确认创建收藏夹
            if (this.cbTitle === '') {
                Toast('收藏夹名不能为空！');
                return
            }
            let obj = { name: this.cbTitle, content: [] }
            let arr = this.actions.filter(el => {
                return el.name === this.cbTitle;
            })
            if (arr.length !== 0) {
                Toast('同名收藏夹已存在！');
                return
            }
            this.collectBox = [...this.collectBox, obj]
            await this.collectUpdate(this.collectBox);
            this.actionsShow = true;
        },
        async onSelect(item) {//收藏选项,添加进收藏夹
            if (item.name === this.createCollect) {//命名
                this.dialogShow = true;
            }
            else {//加入收藏
                if (this.articleId) {//说明是在文章里的操作
                    this.collectBox = this.collectBox.map(el => {
                        if (el.name === item.name)
                            el.content.push(this.articleId)
                        return el
                    })
                    await this.collectUpdate(this.collectBox);
                    this.$emit('hadCollected', true)
                    Toast('收藏成功！')
                }
                else {
                    this.$emit('move', item.name)
                }
            }
        },
        async remove(id) {//移除该条
            this.collectBox = this.collectBox.map(el => {
                if (el.content.indexOf(id) > -1)//找到包含此articleId的收藏夹内容
                {
                    el.content = el.content.filter(item => {//过滤掉此articleId
                        return item !== id
                    })
                }
                return el
            })
            this.$emit('hadCollected', false)
            await this.collectUpdate(this.collectBox);
            Toast('取消收藏成功！')
        },
        async collectUpdate(actions) {//收藏夹后台更新
            const params = { userId: this.$store.state.user.userId, actions };
            await this.$axios.get('collectUpdate', { params }).then(rs => {
                console.log(rs);
            })
        },
        collectBoxGet() {//收藏夹数据获取
            const params = { userId: this.$store.state.user.userId };
            this.$axios.get('userDataShow', { params }).then(rs => {
                this.collectBox = rs.collects.collectBox;
            })
        },
    },
    mounted() {
        if (this.$store.state.user)
            this.collectBoxGet()
        // console.log('收藏夹组件重渲染！');
    },
    computed: {
        actions() {
            const add = { name: this.createCollect, subname: '创建新的收藏夹' }
            return [...this.collectBox, add]
        }
    },
    updated() {
        // console.log('收藏夹组件更新了！');
    }
}
</script>

