<template >
    <div class="article wrapper pt">
        <van-nav-bar left-arrow @click-left="onClickLeft" fixed title="文章详情" @click-right="actionShow(articleId)">
            <template #right>
                <van-icon name="ellipsis" size="18" />
            </template>
        </van-nav-bar>
        <transition name="van-slide-right" class="recover">
            <van-button v-show="visible" size="mini" round icon-position="right" icon="arrow" @click="recover"
                color="black">恢复到上次阅读位置</van-button>
        </transition>
        <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">加载中...</van-loading>
        <div v-if="!loading" class="bulk" id="articleBulk" ref="bulk">
            <ArticleItem :ArticleId="articleId" :Admin="admin" v-if="articleId !== 0 && past" :key="key" ref="article"
                @reload="articleReload">
            </ArticleItem>
            <Comment :ArticleId="articleId" :TempComment="tempComment" v-if="articleId !== 0 && past" ref="comment"
                :TempImages="readImages" :key="commentKey" @reload="commentReload">
            </Comment>
        </div>
        <div class="bottom">
            <van-cell icon="edit" center title-style="flex:1" :border="false">
                <template #title>
                    <van-field v-model="message" :placeholder="'发布评论(' + comCount + ')'" @focus='commentFocus' />
                </template>
                <template #value>
                    <van-grid :border="false" center column-num="3" icon-size="0.5rem">
                        <van-grid-item icon="star" :icon-color="collectState ? '#000' : ''"
                            :text="collectState ? '已收藏' : '收藏'" @click='collectIt' />
                        <van-grid-item icon="good-job" :icon-color="likeState ? '#000' : ''" :text="likeCount + ''"
                            @click="like" />
                        <van-grid-item icon="share" text="分享" @click='share' />
                    </van-grid>
                </template>
            </van-cell>
        </div>
        <van-popup v-model:show="commenting" :position="(this.$store.state.setup&&this.$store.state.setup.system) ? 'top' : 'bottom'" class="pop">
            <van-field v-model="message" rows="2" autosize type="textarea" :placeholder="'发布评论(' + comCount + ')'"
                ref="input" />
            <van-cell center :border="false" v-show="commenting" class="imagesBox">
                <van-uploader v-model="fileList" multiple ref="imagesSend" preview-size="1.7rem" image-fit="cover"
                    :after-read="afterRead" :before-delete="beforeDelete" :before-read="beforeRead">
                    <van-button icon="plus" type="primary" class="placeholder">上传文件</van-button>
                </van-uploader>
            </van-cell>
            <div class="option">
                <van-button type="primary" size="small" color="#000" @click="commentSend">发送</van-button>
                <van-icon name="photo-o" color="grey" size="0.5rem" style="padding:10px" @click="chooseFile" />
                <van-icon name="smile-o" color="grey" size="0.5rem" style="padding:10px"
                    @mousedown.prevent="developing" />
                <van-icon name="arrow-down" color="grey" size="0.5rem" style="padding:10px" @click="loseFocus" />
            </div>
        </van-popup>
        <!-- 收藏 -->
        <CollectBox ref="CollectBox" @hadCollected="hadCollected" :articleId="articleId" :key="key" v-if="past">
        </CollectBox>
    </div>
</template>
<script>
import ArticleItem from '@/components/ArticleItem.vue'
// 引入动作面板
// import ActionSheet from '@/components/ActionSheet.vue';
import qs from 'qs'
import Comment from '@/components/CommentReply.vue'
import { Toast } from 'vant';
import { Dialog } from 'vant'
import CollectBox from '@/components/CollectBox.vue';
import dayjs from '@/assets/util/public'
export default {
    inject: ['reload', 'isExistChecked', 'share', 'actionShow', 'developing'],
    name: 'ArticleHome',
    components: {
        ArticleItem,
        Comment,
        CollectBox,
        // ActionSheet
    },
    data() {
        return {
            loading: true,
            logined: false,//登录状态
            scrollLocation: 0,//滚动位置
            articleId: 0,
            admin: false,
            message: '',
            commenting: false,
            fileList: [],
            commentImgs: [],//要上传到后台的图片
            tempComment: {},//储存临时评论
            readImages: [],//临时评论要显示的图片
            deleteImage: '',
            imageName: [],//图片名数组，供beforeRead使用
            collectState: false,//收藏状态
            likeState: false,//点赞状态
            likeCount: 0,//文章点赞数
            likedId: [],//用户赞过的Id
            myHistory: [],
            mounted: false,
            scrollTop: 0,//缓存的位置
            visible: false,
            key: 1,
            commentKey: 1,
            comCount: 0,
            //检查文章完整性标志
            past: false,
        }
    },
    methods: {
        async like() {//文章的点赞状态
            this.past = await this.isExistChecked(this.articleId)
            if (!this.past) {
                return
            }
            if (!this.logined)
                this.loginState()
            else {
                if (this.likeState === false) {//没有赞过
                    this.likedId.push(this.articleId);
                    const params = { articleId: this.articleId, userId: this.$store.state.user.userId, likedId: this.likedId, option: 'plus' }
                    await this.$axios.get('artLikedIdUpdate', { params })
                    this.likeCount++;
                    this.likeState = true;
                    Toast('点赞成功!')
                }
                else {//赞过
                    this.likedId = this.likedId.filter(el => {
                        return el !== this.articleId;
                    })
                    console.log(this.likedId);
                    const params = { articleId: this.articleId, userId: this.$store.state.user.userId, likedId: this.likedId, option: 'minus' }
                    await this.$axios.get('artLikedIdUpdate', { params })
                    this.likeCount--;
                    this.likeState = false;
                    Toast('取消点赞!')
                }
            }
        },
        loginState() {
            Dialog.confirm({
                title: '未登录',
                message:
                    '你必须要登录才能使用此功能，是否前去登录?',
            })
                .then(() => {
                    this.$router.replace('/login');
                })
                .catch(() => {
                    return
                });
        }, // 登录状态检测
        collectIt() {//收藏功能
            if (!this.logined)
                this.loginState()
            else {
                if (this.collectState === false) {//不在收藏中，显示动作面板
                    this.$refs.CollectBox.actionsShow = true;
                }
                else {//在收藏中，移除收藏
                    this.$refs.CollectBox.remove(this.articleId);
                }
            }
        },
        hadCollected(data) {//改变收藏状态
            this.collectState = data;
        },
        async historyGetUpdate() {//历史获取与更新
            const now = dayjs().valueOf()
            const params = { userId: this.$store.state.user.userId }
            await this.$axios.get('historyGet', { params }).then(rs => {
                this.myHistory = rs[0]
            })
            const record = { articleId: this.articleId, date: now }
            this.myHistory = this.myHistory.filter(el => {//删除重复的浏览
                return el.articleId !== this.articleId
            })
            this.myHistory.unshift(record)
            const params1 = { userId: this.$store.state.user.userId, arr: this.myHistory };
            await this.$axios.get('historyUpdate', { params: params1 })
        },
        chooseFile() {//vant的选择文件功能
            this.$refs.imagesSend.chooseFile();
        },
        onClickLeft() {
            this.$router.back()
        },
        afterRead(file) {
            let _this = this
            this.commentImgs.push(file);//将选择的图片存入数组;
            let reader = new FileReader()
            reader.readAsDataURL(file.file) // 文件转换
            reader.onloadend = function () {
                _this.readImages.push(this.result)
            }
        },
        beforeDelete(file) {
            let _this = this
            let reader = new FileReader()
            reader.readAsDataURL(file.file)
            this.commentImgs.splice(this.commentImgs.indexOf(file), 1)
            this.imageName.splice(this.imageName.indexOf(file.file.name), 1);
            console.log('删除后得图片名数组:', this.imageName);
            console.log('要提交到后台得图片：', this.commentImgs);
            reader.onloadend = function () {
                _this.deleteImage = this.result
            }
            return true;
        },
        beforeRead(file) {
            if (this.$store.state.imageType.indexOf(file.type) !== -1) {
                if (this.imageName.indexOf(file.name) !== -1) {
                    Toast.fail('重复图片！')
                    return false;
                }
                if (this.imageName.length >= 9) {
                    Toast('最多9张图片！')
                    return false
                }
                if (file.length > 1) {//每次选择控制在一张图
                    Toast('每次只能选择一张图片！')
                    return false
                }
                this.imageName.push(file.name);//将选择的图片name存入数组;
                console.log('读取通过后得图片名数组：', this.imageName);
                return true;
            }
            else {
                Toast('格式不支持或选择了多张图片');
                return false;
            }
        },
        commentFocus() {
            if (!this.logined) {
                this.loginState()
            }
            else {
                if (this.tempComment.userInfo) {
                    this.tempComment = {};
                    this.readImages = [];
                }
                this.commenting = true;
                this.$nextTick(() => {
                    this.$refs.input.focus();
                })
            }

        },
        loseFocus() {
            this.$refs.input.blur();
        },
        async commentSend() {
            this.past = await this.isExistChecked(this.articleId)
            if (!this.past) {
                return
            }
            if (this.message === '')
                Toast('不能发送空的内容！');
            else {
                this.scrollTop = document.querySelector("#articleBulk").scrollTop
                let toast1 = Toast.loading({
                    forbidClick: true,
                    message: '提交中',
                    duration: 0,
                });
                let imgPaths = []//上传多个图片
                if (this.commentImgs.length !== 0) {
                    const data = new FormData()
                    this.commentImgs.map(el => {
                        data.append('comImgs', el.file);
                    })
                    await this.$axios.post('comImgsUpload', data).then(rs => {
                        console.log('返回的图片名为:', rs);
                        imgPaths = rs
                    })
                }
                let params = {
                    articleId: this.articleId,
                    userInfo: this.$store.state.user.userId,
                    message: this.message,
                    date: Date.now(),
                    images: imgPaths,
                    like: 0,
                }
                const commentItem = qs.stringify(params)
                this.$axios.post('addCom', commentItem).then(rs => {
                    this.tempComment = rs
                    toast1.clear();
                    this.message = '';
                    this.loseFocus();
                    this.commentKey = this.reload()
                    Toast.success({
                        message: '评论成功！',
                        forbidClick: true,
                        overlay: true,
                        onClose: () => {
                            this.$refs.bulk.scrollTop = this.articleReload;
                        }
                    })
                })
            }
        },
        articleReload(n) {
            this.articleReload = n
        },
        commentReload() {
            this.loaded()
            this.comCount = this.$refs.comment.comCount;
        },
        loaded() {
            if (this.loading === true)
                setTimeout(() => {
                    this.loading = false;
                    // this.scrollRecover()
                }, 100)
        },
        scrollRecover() {//
            let arr = this.$store.state.scrollLocation;
            arr = arr.filter(el => {
                if (el.id === this.articleId) {
                    this.scrollTop = el.scrollTop
                    return el
                }
            })
            if (arr.length)
                setTimeout(() => {
                    this.visible = true;
                }, 500)
            setTimeout(() => {
                this.visible = false;
            }, 2500)
        },//回滚条显示
        recover() {
            document.querySelector("#bulk").scrollTop = this.scrollTop;
            this.visible = false;
        },
        push() {
            if (!history.state.title) {//有了就不要再push了!
                let state1 = {
                    title: "title",
                    url: 'http://www.maomao365.com',
                }
                history.pushState(state1, 'title')
            }
        },
        async dataGet() {
            const arr = this.$route.path.split('/');
            this.articleId = parseInt(arr[arr.length - 1]);
            // console.log('文章Id', this.articleId);
            // 检查文章存在
            this.past = await this.isExistChecked(this.articleId)
            if (!this.past) {
                return
            }
            if (this.$store.state.user) {
                this.logined = true;
                const params = { userId: this.$store.state.user.userId, articleId: this.articleId }
                await this.$axios.get('articleState', { params }).then(rs => {//获得用户的收藏夹
                    this.likedId = rs[1];//赞过的文章
                    this.likeCount = rs[2];//文章点赞数
                    if (rs[1].indexOf(this.articleId) > -1) {//表示已赞过
                        this.likeState = true;
                    }
                    let arr = []
                    rs[0].forEach(el => {//所有收藏夹的文章Id拼接起来
                        arr = [...arr, ...el.content]
                    })
                    if (arr.indexOf(this.articleId) > -1)//文章id在收藏夹中
                        this.collectState = true;//已收藏
                })
                // 添加历史记录
                await this.historyGetUpdate()
            }
            this.loaded()
        },
        fun() {
            let arr = this.$store.state.scrollLocation;
            // alert(!arr)
            if (arr === 0 || !arr)
                arr = []
            try {
                let modify = false;//是否修改了该文章的滚动位置
                const obj = { id: this.articleId, scrollTop: this.scrollTop }
                if (this.scrollTop >= 300) {//滚动位置大于300将文章的阅读位置存到本地
                    console.log('arr:', arr);
                    arr.forEach(el => {//代替
                        if (el.id === obj.id) {
                            el.scrollTop = obj.scrollTop;
                            modify = true
                        }
                    })
                }
                else {
                    console.log('arr:', arr);
                    arr = arr.filter(el => {
                        return el.id !== obj.id
                    })
                    modify = true
                }
                if (!modify)
                    arr.push(obj)
                this.$store.commit('setScroll', arr)
            }
            catch (e) {
                alert.log(e);
            }
        }
    },
    mounted() {
        this.push();
        this.mounted = true;
        this.dataGet();
    },
    activated() {
        if (!this.mounted) {
            if (this.$store.state.isBack) {
                console.log('回滚！');
                document.querySelector("#articleBulk").scrollTop = this.scrollTop;
            }
            else {
                console.log('刷新！');
                this.push()
                Object.assign(this.$data, this.$options.data())
                this.dataGet();
                this.key = this.reload()
                this.commentKey = this.reload()
            }
        }//没有执行挂载才执行
        else
            this.mounted = false;
    },
    deactivated() {
        if (document.querySelector("#articleBulk"))
            this.scrollTop = document.querySelector("#articleBulk").scrollTop
        // this.fun()//记住每个文章的滚动条
    },
    watch: {
        deleteImage(newVal) {
            this.readImages.splice(newVal, 1);
        }
    },
}
</script>

<style scoped lang="less">
.pop {
    .option {
        padding: 10px;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
    }
}

.article {
    padding-bottom: 48.75px;

    :deep(.imagesBox .van-cell__value) {
        background: rgb(255, 255, 255)
    }

    .placeholder {
        position: absolute;
        opacity: 0;
        height: 0.2rem;
    }


}

div.bottom {
    position: fixed;
    bottom: 0;
    width: 100%;
    box-shadow: 0 0 2px 1px grey;
    z-index: 2;
}

:deep(div.bottom .van-cell) {
    padding: 6px;
}

:deep(div.bottom .van-grid-item__text) {
    margin: 0;
    font-size: 8px;
}

:deep(.van-grid-item__content) {
    padding: 0;
}

:deep(.van-field__body) {
    background: rgb(250, 248, 248);
}

.recover {
    position: fixed;
    left: 60%;
    bottom: 20%;
    z-index: 999;
}
</style>