<template>
    <div class="comAdd wrapper pt">
        <van-nav-bar title="增加社区" left-text="返回" left-arrow @click-left="onClickLeft" fixed />
        <div class="iconBox_edit">
            <div class="background" :style={background:background}>
                <div class="foreground" :style={background:foreground}>
                    <van-icon :class-prefix="icon" class="iconfont" :color="color" size="0.8rem" />
                </div>
                <span class="p7">
                    <van-icon name="fire" v-show="isHot" />{{communityName}}
                </span>
            </div>
        </div>
        <van-form @submit="onSubmit">
            <van-cell-group inset>
                <van-field v-model="icon" name="图标" label="图标" placeholder="图标"
                    :rules="[{ required: true, message: '未填写' }]" />
                <van-field v-model="communityName" name="社区名" label="社区名" placeholder="社区名"
                    :rules="[{ required: true, message: '未填写' }]" />
                <van-field v-model="color" name="图标颜色" label="图标颜色" placeholder="图标颜色"
                    :rules="[{ required: true, message: '未填写' }]" />
                <van-field v-model="foreground" name="前景色" label="前景色" placeholder="前景色"
                    :rules="[{ required: true, message: '未填写' }]" />
                <van-field v-model="background" name="背景色" label="背景色" placeholder="背景色"
                    :rules="[{ required: true, message: '未填写' }]" />
                <van-field name="switch" label="热门">
                    <template #input>
                        <van-switch v-model="isHot" size="20" />
                    </template>
                </van-field>
                <van-field name="checkboxGroup" label="社区分类">
                    <template #input>
                        <van-checkbox-group v-model="communityClass" direction="horizontal">
                            <van-checkbox :name="el" shape="square" v-for="el in allCommunityClass" :key="el">{{el}}</van-checkbox>
                        </van-checkbox-group>
                    </template>
                </van-field>
            </van-cell-group>
            <div style="margin: 16px;">
                <van-button round block :color="editing?'red':'black'" native-type="submit">
                    {{editing?'确定':'提交'}}
                </van-button>
            </div>
        </van-form>
        <div style="margin: 16px;">
            <van-button round block :color="editing?'green':'lightblue'" @click="cancelOrShow">
                {{editing?'取消':'编辑'}}
            </van-button>
        </div>
        <van-popup v-model:show="show" position="bottom" :style="{ width: '100%', height: '80%' }" class="pop">
            <div v-for="el in allCommunitys" :key="el">
                <van-cell :title="el.communityClass" />
                <van-grid direction="horizontal" :column-num="2" clickable gutter="0.2rem">
                    <van-grid-item v-for='item in el.content' :key="item" @click="comSelect(item)">
                        <div class="outBox">
                            <div class="iconBox">
                                <div class="foreground" :style="{ 'background': item.foreground }">
                                    <van-icon :class-prefix="item.icon + ' iconfont'" :color="item.color"
                                        size="0.6rem" />
                                </div>
                            </div>
                            <p class="textBox">{{ item.communityName }}</p>
                        </div>
                    </van-grid-item>
                </van-grid>
            </div>
        </van-popup>
    </div>
</template>

<script>
import { Dialog, Toast } from 'vant';
import Qs from 'qs'
export default {
    name: 'comAdd',
    data() {
        return {
            icon: 'icon-xiugai',
            communityName: '',
            color: '',
            foreground: '',
            background: '',
            isHot: true,
            communityClass: [],
            show: false,//pop
            allCommunitys: [],
            allCommunityClass: this.$store.state.communityClass,
            editing: false,
            editedId: -1,//被修改的社区id
        }
    },
    methods: {
        onClickLeft() {
            this.$router.back();
        },
        async onSubmit() {
            let params = {
                icon: this.icon, communityName: this.communityName, color: this.color,
                foreground: this.foreground, background: this.background, isHot: this.isHot, communityClass: this.communityClass,
            }
            Toast.loading({
                message: '提交中...',
                overlay: true,
                loadingType: 'spinner',
                duration: 0,
            });
            if (this.editing)
                await Dialog.confirm({
                    title: '确定修改吗',
                })
                    .then(async () => {
                        // on confirm  
                        params = { ...params, communityId: this.editedId }
                        let data = Qs.stringify(params)
                        await this.$axios2.post('comAdd', data).then(rs => {
                            console.log(rs);

                        })
                    })
                    .catch(() => {
                        // on cancel
                    });

            else {
                await Dialog.confirm({
                    title: '确定增加吗',
                })
                    .then(async () => {
                        // on confirm  
                        let data = Qs.stringify(params)
                        await this.$axios2.post('comAdd', data).then(rs => {
                            console.log(rs);
                        })
                    })
                    .catch(() => {
                        // on cancel

                    });
            }
            Toast.clear()
            Toast.success('成功！');
            this.editing = false;
        },
        async cancelOrShow() {
            if (!this.editing) {
                await this.communityGet()
                this.show = true;
            }
            else
                this.editing = false;
        },
        comSelect(item) {
            this.icon = item.icon;
            this.communityName = item.communityName;
            this.color = item.color;
            this.foreground = item.foreground;
            this.background = item.background;
            this.isHot = item.isHot;
            this.communityClass = item.communityClass.split(' ');
            this.editedId = item.communityId;
            this.show = false;
            this.editing = true;
        },
        async communityGet() {
            const params = { userId: this.$store.state.user.userId }
            await this.$axios.get('comGet', { params }).then(rs => {
                this.allCommunitys = this.allCommunityClass.map(el => {//选择社区页面的数据
                    let obj = {}
                    obj.communityClass = el;
                    obj.content = rs.filter(item => {
                        return item.communityClass.indexOf(el) > -1;
                    })
                    return obj
                })
            });
        },
    },
    mounted() {
        // this.communityGet();
    },

}
</script>

<style scoped lang="less">
div.iconBox_edit {
    height: 100px;
    display: flex;

    div.background {
        height: 70px;
        width: 70px;
        margin: auto;
        display: flex;
        flex-direction: column;

        div.foreground {
            height: 40px;
            width: 40px;
            margin: auto;
            display: flex;

            i {
                margin: auto;
            }
        }

        span {
            color: white;
            text-align: center;
            padding-bottom: 10px;
        }
    }
}

.outBox {
    width: 100%;
    display: flex;

    .iconBox {
        flex: 1;
        display: flex;
        justify-content: end;
        align-items: center;

        .foreground {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 0.7rem;
            height: 0.7rem;
        }
    }

    .textBox {
        padding-left: 10px;
        flex: 1;
        font-size: 14px;
        display: flex;
        justify-content: start;
        align-items: center;
    }
}
</style>