<template>
    <div class="articleItem wrapper p4" ref="article">
        <van-image width="100%" height="5rem" fit="cover"
            :src="this.$store.state.URL + 'upload/cover/' + articleItem.cover" alt=""
            v-if="articleItem.cover && articleItem.cover !== ' '" />
        <div class="article">
            <p class="van-multi-ellipsis--l2 p3b title">{{ articleItem.title }}</p>
            <div class="author" v-if="user.userId">
                <div class="avatarBox">
                    <router-link :to="'/personal/' + articleItem.authorId">
                        <van-image width="1rem" height="1rem" round fit="cover"
                            :src="avatarGet(user.avatar)" />
                    </router-link>
                </div>
                <div class="info">
                    <router-link :to="'/personal/' + articleItem.authorId">
                        <p class="p5b">{{ user.nickname }}</p>
                        <p class="p6g postTime">{{ before(createdAt) }}</p>
                    </router-link>
                </div>
                <!-- //作者是自己就不显示关注功能 -->
                <div class="subscribe" v-if="!this.$store.state.user||this.$store.state.user.userId!=user.userId">
                    <van-button size="small" :color="subscribeState?'grey':'#000'"
                        :icon="subscribeState?'success':'plus'" @click="subscribe(user.userId)">
                        {{subscribeState?'已关注':'关注Ta'}}</van-button>
                </div>
            </div>
            <p>
                <van-button size="mini" icon="guide-o" type="primary" to="">{{ channel.channelName }}</van-button>
            </p>
            <div v-html="articleItem.content" class="content" v-if="articleItem.content">
            </div>
        </div>
    </div>
</template>
<script>
import dayjs from '../assets/util/public'
import { ImagePreview } from 'vant';
import { Dialog } from 'vant'
import { useRect } from '@vant/use';
export default {
    name: 'articleItem',
    inject: ['avatarGet'],
    props: {
        ArticleId: {
            type: Number,
        },
        Admin: {
            type: Boolean,
            default: true,
        }
    },
    data() {
        return {
            articleId: this.ArticleId,
            articleItem: {},
            user: {},
            channel: {},
            createdAt: '',
            manage: true,
            images: [],
            admin: this.Admin,
            subscribeState: false,//对作者关注状态
            subscribeList: [],
            fanList: [],
        }
    },
    methods: {
        loginState() {
            Dialog.confirm({
                title: '未登录',
                message:
                    '你必须要登录才能使用此功能，是否前去登录?',
            })
                .then(() => {
                    this.$router.replace('/login');
                })
                .catch(() => {
                    return
                });
        }, // 登录状态检测
        before(date) {//时间显示
            const createdAt = dayjs(date)
            const now = dayjs().valueOf()
            const diff = createdAt.diff(now, 'day', true)
            if (diff <= -7)
                return dayjs(date).format('YYYY/MM/DD')
            return dayjs(date).fromNow()
        },
        onClickLeft() {//返回
            this.$router.back();
        },
        imgPreview(n) {//图片预览
            ImagePreview({ images: this.images, startPosition: n });
        },
        subscribe(id) {
            if (this.$store.state.user) {
                if (this.subscribeState) {//取消关注
                    Dialog.confirm({
                        title: '取消关注',
                    })
                        .then(() => {
                            this.subscribeList = this.subscribeList.filter(el => {
                                return el - 0 !== id
                            })
                            this.subscribeState = false;
                            this.fanList = this.fanList.filter(el => {
                                return el - 0 != this.$store.state.user.userId;
                            })
                            this.subscribeUpdate()
                        })
                        .catch(() => {
                            return
                        });
                }
                else {//关注
                    this.subscribeList.push(id);
                    this.fanList.push(this.$store.state.user.userId)
                    this.subscribeState = true;
                    this.subscribeUpdate()
                }
            }
            else
                this.loginState()
        },
        async articleDataGet() {
            if (this.admin === true) {//管理员
                const params = { articleId: this.articleId }
                // console.log('管理员',params);
                await this.$axios2.get('articleItemGet', { params }).then(rs => {
                    rs[0].content = rs[0].content.replace(/(http)[\S]+(1337\/)/gi, this.$store.state.URL);//替换图片地址
                    this.articleItem = rs[0];
                    this.user = rs[1];
                    
                    this.channel = rs[2];
                    this.manage = rs[3];
                    this.$emit('articleState', this.manage);
                    this.createdAt = this.articleItem.createdAt;
                })
            }
            else {
                const params = { articleId: this.articleId, URL: this.$store.state.URL }
                // console.log('用户', params);
                await this.$axios.get('articleItemGet', { params }).then(rs => {
                    this.articleItem = rs[0];
                    this.user = rs[1];
                    console.log(this.user);
                    this.channel = rs[2];
                    this.images = rs[3]
                    this.createdAt = this.articleItem.createdAt;
                })
            }
        },
        async targetFansGet(userId) {//目标的粉丝获取
            const params = { userId }
            await this.$axios.get('subscribeListGet', { params }).then(rs => {
                this.fanList = rs[1];
            })
        },
        async subscribeGet() {
            const params = { userId: this.$store.state.user.userId }
            await this.$axios.get('subscribeListGet', { params }).then(rs => {//获取用户关注列表
                this.subscribeList = rs[0];
            })
            await this.targetFansGet(this.articleItem.authorId)
            if (this.fanList.indexOf(this.$store.state.user.userId + '') > -1)
                this.subscribeState = true
        },
        async dataGet() {
            await this.articleDataGet();
            if (this.$store.state.user)
                this.subscribeGet();
        },
        subscribeUpdate() {
            const params = { fan: this.fanList, follow: this.subscribeList, userId: this.$store.state.user.userId, authorId: this.articleItem.authorId }
            this.$axios.get('subscribeUpdate', { params })
        }//关注与粉丝更新
    },
    created() {
        window.imgPreview = this.imgPreview
    },
    mounted() {
        this.dataGet()
        // console.log('文章组件重渲染了，文章ID：', this.articleId);
    },
    updated() {
        // console.log('文章组件更新了');
        const root = this.$refs.article;
        const rect = useRect(root);
        this.$emit('reload',rect.height)
    }
}
</script>

<style scoped lang="less">
:deep(iframe) {
    width: 100% !important;
}

:deep(.content img) {
    width: 100% !important;
    border-radius: 5px;
}

:deep(.content) {
    margin-top: 10px;
}

:deep(.content>p) {
    margin-bottom: 10px;
}

:deep(.content>blockquote) {
    margin-bottom: 10px;
    border-left: solid grey 4px;
    padding-left: 15px;
    background-color: #eee;
}

:deep(.content>blockquote span) {
    color: grey !important;
    background: transparent !important;
}

:deep(.content>blockquote a) {
    color: blue !important;
}

.article {
    padding: 10px;

    .title {
        margin-bottom: 10px;
    }

    .author {
        display: flex;
        margin-bottom: 10px;

        div.info {
            padding-left: 10px;

            p {
                padding-top: 2px;
            }
        }

        div.subscribe {
            flex: 1;
            display: flex;
            justify-content: flex-end;
        }
    }
}
</style>