<template>
    <div ref="channelListBulk">
        <van-pull-refresh v-model="refreshing" :head-height="80" style="min-height:calc(100vh - 96px)" @refresh="onRefresh"
         :disabled="currentChannel===-1?true:false">
            <van-list v-model:loading="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                <SubmitList :ArticleList="list"></SubmitList>
            </van-list>
        </van-pull-refresh>
    </div>
</template>
<script>
import SubmitList from '@/components/SubmitList.vue'
import { Toast } from 'vant'
import { useRect } from '@vant/use';
export default {
    name: 'ChannelList',
    inject: ['minHeight'],
    props: {
        channelId: {
            type: Number,
        },
        currentChannel: {
            type: Number,
        },
        params: {
            type: Object,
        }
    },
    components: {
        SubmitList,
    },
    data() {
        return {
            loading: false,
            finished: false,
            size: this.$store.state.size,
            skip: 0,
            list: [],
            refreshing: false,
            top:0,
        }
    },
    methods: {
        onRefresh() {
            if (this.loading === true) {
                Toast.fail({
                    overlay: true,
                    message: '不要在加载时刷新!'
                })
                this.refreshing = false;
                return
            }
            this.loading = true;
            this.finished = false;
            this.skip = 0;
            this.onLoad();
        },
        onLoad() {
            if (this.channelId === this.currentChannel) {
                const params = { ...this.params, skip: this.skip, size: this.size }
                this.$axios.get('articlesList', { params }).then(rs => {
                    setTimeout(() => {
                        if (this.refreshing) {
                            this.list = [...rs];
                            Toast('刷新成功');
                            this.refreshing = false;
                        } else {
                            this.list = [...this.list, ...rs];
                        }
                        if (this.currentChannel === -1) {
                            this.$emit('refreshed',false)
                        }
                        this.loading = false;
                        if (rs.length < this.size) {
                            this.finished = true;
                        }
                        else
                            this.skip = this.skip + this.size
                    }, 100);
                })
            }
        },
        getHeight() {
            return { 'min-height': `calc(100vh - 50px - ${this.top + 'px'})` }
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.top = useRect(this.$refs.channelListBulk).top;
        })
    },
    activated() {
    },
    deactivated() {
    },
}
</script>

