<template>
    <div class="community pbt wrapper" ref="">
        <van-tabs v-model:active="active" shrink class="out bulk allCommunity" @change="onChange">
            <van-tab title="推荐">
                <van-tabs v-model:active="active3" class="myCommunitys" line-height="0" shrink>
                    <van-tab v-for="item in myCommuitys" :key="item.communityId">
                        <template #title>
                            <div class="box">
                                <div class="foreground" :style='{ background: item.foreground }'>
                                    <van-icon :class-prefix="item.icon" class="iconfont" :color="item.color"
                                        size="0.8rem" @click.stop="goCommunityItem(item.communityId)" />
                                </div>
                                <span class="p6">
                                    {{ item.communityName }}
                                </span>
                            </div>
                        </template>
                    </van-tab>
                    <van-tab>
                        <template #title>
                            <div class="box">
                                <div class="foreground" style="background:black">
                                    <van-icon name="more" color="white" size="0.8rem" @click.stop='active = 1' />
                                </div>
                                <span class="p6">
                                    全部社区
                                </span>
                            </div>
                        </template>
                    </van-tab>
                </van-tabs>
                <CommunityList :params="communityParams" v-if="communityParams.isSubmit !== undefined" :key="key">
                </CommunityList>
            </van-tab>
            <van-tab title="社区">
                <van-cell title="我的社区">
                    <template #extra>
                        <span @click="edit">{{ isEditing === false ? '编辑' : '完成' }}</span>
                    </template>
                </van-cell>
                <van-empty image="https://fastly.jsdelivr.net/npm/@vant/assets/custom-empty-image.png" image-size="80"
                    description="点击“编辑”添加社区吧" v-if="myCommuitys.length === 0" />
                <van-tabs v-model:active="active2" class="myCommunitys" line-height="0" shrink v-else>
                    <van-tab v-for="item in myCommuitys" :key="item.communityId">
                        <template #title>
                            <div class="box">
                                <div class="foreground" :style='{ background: item.foreground }'
                                    @click.stop="clear(item.communityId)">
                                    <div class="minus">
                                        <van-icon name="minus" size="0.4rem" v-show="isEditing" />
                                    </div>
                                    <van-icon :class-prefix="item.icon" class="iconfont" :color="item.color"
                                        size="0.8rem" />
                                </div>
                                <span class="p6">
                                    {{ item.communityName}}
                                </span>
                            </div>
                        </template>
                        <!-- <span>Checked names: {{ checkedNames }}</span> -->
                    </van-tab>
                </van-tabs>
                <van-tabs v-model:active="active1" swipeable class="in">
                    <van-tab v-for="(el, index) in communityClass" :key="el">
                        <template #title>{{ titles[index] }}</template>
                        <van-row justify="left" class="row" gutter="5">
                            <van-col span="6" v-for="item in el" :key="item" class="iconBox">
                                <div class="background" :style='{ background: item.background }'
                                    @click="goCommunityItem(item.communityId)">
                                    <van-icon name="star" size=".4rem" v-show="!isEditing && item.selected"
                                        color="white" class="star" />
                                    <div class="inputBox" v-show="isEditing"
                                        @click.prevent="clearAndAdd(item.communityId)">
                                        <input type="checkbox" :value="item.communityId" v-model="checkedNames" />
                                    </div>
                                    <div class="foreground" :style='{ background: item.foreground }'>
                                        <van-icon :class-prefix="item.icon" class="iconfont" :color="item.color"
                                            size="0.8rem" />
                                    </div>
                                    <span class="p5">
                                        <van-icon name="fire" v-show="item.isHot" />{{ item.communityName }}
                                    </span>
                                </div>
                            </van-col>
                        </van-row>
                    </van-tab>
                </van-tabs>
            </van-tab>
        </van-tabs>
        <TabBar></TabBar>
    </div>
</template>
<script>
import { Dialog, Toast } from 'vant'
import CommunityList from '@/components/CommunityList.vue'
import TabBar from '@/views/layout/TabBar.vue'
export default {
    name: 'communityView',
    inject: ['reload'],
    components: {
        CommunityList, TabBar
    },
    data() {
        return {
            active: 0,
            active1: 0,
            active2: 0,
            active3: 0,
            allCommunitys: [],
            titles:  this.$store.state.communityClass,
            checkedNames: [],
            isEditing: false,
            logined: false,
            articleList: [],
            //列表加载
            communityParams: {},
            key: 0,
            scrollLocation: 0,
        }
    },
    methods: {
        goCommunityItem(id) {
            if (this.isEditing === false)
                this.$router.push('/communityItem/' + id)
        },
        edit() {
            if (this.logined === false) {
                Dialog.confirm({
                    title: '未登录',
                    message:
                        '你必须要登录才能使用此功能，是否前去登录?',
                })
                    .then(() => {
                        this.$router.replace('/login');
                    })
                    .catch(() => {
                        // on cancel
                    });
            }
            else {
                if (this.isEditing === false)
                    this.isEditing = true;
                else {
                    this.isEditing = false;
                    console.log(this.checkedNames);
                    const params = { userId: this.$store.state.user.userId, community: this.checkedNames }
                    this.$axios.get('communityUpdate', { params }).then(rs => {
                        console.log(rs);
                        this.key = this.reload();
                        Toast('社区偏好已更新!')
                    })
                }
            }

        },
        clear(id) {
            if (this.isEditing) {
                this.checkedNames = this.checkedNames.filter(el => {
                    return el !== id
                })
            }
            else {
                this.goCommunityItem(id);
            }
        },
        clearAndAdd(id) {
            if (this.checkedNames.indexOf(id) > -1)
                this.checkedNames = this.checkedNames.filter(el => {
                    return el !== id
                })
            else
                this.checkedNames.push(id);
        },
        articleListGet() {//获取社区帖子
            if (this.logined)
                this.communityParams = { isSubmit: false, userId: this.$store.state.user.userId }
            else
                this.communityParams = { isSubmit: false }
        },
        async dataGet() {
            if (this.$store.state.user) {
                this.logined = true;
                const params = { userId: this.$store.state.user.userId }
                await this.$axios.get('comListShow', { params }).then(rs => {
                    this.checkedNames = rs.map(el => {
                        return el.communityId;
                    })
                })
            }
            else {
                this.checkedNames = [1, 2, 3, 5]
            }
            await this.$axios.get('comGet').then(rs => {//所有社区
                this.allCommunitys = rs;
                // console.log(this.allCommunitys);
            })
            this.articleListGet();
        },
        scrollHandler() {
            if (!this.active)
                this.scrollLocation = document.querySelector('.allCommunity').scrollTop;
        },
        onChange(name) {
            this.$nextTick(() => {
                if (!name)
                    document.querySelector('.allCommunity').scrollTop = this.scrollLocation;
            })
        }
    },
    mounted() {
        this.$nextTick(() => {
            document.querySelector('.allCommunity').addEventListener('scroll', this.scrollHandler)
        })
        this.dataGet();
    },
    computed: {
        myCommuitys() {
            let arr = [];
            arr = this.allCommunitys.filter(el => {
                return this.checkedNames.indexOf(el.communityId) !== -1;
            })
            return arr;
        },
        communityClass() {
            let arr = [[], [], [], [], []];
            this.allCommunitys.forEach(el => {
                this.titles.forEach((item, index) => {
                    if (el.communityClass.indexOf(item) !== -1) {
                        if (this.checkedNames.indexOf(el.communityId) !== -1)
                            el.selected = true;
                        else
                            el.selected = false
                        arr[index].push(el);
                    }
                })
            })
            return arr
        }
    },
    activated() {
        this.$nextTick(() => {
            if (!this.active)
                document.querySelector('.allCommunity').scrollTop = this.scrollLocation;
        })
    }
}
</script>

<style scoped lang="less">
:deep(.out>.van-tabs__wrap) {
    position: fixed;
    z-index: 100;
    top: 0;
}

.van-empty {
    padding: 0;

}

.row {
    padding: 5px;

    div.iconBox {
        height: 100px;
        display: flex;
        margin-bottom: 5px;

        div.background {
            height: 100%;
            width: 100%;
            margin: auto;
            display: flex;
            flex-direction: column;
            border-radius: 10px;
            position: relative;

            .star {
                position: absolute;
            }

            div.inputBox {
                height: 100%;
                width: 100%;
                position: absolute;
                display: block;
                border-radius: 10px;
                z-index: 100;

                input {
                    position: absolute;
                    right: 0px;
                    top: 0px;
                }
            }

            div.foreground {
                height: 40px;
                width: 40px;
                margin: auto;
                display: flex;
                border-radius: 4px;

                i {
                    margin: auto;
                }
            }

            span {
                color: white;
                text-align: center;
                padding-bottom: 10px;
            }
        }
    }
}

:deep(.myCommunitys .van-tabs__wrap) {
    height: 70px;
}

:deep(.myCommunitys .van-tabs__wrap .van-tabs__nav) {
    padding-top: 10px;
}

.myCommunitys {
    .box {
        height: 100%;
        width: 100%;
        margin: auto;
        display: flex;
        flex-direction: column;

        // position: relative;
        div.foreground {
            height: 40px;
            width: 40px;
            margin: auto;
            display: flex;
            border-radius: 4px;
            position: relative;

            div.minus {
                height: 100%;
                width: 100%;
                position: absolute;
                display: block;
                border-radius: 4px;

                i {
                    background: grey;
                    color: white;
                    border-radius: 50%;
                    position: absolute;
                    top: -0.2rem;
                    right: -0.2rem;
                    z-index: 300;
                }
            }

            i {
                margin: auto;
            }
        }

        span {
            color: rgb(0, 0, 0);
            text-align: center;
        }
    }

}
</style>