<template>
  <div id="app" class="Router wrapper">
    <router-view v-slot="{ Component, }">
      <transition :name="transitionName">
        <keep-alive>
          <component :is="Component" v-if="$route.meta.keepAlive" :key="$route.path" />
        </keep-alive>
      </transition>
      <transition :name="transitionName">
        <component :is="Component" v-if="!$route.meta.keepAlive" />
      </transition>
    </router-view>
    <!-- 举报面板 -->
    <van-action-sheet v-model:show="show" :actions="actions" @select="onSelect" cancel-text="取消" title="你认为此内容有什么问题？"
      :closeable="false" close-on-click-action />
    <!-- 分享面板 -->
    <van-share-sheet v-model:show="showShare" title="立即分享" :options="options" @select="shareSelect" />
    <!-- 文章动作面板 -->
    <!-- 文章动作面板 -->
    <van-action-sheet v-model:show="actSheetShow" cancel-text="取消" title="操作" :closeable="false">
      <template #default>
        <ActionSheet :articleId="articleId" :key="key" @closeAction="actSheetShow = false"></ActionSheet>
      </template>
    </van-action-sheet>
  </div>
</template>

<script>
import { Dialog, Toast } from 'vant'
import dayjs from '@/assets/util/public'
import { useRect } from '@vant/use';
import ActionSheet from '@/components/ActionSheet.vue';
export default {
  name: 'App',
  components: {
    ActionSheet
  },
  provide() {
    return {
      reload: this.reload,
      getId: this.getId,
      before: this.before,
      isExistChecked: this.isExistChecked,
      untilMin: this.untilMin,
      goPersonal: this.goPersonal,
      goArticle: this.goArticle,
      minHeight: this.minHeight,
      developing: this.developing,
      toLogin: this.toLogin,
      report: this.report,
      share: this.share,
      actionShow: this.actionShow,
      avatarGet: this.avatarGet,
    }
  },
  data() {
    return {
      show: false,
      actions: [
        { name: '发布 违法违规 内容', color: '#ee0a24' },
        { name: '发布 色情低俗 内容', color: '#ee0a24' },
        { name: '发布 有害信息 内容', color: '#ee0a24' },
        { name: '发布 垃圾广告 内容', color: '#ee0a24' },
        { name: '发布 钱物交易 内容', color: '#ee0a24' },
        { name: '发布 辱骂/引战 内容', color: '#ee0a24' },
        { name: '发布 无意义 内容', color: '#ee0a24' },
        { name: '发布 侵权/抄袭 内容', color: '#ee0a24' },
        { name: '发布 侵犯隐私 内容', color: '#ee0a24' },
        { name: '发布 血腥/暴力 内容', color: '#ee0a24' },
        { name: '发布 令人羡慕/嫉妒 内容', color: '#ee0a24' },
      ],
      options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' },
        ],
      ],
      actSheetShow: false,
      key: -1,//actionShow
      showShare: false,
      transitionName: '',
      include: [],
      backAble: true,
      myHistory: {},
      backTwice: true,
      articleId: null,
    }
  },
  methods: {
    actionShow(articleId) {
      this.articleId = articleId;
      this.key = this.reload();
      this.actSheetShow = true;
    },
    avatarGet(src) {
      if (!src || src.length === 0)
        return this.$store.state.URL + '/images/avatar/notlogin.png'
      else
        return this.$store.state.URL + 'upload/avatar/' + src
    },
    report() {
      this.show = true;
    },
    share() {
      this.showShare = true;
    },
    onSelect() {
      Toast.success('举报成功!')
      this.actSheetShow = false;
    },
    shareSelect() {
      Toast.success('到此为止!')
      this.showShare = false;
    },
    developing() {
      Toast('敬请期待！')
    },
    toLogin() {
      Dialog.confirm({
        title: '未登录',
        message:
          '你必须要登录才能使用此功能，是否前去登录?',
      })
        .then(() => {
          this.$router.replace('/login');
        })
        .catch(() => {
          // on cancel
        });
    },
    minHeight(ref, noBottom) {
      this.$nextTick(() => {
        const rect = useRect(ref)
        if (noBottom)
          return { 'min-height': `calc(100vh - ${rect.top + 'px'})` }
        else
          return { 'min-height': `calc(100vh - 50px - ${rect.top + 'px'})` }
      })
    },
    untilMin(date) {
      return dayjs(date).format('MM/DD HH:mm')
    },
    before(date) {
      const createdAt = dayjs(date)
      const now = dayjs().valueOf()
      const diff = createdAt.diff(now, 'day', true)
      if (diff <= -7)
        return dayjs(date).format('YYYY/MM/DD')
      return dayjs(date).fromNow()
    },
    getId(path) {
      const arr = path.split('/');
      const id = arr[arr.length - 1];
      return id
    },
    reload() {
      return new Date().getTime()
    },
    goPersonal(id) {
      this.$router.push('/personal/' + id)
    },
    goArticle(id) {
      this.$router.push('/articleHome/' + id)
    },
    async isExistChecked(articleId) {
      return await this.$axios.get('isExistChecked', { params: { articleId, userId: 0 } }).then(rs => {
        if (!rs) {
          Toast.fail({
            message: '文章已删除!',
            duration: 3000,
            overlay: true,
            onClose: this.$router.back
          })
        }
        return rs
      })
    },
    fun() {
      if (this.backTwice === true) {
        if (this.backAble === true) {
          history.back()
          // console.log('执行返回');
          this.$store.commit('setBack', true)
          // console.log('是后退操作！');
          this.backAble = false
        }
        else {
          this.$store.commit('setBack', false)
          // console.log('不执行返回');
          this.backAble = true
        }
      }
    },
    toastShow() {
      Toast({
        message: '再按一次退出应用',
        position: 'bottom',
        duration: 2000,
      })
    }
  },
  watch: {
    $route(to, from) {
      if (from.meta.keepAlive === true && !from.meta.spe)//是缓存的且不特殊就返回两次
        this.backTwice = true;
      else
        this.backTwice = false;
      if (to.meta.index === 100 && from.meta.index === 100) {
        // if (this.$store.state.isBack) {
        //   this.transitionName = 'back';
        // }
        // else {
        //   this.transitionName = 'slide'
        // }
        this.transitionName = 'scale';
      }
      else {
        if (to.meta.index > from.meta.index) {
          this.transitionName = "slide";
        } else if (to.meta.index < from.meta.index) {
          this.transitionName = "back";
        } else {
          this.transitionName = ''
        }
      }
    },
  },
  created() {
    window.toastShow = this.toastShow;
  },
  mounted() {
    this.$store.commit('setBack', false)//初始值
    window.addEventListener('popstate', this.fun, false);
  },
}
</script>

<style lang="less">
#toast {
  position: fixed;
  z-index: 1000000;
  width: 75px;
  height: 75px;
  margin: auto;
  background: black;
  top: 500px;
}

.slide-enter-active,
.back-leave-active,
.scale-leave-active {
  z-index: 200;
  position: fixed;
  will-change: transform;
  transition: all 0.5s ease-out;
}

.slide-enter-to {
  transform: translateX(0%);
}

.slide-enter-from {
  transform: translateX(100%);
}

.back-leave-to {
  transform: translateX(100%);
}

.back-leave-from {
  transform: translateX(0%);
}

.scale-leave-to {
  opacity: 0;
  transform: scale(0.9);
}

.scale-leave-from {
  opacity: 1;
  transform: scale(1);
}
</style>
