<template>
    <div class="listCell " ref="listCellBulk">
        <van-pull-refresh v-model="refreshing" :head-height="80" :style="minHeight(this.$refs.listCellBulk)" @refresh="onRefresh">
            <van-tabs v-model:active="active" type="card" color="#fff" background="#f2f4f3" title-active-color="black"
                title-inactive-color="#999">
                <van-tab :title="'发帖' + postIdArr.length">
                    <van-empty image="https://fastly.jsdelivr.net/npm/@vant/assets/custom-empty-image.png"
                        image-size="80" description="暂无发帖" v-if="postFinished === true && post.length === 0" />
                    <div class="scrollBox">
                        <van-list v-model:loading="postLoading" :finished="postFinished" finished-text="没有更多了"
                            @load="onLoadPost">
                            <PostList :ArticleList="post"></PostList>
                        </van-list>
                    </div>
                </van-tab>
                <van-tab :title="'评论' + commentArr.length">
                    <!-- <van-pull-refresh v-model="refreshing" :head-height="80" @refresh="onRefresh"> -->
                    <van-empty image="https://fastly.jsdelivr.net/npm/@vant/assets/custom-empty-image.png"
                        image-size="80" description="暂无评论" v-if="commentFinished === true && comment.length === 0" />
                    <van-list v-model:loading="commentLoading" :finished="commentFinished" finished-text="没有更多了"
                        @load="onLoadComment">
                        <div class="scrollBox">
                            <CommentList :ArticleList="comment" :listCell="true"></CommentList>
                        </div>
                    </van-list>
                    <!-- </van-pull-refresh> -->
                </van-tab>
                <van-tab :title="'投稿' + submitIdArr.length">
                    <van-empty image="https://fastly.jsdelivr.net/npm/@vant/assets/custom-empty-image.png"
                        image-size="80" description="暂无投稿" v-if="finished === true && submit.length === 0" />
                    <div class="scrollBox">
                        <van-list v-model:loading="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                            <SubmitList :ArticleList="submit"></SubmitList>
                        </van-list>
                    </div>
                </van-tab>
            </van-tabs>
        </van-pull-refresh>
    </div>
</template>

<script>
import PostList from '@/components/PostList.vue'
import SubmitList from '@/components/SubmitList.vue'
import CommentList from '@/components/CommentList.vue'
import { Toast } from 'vant';
export default {
    name: 'postList',
    components: {
        PostList,
        SubmitList,
        CommentList,
    },
    inject: ['reload', 'minHeight'],
    props: {
        userId: {
            type: Number,
        },
        submitIdArr: {
            type: Array
        },
        postIdArr: {
            type: Array
        },
        commentArr: {
            type: Array
        }
    },
    data() {
        return {
            active: 0,
            size: this.$store.state.size,//加载的条数
            submit: [],
            loading: false,
            finished: false,
            skip: 0,
            post: [],
            postLoading: false,
            postFinished: false,
            postSkip: 0,
            comment: [],
            commentLoading: false,
            commentFinished: false,
            commentSkip: 0,
            refreshing: false,
        }
    },
    methods: {
        async onRefresh() {
            if (this.loading||this.postLoading||this.commentLoading) {
                Toast.fail({
                    overlay: true,
                    message: '不要在加载时刷新!'
                })
                this.refreshing = false;
                return
            }
            this.$emit('refresh', 'data', () => {
                switch (this.active) {
                    case 0:
                        this.postLoading = true;
                        this.postFinished = false;
                        this.postSkip = 0;
                        this.onLoadPost();
                        break;
                    case 1:
                        this.commentLoading = true;
                        this.commentFinished = false;
                        this.commentSkip = 0;
                        this.onLoadComment();
                        break;
                    case 2:
                        this.loading = true;
                        this.finished = false;
                        this.skip = 0;
                        this.onLoad();
                        break;
                }
            })
        },
        onLoad() { //加载投稿
            const size = this.size;
            let start = this.skip;
            let end = start + size;
            let arr = [];
            for (start; start < end; start++) {
                arr.push(this.submitIdArr[start]);
            }
            let Arr = arr.filter(el => {
                return typeof (el) === 'number'
            })
            const params = { submitIdArr: Arr, URL: this.$store.state.URL, userId: this.userId }
            console.log(params);
            setTimeout(() => {
                this.$axios.get('userSubmitShow', { params }).then(rs => {
                    if (this.refreshing) {
                        this.submit = [...rs];
                        Toast('刷新成功');
                        this.refreshing = false;
                    }
                    else
                        this.submit = [...this.submit, ...rs];
                    this.loading = false;
                    if (rs.length < this.size)
                        this.finished = true;
                    else
                        this.skip = this.skip + this.size
                })
            }, 100)
        },
        onLoadPost() { //加载发帖
            const size = this.size;
            let start = this.postSkip;
            let end = start + size;
            let arr = [];
            for (start; start < end; start++) {
                arr.push(this.postIdArr[start]);
            }
            let Arr = arr.filter(el => {
                return typeof (el) === 'number'
            })
            const params = { postIdArr: Arr, URL: this.$store.state.URL, userId: this.userId }
            setTimeout(() => {
                this.$axios.get('userPostShow', { params }).then(rs => {
                    if (this.refreshing) {
                        this.post = [...rs];
                        Toast('刷新成功');
                        this.refreshing = false;
                    }
                    else
                        this.post = [...this.post, ...rs];
                    this.postLoading = false;
                    if (rs.length < this.size)
                        this.postFinished = true;
                    else
                        this.postSkip = this.postSkip + this.size
                })
            }, 100)
        },
        onLoadComment() {
            const size = this.size;
            let start = this.commentSkip;
            let end = start + size;
            let arr = [];
            for (start; start < end; start++) {
                arr.push(this.commentArr[start]);
            }
            let Arr = arr.filter(el => {
                return typeof (el) === 'object'
            })
            const params = { commentArr: Arr, userId: this.userId }
            setTimeout(() => {
                this.$axios.get('userCommentShow', { params }).then(rs => {
                    rs = rs.filter(el => {
                        return el
                    })
                    if (this.refreshing) {
                        this.comment = [...rs];
                        Toast('刷新成功');
                        this.refreshing = false;
                    }
                    else
                        this.comment = [...this.comment, ...rs];
                    this.commentLoading = false;
                    if (rs.length < this.size)
                        this.commentFinished = true;
                    else
                        this.commentSkip = this.commentSkip + this.size
                })
            }, 100)
        }
    },
    mounted() {

    },
    updated() {

    }
}
</script>

<style scoped lang="less">
.listCell {
    border-top: 2px solid #f2f4f3;
}
</style>