<template>
    <div class="channelSelect wrapper">
        <div class="top">
            <p class="p2">选择感兴趣的内容</p>
            <p class="p5g">根据兴趣，生产私人定制</p>
        </div>
        <div class="main">
            <van-grid :column-num="3" square :gutter="10">
                <van-grid-item v-for="el in allChannels" :key="el.channelId">
                    <div class="box">
                        <van-image width="100%" height="100%" fit="cover"
                            src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg" />
                        <van-checkbox-group v-model="checked" ref="checkboxGroup">
                            <van-checkbox :name="el.channelId"></van-checkbox>
                        </van-checkbox-group>
                        <span class="p5">{{el.channelName}}</span>
                    </div>
                </van-grid-item>
            </van-grid>
        </div>
        <div class="bottom">
            <van-button round :color="enterAllow?'red':'pink'" block @click="enter">{{text(checked)}}</van-button>
            <van-button round type="primary" block @click="checkAll">一键全选并进入</van-button>
        </div>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    name: 'ChannelSelect',
    data() {
        return {
            allChannels: [],
            checked: [],
            enterAllow: false,
            allId: [],
        }
    },
    methods: {
        text(checked) {
            this.enterAllow = false;
            console.log(checked);
            if (!checked.length)
                return '至少选择4个兴趣'
            else if (checked.length <= 3)
                return '至少选择4个兴趣(' + checked.length + '/4)'
            else {
                this.enterAllow = true;
                return '进入'
            }
        },
        enter() {
            if (this.enterAllow)
                this.submit(this.checked)
        },
        submit(arr) {
            const toast1 = Toast.loading({
                message: '私人定制生成中...',
                forbidClick: true,
                duration: 3000,
                overlay: true,
            });
            setTimeout(() => {
                const params = { userId: this.$store.state.user.userId, channels: arr }
                this.$axios.get('channelUpdate', { params }).then(rs => {
                    console.log(rs);
                    toast1.clear;
                    Toast({
                        icon: 'checked',
                        message: '成功!',
                        forbidClick: true,
                    });
                })
                this.$router.replace('/')
            }, 2000)
        },
        checkAll() {
            this.checked = this.allId;
            this.submit(this.checked)
        },
    },
    mounted() {
        this.$axios.get('chaAllGet',).then(rs => {
            this.allChannels = rs
            console.log(rs);
            this.allId = rs.map(el => {
                return el.channelId
            })

        })
    }

}
</script>

<style scoped lang="less">
div.channelSelect {
    box-sizing: border-box;
    padding-top: 90px;
    min-height: 100%;

    div.top {
        box-sizing: border-box;
        background: grey;
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        padding: 20px 40px;
        height: 90px;

        p {
            text-align: center;
        }
    }

    div.main {
        padding: 20px 40px;

        :deep(.van-grid-item__content) {
            padding: 0;
        }

        div.box {
            position: relative;
            width: 100%;
            height: 100%;

            .van-checkbox-group {
                position: absolute;
                right: 5px;
                top: 5px;
            }

            span {
                position: absolute;
                bottom: 5px;
                left: 5px;
                color: white;
            }
        }
    }

    div.bottom {
        box-sizing: border-box;
        background: grey;
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        padding: 10px 40px;

        button {
            margin-bottom: 10px;
        }
    }
}
</style>