<template>
  <div class="announceList wrapper pt">
    <van-nav-bar title="公告管理" left-text="返回" left-arrow @click-left="onClickLeft" fixed />
    <van-cell is-link @click="show = true">增加公告</van-cell>
    <van-popup v-model:show="show" position="top" :style="{ height: '100%' }" closeable>
      <van-form @submit="onSubmit" style="margin-top:30vh">
        <van-cell-group inset>
          <van-field v-model="type" name="类型" label="类型" placeholder="类型"
            :rules="[{ required: true, message: '请填写类型' }]" />
          <van-field v-model="message" rows="1" autosize label="内容" type="textarea" placeholder="请输入内容"
            :rules="[{ required: true, message: '请输入内容' }]" />
          <van-field name="switch" label="最新">
            <template #input>
              <van-switch v-model="checked" size="20" />
            </template>
          </van-field>
        </van-cell-group>
        <div style="margin: 16px;">
          <van-button round block type="primary" native-type="submit">
            提交
          </van-button>
        </div>
      </van-form>
    </van-popup>
    <van-cell-group title="当前公告">
      <div v-for="el in showList " :key="el.id">
        <van-swipe-cell :before-close="beforeClose" stop-propagation :name="el.inSwipe + '-' + el.articleId">
          <van-card :title="el.type">
            <template #num>
              {{ untilMin(el.updatedAt) }}
            </template>
            <template #desc>
              <p class="van-multi-ellipsis--l2">{{ el.message }}</p>
            </template>
          </van-card>
          <template #right>
            <van-button square type="danger" text="编辑" class="delete-button" @click="edit(el)" />
          </template>
          <template #left>
            <van-button square type="danger" text="删除" class="delete-button" @click="del(el)" />
          </template>
        </van-swipe-cell>
      </div>
    </van-cell-group>
    <van-cell-group title="其他公告">
      <div v-for="el in otherList " :key="el.id">
        <van-swipe-cell :before-close="beforeClose" stop-propagation :name="el.inSwipe + '-' + el.articleId">
          <van-card :title="el.type">
            <template #num>
              {{ untilMin(el.updatedAt) }}
            </template>
            <template #desc>
              <p class="van-multi-ellipsis--l2">{{ el.message }}</p>
            </template>
          </van-card>
          <template #right>
            <van-button square type="danger" text="编辑" class="delete-button" @click="edit(el)" />
          </template>
          <template #left>
            <van-button square type="danger" text="删除" class="delete-button" @click="del(el)" />
          </template>
        </van-swipe-cell>
      </div>
    </van-cell-group>
  </div>
</template>

<script>
import { Dialog } from 'vant'
export default {
  name: 'AnnounceView',
  inject: ['untilMin'],
  data() {
    return {
      showList: [],
      otherList: [],
      show: false,
      message: '',
      type: '',
      checked: false,
      editing: false,
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    beforeClose() {

    },
    async onSubmit() {
      let params;
      if (!this.editing) {
        params = { message: this.message, type: this.type, show: this.checked };
      }
      else {
        params = { message: this.message, type: this.type, show: this.checked, id: this.target.id };
      }
      await this.$axios2.get('announceAdd', { params });
      history.go(0);
    },
    edit(obj) {
      this.show = true;
      this.$nextTick(() => {
        this.editing = true;
        this.target = obj;
        this.type = obj.type;
        this.message = obj.message;
        this.checked = obj.show;
      })
    },
    del(obj) {
      Dialog.confirm({
        title: '确定吗',

      })
        .then(() => {
          this.$axios2.get('announceDel', { params: { id: obj.id } });
          history.go(0);
        })
        .catch(() => {
        });

    }
  },
  mounted() {
    this.$axios2.get('announceGet',).then(rs => {
      this.showList = rs[0];
      this.otherList = rs[1];
    });
  },

}
</script>

<style scoped lang="less">
.delete-button {
  height: 100%;
}
</style>