<template>
    <div class="personalInfo">
        <van-cell center clickable :to="'/personal/' + userInfo.userId">
            <template #icon>
                <van-image round width="1.5rem" height="1.5rem"
                    :src="avatarGet(userInfo.avatar)" fit="cover"  />
            </template>
            <template #title>
                <div class="text ">
                    <p class="p5">{{userInfo.nickname}}
                        <van-icon class-prefix="iconfont icon-nan" size="0.5rem" color="blue"
                            v-if="userInfo.sex==='男'" />
                        <van-icon class-prefix="iconfont icon-nv" size="0.5rem" color="pink"
                            v-if="userInfo.sex==='女'" />
                    </p>
                    <span class="p6g  van-ellipsis" style="width:5rem;">{{ userInfo.signature}}</span>
                </div>
            </template>
            <template #value>
                <van-button type="primary" size="small" @click.stop :icon="followed?'success':'plus'"
                    :color="followed?'grey':'#000'" @click="follow(userInfo.userId)">{{followed?'已关注':'关注Ta'}}
                </van-button>
            </template>
        </van-cell>
    </div>
</template>

<script>
import { Dialog } from 'vant'
import { Toast } from 'vant'
export default {
    name: 'PersonalInfo',
    inject: ['avatarGet'],
    props: {
        userInfo: {
            type: Object
        }
    },
    data() {
        return {
            myFollows: [],
            followed: false,//已经关注
            targetFans: [],
        }
    },
    methods: {
        async follow(id) {
            if (!this.$store.state.user) {
                this.loginState();
            }
            else if (id == this.$store.state.user.userId) {
                Toast('不能粉自己哦！')
            }
            else {
                await this.targetFansGet(id);//获取点击用户的粉丝
                // console.log(this.targetFans);
                if (this.followed) {
                    await Dialog.confirm({
                        title: '取消关注',
                    })
                        .then(() => {
                            this.targetFans = this.targetFans.filter(el => {
                                return el != this.$store.state.user.userId
                            })
                            this.myFollows = this.myFollows.filter(el => {
                                return el != id
                            })
                            this.followed = false;
                            // console.log(this.myFollows,this.targetFans);
                        })
                        .catch(() => {
                            return
                        });
                }
                else {
                    this.targetFans.push(this.$store.state.user.userId);
                    this.myFollows.push(id);
                    this.followed = true;
                    // console.log(this.myFollows, this.targetFans);
                }
                console.log(this.myFollows, this.targetFans);
                this.subscribeUpdate(id);
            }
        },
        loginState() {
            Dialog.confirm({
                title: '未登录',
                message:
                    '你必须要登录才能使用此功能，是否前去登录?',
            })
                .then(() => {
                    this.$router.replace('/login');
                })
                .catch(() => {
                    return
                });
        }, // 登录状态检测
        subscribeUpdate(id) {
            const params = { fan: this.targetFans, follow: this.myFollows, userId: this.$store.state.user.userId, authorId: id }
            this.$axios.get('subscribeUpdate', { params })
        },//关注与粉丝更新
        async targetFansGet(userId) {//目标的粉丝获取
            const params = { userId }
            await this.$axios.get('subscribeListGet', { params }).then(rs => {
                this.targetFans = rs[1];
            })
        },
        async subscribeGet() {
            const params = { userId: this.$store.state.user.userId }
            await this.$axios.get('subscribeListGet', { params }).then(rs => {//获取用户关注列表
                this.myFollows = rs[0];
                if (rs[0].indexOf(this.userInfo.userId + '') > -1)
                    this.followed = true;
            })
        },
    },
    mounted() {
        if (this.$store.state.user)
            this.subscribeGet();//获得我的关注与粉丝
    },
}
</script>

<style scoped lang="less">
div.text {
    box-sizing: border-box;
    padding: 10px;
    display: flex;
    flex-flow: column;
}
</style>