<template>
    <div class="collectDetail wrapper pt">
        <van-nav-bar :title="boxName" left-arrow @click-left="onClickLeft" fixed>
            <template #right>
                <van-icon name="weapp-nav" size="18" color='black' @click='this.show = true' v-if="!isCollect" />
                <span v-else @click='this.isCollect = false'>完成</span>
            </template>
        </van-nav-bar>
        <van-tabs v-model:active="active" swipeable shrink class="bulk" id="collectDetailBulk">
            <van-tab title="收藏顺序">
                <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">加载中...</van-loading>
                <div v-show="!loading">
                    <van-empty description="暂无收藏" style="height:10rem" v-if='list.length === 0' />
                    <div>
                        <PostList :ArticleList="list" :isCollect="isCollect" ref="PostList" :key="key"></PostList>
                    </div>
                </div>
            </van-tab>
            <van-tab title="最近更新">
                <van-empty description="暂无收藏" style="height:10rem" />
            </van-tab>
        </van-tabs>
        <!-- //操作选项 -->
        <van-action-sheet v-model:show="show" :actions="actions" cancel-text="取消" close-on-click-action title="管理"
            @select="onSelect" />
        <van-dialog v-model:show="dialogShow" title="修改名称" show-cancel-button theme="round-button"
            confirm-button-color="#000" confirm-button-text="修改" cancel-button-color="grey" @confirm="createConfirm">
            <template #default>
                <van-field v-model="cbTitle" placeholder="收藏夹名字(8字内)" required size="large" maxlength='8'
                    input-align="center" />
            </template>
        </van-dialog>
        <!-- 收藏组件 -->
        <CollectBox ref="CollectBox" @move="move" :key="key"></CollectBox>
        <div class="bottomBtn" v-show="isCollect">
            <van-row gutter="40" justify="center">
                <van-col span="12">
                    <van-button type="default" block @click="showSelect">移动</van-button>
                </van-col>
                <van-col span="12">
                    <van-button type="danger" block @click="delConfirm">删除</van-button>
                </van-col>
            </van-row>
        </div>
    </div>
</template>

<script>
import PostList from '@/components/PostList.vue'
import { Toast } from 'vant';
import { Dialog } from 'vant';
import CollectBox from '@/components/CollectBox.vue';
export default {
    name: 'CollectDetail',
    inject: ['reload'],
    components: {
        PostList,
        CollectBox
    },
    data() {
        return {
            dialogShow: false,//名称修改
            cbTitle: '',//收藏夹名(给修改名称使用)
            show: false,//收藏夹管理动作面板
            boxName: '',
            active: 0,
            collectBox: [],
            list: [],
            isCollect: false,
            actions: [//操作选项
                // { name: '修改名称' },
                { name: '批量操作' },
                { name: '删除所有失效内容' },
                { name: '删除收藏夹', color: 'red' },
            ],
            checkedArr: [],
            loading: true,
            key: 1,
            scrollTop: 0,
            overdue: [],
        }
    },
    methods: {
        showSelect() {//显示收藏夹选择框收藏
            this.checkedArr = this.$refs.PostList.checkedId;
            if (!this.checkedArr.length)
                return
            this.$refs.CollectBox.actionsShow = true;
        },
        async move(name) {
            const params = { userId: this.$store.state.user.userId, boxName: this.boxName, URL: this.$store.state.URL }
            await this.$axios('collectDetail', { params }).then(rs => {
                this.collectBox = rs[1];
            })
            if (name === this.boxName) {
                Toast('无效移动！')
                return
            }
            Dialog.confirm({
                title: '移动收藏',
                message:
                    '确定将选中的内容内容移动至《' + name + '》收藏夹吗？',
            })
                .then(() => {
                    this.collectBox = this.collectBox.map(el => {
                        if (el.name === name)
                            el.content = [...el.content, ...this.checkedArr]
                        return el
                    })
                    this.del();
                    // this.checkedArr.length.length=0;

                    this.collectUpdate()
                }).catch(() => {
                });
        },
        del() {
            this.collectBox = this.collectBox.map(el => {
                if (el.name === this.boxName)
                    el.content = el.content.filter(item => {
                        return this.checkedArr.indexOf(item) === -1//返回没有被选中的id
                    })
                this.list = this.list.filter(item => {
                    return this.checkedArr.indexOf(item.articleId) === -1//改变list
                })
                return el
            })
            this.$refs.PostList.checkedId.length = 0;
        },
        collectUpdate() {
            const params = { userId: this.$store.state.user.userId, actions: this.collectBox }
            this.$axios('collectUpdate', { params }).then(rs => {
                console.log('收藏夹更新了：', rs);
                Toast('收藏更新！');
            })
        },
        delConfirm() {//删除确认
            this.checkedArr = this.$refs.PostList.checkedId;
            if (!this.checkedArr.length)
                return
            Dialog.confirm({
                title: '移出收藏',
                message:
                    '确定将选中的内容内容移除收藏吗？',
            })
                .then(() => {
                    this.del()
                    // this.checkedArr.length;
                    this.collectUpdate()
                }).catch(() => {
                    // on cancel
                });
        },
        onClickLeft() {
            this.$router.back();
        },
        createConfirm() {//确认修改名称
            if (this.cbTitle === '') {
                Toast('收藏夹名不能为空！');
                return
            }
            let arr = []
            arr = this.collectBox.filter(el => {
                return el.name === this.cbTitle;
            })
            if (arr.length !== 0) {
                Toast('同名收藏夹已存在！');
                return
            }
            arr = this.collectBox.map(el => {
                if (el.name === this.boxName) {
                    el.name = this.cbTitle;
                    this.boxName = this.cbTitle
                }
                return el
            })
            const params = { actions: this.collectBox, userId: this.$store.state.user.userId }
            this.$axios.get('collectUpdate', { params })
        },
        onSelect(item) {//操作选项
            if (item.name === '删除收藏夹') {
                Dialog.confirm({
                    title: '收藏夹删除！',
                    message:
                        '删除收藏夹后，该收藏夹中的内容也会一并删除。操作不可逆！',
                })
                    .then(() => {
                        let arr = this.collectBox.filter(el => {
                            return el.name !== this.boxName
                        })
                        const params = { userId: this.$store.state.user.userId, actions: arr }
                        this.$axios('collectUpdate', { params }).then(rs => {
                            console.log('删除了', rs);
                            Toast('删除成功！');
                            this.$router.back();
                        })
                    })
                    .catch(() => {

                    });
            }
            if (item.name === '修改名称') {
                this.dialogShow = true;
            }
            if (item.name === '批量操作') {
                this.isCollect = true;
            }
            if (item.name === '删除所有失效内容') {
                this.collectBox = this.collectBox.filter(el => {
                    if (el.name === this.boxName)
                        el.content = el.content.filter(item => {
                            return this.overdue.indexOf(item) === -1;
                        })
                    return el
                })
                console.log(this.collectBox);
                this.list = this.list.filter(item => {
                    return !item.notExist
                })
                this.collectUpdate()
            }
        },
        loaded() {
            if (this.loading === true)
                setTimeout(() => {
                    this.loading = false;
                }, 500)
        },
        dataGet() {
            this.boxName = this.$route.query.boxName;
            if (this.boxName === '默认收藏夹') {
                this.actions = [{ name: '批量操作' }, { name: '删除所有失效内容' },]
            }
            const params = { userId: this.$store.state.user.userId, boxName: this.boxName, URL: this.$store.state.URL }
            this.$axios('collectDetail', { params }).then(rs => {
                this.collectBox = rs[1];
                this.list = rs[0];
                this.overdue = rs[2];
                this.loaded();
            })
        }
    },
    mounted() {
        this.dataGet()
    },
    activated() {
        if (this.$route.meta.isBack === false) {//回到你原来的模样
            Object.assign(this.$data, this.$options.data())
            this.dataGet();
            this.key = this.reload()
        }
        else {
            this.$nextTick(() => {
                document.querySelector("#collectDetailBulk").scrollTop = this.scrollTop;
            })
        }
    },
    beforeRouteEnter(to, from, next) {
        if (from.path.indexOf('myCollect') !== -1) { to.meta.isBack = false; }
        next()
    },
    deactivated() {
        this.scrollTop = document.querySelector("#collectDetailBulk").scrollTop
    },
    updated() {

    }
}
</script>

<style scoped lang="less">
div.bottomBtn {
    position: fixed;
    height: 50px;
    width: 10rem;
    bottom: 0;
    background: rgb(232, 232, 232);
    padding: 5px 0;
}
</style>