<template>
    <div class="articlesList wrapper pt">
        <van-nav-bar title="文章管理与审核" left-text="返回" left-arrow @click-left="onClickLeft"
            :right-text="managing ? '管理' : '审核'" fixed @click-right="managing = !managing" />
        <div v-if="!managing">
            <div v-for="el in list" :key="el.articleId">
                <van-cell is-link :to="'/article/' + el.articleId" :label="el.updatedAt">
                    <template #title>
                        <span class="custom-title van-multi-ellipsis--l2">{{ el.title }}</span>
                    </template>
                </van-cell>
            </div>
        </div>
        <div v-if="managing">
            <van-cell title="在轮播">
                <template #right-icon>
                    <van-icon name="checked" color="blue" size="0.5rem" />
                </template>
            </van-cell>
            <div v-for="el in list2" :key="el.articleId">
                <van-swipe-cell :before-close="beforeClose" stop-propagation :name="el.inSwipe+'-'+el.articleId">
                    <van-cell is-link :to="'/article/' + el.articleId" :label="el.updatedAt">
                        <template #title>
                            <span class="custom-title van-multi-ellipsis--l2">{{ el.title }}</span>
                        </template>
                    </van-cell>
                    <template #right>
                        <van-button square type="danger" :text="el.inSwipe?'下轮播':'上轮播'" />
                    </template>
                </van-swipe-cell>
            </div>
            <br>
            <van-cell title="不在轮播">
                <template #right-icon>
                    <van-icon name="clear" color="red" size="0.5rem" />
                </template>
            </van-cell>
            <div v-for="el in list3" :key="el.articleId">
                <van-swipe-cell :before-close="beforeClose" stop-propagation :name="el.inSwipe+'-'+el.articleId">
                    <van-cell is-link :to="'/article/' + el.articleId" :label="el.updatedAt">
                        <template #title>
                            <span class="custom-title van-multi-ellipsis--l2">{{ el.title }}</span>
                        </template>
                    </van-cell>
                    <template #right>
                        <van-button square type="primary" :text="el.inSwipe?'下轮播':'上轮播'" />
                    </template>
                </van-swipe-cell>
            </div>
        </div>
    </div>
</template>

<script>
import dayjs from '../../../assets/util/public'
import { Dialog } from 'vant';
export default {
    name: 'articlesList',
    data() {
        return {
            list: [],
            list2: [],
            list3: [],
            managing: true,
        }
    },
    methods: {
        onClickLeft() {
            this.$router.back();
        },
        beforeClose({name,position}) {
            switch (position) {
                case 'left':
                case 'cell':
                case 'outside':
                    return true;
                case 'right':
                    console.log(name.split('-'));
                    if(name.split('-')[0]==='false') 
                    Dialog.confirm({
                        title: '上轮播',
                        message:
                            '确定将此文章发布到首页轮播图吗?',
                    })
                        .then(() => {
                            const params = { articleId: name.split('-')[1],action:'up'}
                            this.$axios2.get('swipeKa',{params}).then(re=>{
                                console.log(re);
                                this.$router.go(0);
                            })
                        })
                        .catch(() => {
                        });
                    else   
                        Dialog.confirm({
                            title: '下轮播',
                            message:
                                '确定将此文章从轮播图上撤下吗?',
                        })
                            .then(() => {
                                const params = { articleId: name.split('-')[1], action: 'down' }
                                this.$axios2.get('swipeKa', { params }).then(re => {
                                    console.log(re);
                                    this.$router.go(0);
                                })
                            })
                            .catch(() => {
                            });
            }
        },
    },
    mounted() {
        if (this.$route.query.managing === 'false')
            this.managing = false
        else
            this.managing = true
        this.$axios2.get('articlesList').then(rs => {
            this.list = rs[0].map(el => {
                el.updatedAt = dayjs(el.updatedAt).format('YYYY-MM-DD HH:mm:ss');
                return el
            });
            this.list2 = rs[1].map(el => {
                el.updatedAt = dayjs(el.updatedAt).format('YYYY-MM-DD HH:mm:ss');
                return el
            });
            this.list3 = rs[2].map(el => {
                el.updatedAt = dayjs(el.updatedAt).format('YYYY-MM-DD HH:mm:ss');
               
                return el
            });
        })

    },

}
</script>

<style scoped lang="less">
:deep(.van-swipe-cell__right button) {
    height: 100%;
}
</style>