
<template>
    <div class="channelEdit ">
        <van-cell title="我的频道" />
        <van-grid :gutter="10" class="p4" clickable>
            <!-- <van-grid-item class="grid" v-show="!select"><template #text>全部</template></van-grid-item> -->
            <van-grid-item v-for="el in myChannels" :key="el.channelId" class="grid">
                <template #default>
                    <div @click="clearOrGo(el.channelId)" class="box">
                        <van-icon name="clear" class="clear" size="0.5rem" v-show="isEdit" color="red" />
                        <p>{{ el.channelName }}</p>
                    </div>
                </template>
            </van-grid-item>
        </van-grid>
        <van-cell title="推荐频道" />
        <van-grid :gutter="10" class="p4" clickable>
            <van-grid-item v-for="el in recChannels" :key="el.channelId" class="grid">
                <template #default>
                    <div @click="addOrGo(el.channelId)" class="box">
                        <van-icon name="add" class="add" size="0.5rem" v-show="isEdit" color="blue" />
                        <p>{{ el.channelName }}</p>
                    </div>
                </template>
            </van-grid-item>
        </van-grid>
    </div>
</template>

<script>

export default {
    name: 'ChannelEdit',
    props:{
        my: {
                type: Array,
            },
        isEdit: {
            type: Boolean,
            default:false
        },
        isSelect:{
            type:Boolean,
            default:false,
        }
    },
    data() {
        return {
            myChannels:this.my,
            allChannels:[],
            select:this.isSelect,
            selectedCom:[],
        }
    },
    methods: {
        clear(index) {
            let arr = [];
            arr = this.myChannels.filter(el => {
                return el.channelId !== index
            });
            this.myChannels = arr;
        },
        go(index) {//选择频道时使用
            if(this.select===true){
                this.selectedCom=this.allChannels.filter(el=>{
                   return el.channelId===index;
                })
                this.$emit('hidPop')
            }
        },
        add(index) {
            this.allChannels.forEach(el => {
                if (el.channelId === index)
                    this.myChannels.push(el);
            });
        },
        clearOrGo(index) {
            if (this.isEdit === true)
                this.clear(index)
            else
                this.go(index)
        },
        addOrGo(index) {
            if (this.isEdit === true)
                this.add(index)
            else
                this.go(index)
        }
    },
    mounted() {
        this.$axios.get('chaAllGet',).then(rs => {
            this.allChannels = rs
        })
    },
    computed: {
        recChannels() {
            let A = this.allChannels.map(el => {
                return el.channelId;
            })
            let B = this.myChannels.map(el => {
                return el.channelId;
            })
            let arr = A.filter(v => !B.find((item) => item === v))
            let obj = []
            this.allChannels.forEach(el => {
                if (arr.indexOf(el.channelId) !== -1)
                    obj.push(el);
            })
            return obj;
        },
    },
   
}
</script>

<style scoped lang="less">


 :deep(.grid .van-grid-item__content) {
     padding: 5px;
 }

 .channelEdit {

     .box {
         display: flex;
         flex-direction: column;
         justify-content: center;

         i {
             margin: auto;
         }
     }
 }
</style>
