import { createStore } from 'vuex'
import { getItem, setItem } from '../assets/util/storage'
const axios = require('axios');
const USER_KEY = 'USER_KEY'
const HISTORY_KEY = 'HISTORY_KEY'
const SCROLL_KEY = 'SCROLL_KEY'
const DATE_KEY = 'DATE_KEY';
const SETUP_KEY = 'SETUP_KEY';
export default createStore({
  state: {
    size: 10,
    URL: 'http://47.102.115.96:1337/',
    // URL: 'http://192.168.1.107:1337/',
    user: getItem(USER_KEY),
    history: getItem(HISTORY_KEY),//搜索历史
    lastDate:getItem(DATE_KEY),
    include: [],
    setup: getItem(SETUP_KEY),
    imageType: ['image/jpeg','image/png','image/gif','image/webp'],
    scrollLocation: getItem(SCROLL_KEY),
    communityClass: ['推荐', '生活', '游戏', '数码科技', '影视动漫'],
    isBack: getItem('BACK_KEY'),//是否是返回操作
  },
  getters: {
  },
  mutations: {
    setUser(state, data) {
      state.user = data;
      setItem(USER_KEY, JSON.stringify(state.user));
    },
    setHistory(state, data) {//搜索的历史记录
      state.history = data;
      setItem(HISTORY_KEY, JSON.stringify(state.history));
    },
    setLastDate(state, data) {//搜索的历史记录
      state.lastDate = data;
      setItem(DATE_KEY, JSON.stringify(state.lastDate));
    },
    setSetUp(state, data) {//搜索的历史记录
      state.setup = data;
      setItem(SETUP_KEY, JSON.stringify(state.setup));
    },
    setScroll(state, data) {
      state.scrollLocation = data;
      setItem(SCROLL_KEY, state.scrollLocation);
    },
    setBack(state, data) {
      state.isBack = data;
      setItem('BACK_KEY', state.isBack)
    },
    includeAdd(state, data) {
      if (state.include.indexOf(data) === -1) {
        state.include.push(data)
      }
    },
    includeDel(state, data) {
      if (state.include.indexOf(data) !== -1);
      state.include.splice(state.include.indexOf(data), 1);
    },

    UserRefresh(state, data) {//本地用户数据刷新，暂时废弃
      console.log('data:', data);
      let newData;
      axios.get(state.URL + 'users/userDataRefresh', { params: { userId: data } }).then(rs => {
        console.log(rs);
        newData = rs;
        console.log(newData.data);
        this.commit('setUser', newData.data)
      })

    }
  },
  actions: {
  },
  modules: {
  }
})
