<template>
    <div class="postList ">
        <div class="postItem" v-for="el in ArticleList" :key="el.articleId">
            <div class="exist" v-if="!el.notExist">
                <div class="author">
                    <div class="aBox">
                        <div class="left" @click.stop="goPersonal(el.authorId)">
                            <van-image round width="0.8rem" height="0.8rem" fit="cover" :src="avatarGet(el.avatar)"
                                lazy-load />
                            <span class="p5">{{ el.authorName }}</span>
                        </div>
                        <div class="inputBox" v-if="isCollect">
                            <input type="checkbox" :value="el.articleId" v-model="checkedId" @click.stop />
                        </div>
                        <!-- </router-link> -->
                    </div>
                </div>
                <div class="main" @click="goArticle(el.articleId)">
                    <div class="title p4 van-multi-ellipsis--l2">
                        {{ el.title }}
                    </div>
                    <div class="text p5g">
                        <p class=" van-multi-ellipsis--l3"> {{ el.text }}</p>
                    </div>
                    <div class="imgs">
                        <van-row style="width:100%" gutter="5">
                            <van-col span="8" v-for="(img, index) in el.urls" :key="img" style="position:relative"
                                @click.stop="imageView(el.urls, index)">
                                <p class="imgCount p6" v-show="index === 2 && el.urls.length > 3">{{
                                        el.urls.length > 10 ? '10+' : el.urls.length
                                }}张</p>
                                <van-image width="100%" height="1.8rem" fit="cover" :src="img" lazy-load
                                    radius="0.08rem" v-if="index < 3" />
                            </van-col>
                        </van-row>
                    </div>
                    <div class="bottom">
                        <span class="p6g">{{ before(el.createdAt) }}-&nbsp;-{{ el.channelName }}</span>
                        <div class="p5g">
                            <van-icon name="comment">&nbsp;{{ el.commentCount }}</van-icon>&nbsp;
                            <van-icon name="good-job">&nbsp;{{ el.likeCount }}</van-icon>
                        </div>
                    </div>
                </div>
            </div>
            <div v-else class="notExist">
                <div class="info" style="color:red">
                    <span>抱歉，该内容已被删除或失效！</span>
                </div>
                <van-skeleton title :row="1" :animate="false" />
            </div>
        </div>
    </div>
</template>

<script>
import { ImagePreview } from 'vant';
export default {
    name: 'postList',
    inject: ['before', 'goPersonal', 'goArticle', 'avatarGet'],
    props: {
        ArticleList: {
            type: Array,
        },
        isCollect: {
            type: Boolean,
            default: false,
        }
    },
    data() {
        return {
            checkedId: [],
        }
    },
    methods: {

        imageView(urls, start) {
            ImagePreview({
                images: urls,
                startPosition: start,
                closeable: true,
            });
        }
    },
    mounted() {
        // console.log('postlist重渲染！');
    },
}
</script>

<style scoped lang="less">
div.postList {
    div.postItem {
        padding: 10px;
        background: white;
        margin-top: 10px;

        >.exist>div {
            padding: 5px 0;
        }

        div.title {
            padding: 5px 0 0 0 !important;
        }

        div.text {
            padding: 5px 0!important;
        }

        div.author {
            div.aBox {
                display: flex;
                align-items: center;
                justify-content: space-between;

                div.left {
                    display: flex;
                    align-items: center;

                    >span {
                        padding-left: 5px;
                    }
                }

            }

        }

        div.imgs {
            .imgCount {
                position: absolute;
                z-index: 100;
                width: 35px;
                display: flex;
                justify-content: center;
                align-items: center;
                height: 15px;
                background: black;
                right: 0;
                top: 0;
                color: white;
            }
        }

        div.bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    div.notExist {
        position: relative;

        div.info {
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
        }
    }
}
</style>