<template>
    <div class="myCollect wrapper pt">
        <van-nav-bar title="我的收藏" left-arrow @click-left="onClickLeft" fixed />
        <van-cell-group>
            <van-cell center :title="el.name" :label="el.content.length + '篇'" is-link v-for="el in collectBox"
                :key='el' :to="{ path: '/collectDetail', query: { boxName: el.name } }" />
        </van-cell-group>
        <van-button type="primary" block icon="plus" color="black" style="margin-top:1rem" @click="createCollect">创建收藏夹
        </van-button>
        <van-dialog v-model:show="dialogShow" title="创建收藏夹" show-cancel-button theme="round-button"
            confirm-button-color="#000" confirm-button-text="创建" cancel-button-color="grey" @confirm="createConfirm">
            <template #default>
                <van-field v-model="cbTitle" placeholder="收藏夹名字(8字内)" required size="large" maxlength='8'
                    input-align="center" />
            </template>
        </van-dialog>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    name: 'MyCollect',
    data() {
        return {
            collectBox: [],
            dialogShow: false,//收藏夹名的确认与取消
            cbTitle: '',//收藏夹名
        }
    },
    methods: {
        onClickLeft() {
            this.$router.back();
        },
        createCollect() {
            this.dialogShow = true;
        },
        createConfirm() {//确认创建收藏夹
            if (this.cbTitle === '') {
                Toast('收藏夹名不能为空！');
                return
            }
            let obj = { name: this.cbTitle, content: [] }
            let arr = this.collectBox.filter(el => {
                return el.name === this.cbTitle;
            })
            if (arr.length !== 0) {
                Toast('同名收藏夹已存在！');
                return
            }
            this.collectBox.splice(this.collectBox.length, 0, obj)
            const params = { actions: this.collectBox, userId: this.$store.state.user.userId }
            this.$axios.get('collectUpdate', { params })
        },
    },
    mounted() {
        console.log('mounted');
        const params = { userId: this.$store.state.user.userId }
        this.$axios('articleState', { params }).then(rs => {//获得用户的收藏夹
            console.log('该用户的收藏夹为：', rs[0]);
            this.collectBox = rs[0]
        })
    },
    activated() {
        console.log('收藏夹缓存');
    }
}
</script>

