
<template>
    <div class="CandR">
        <div class="top">
            <p class="p4"><span>评论 {{ comCount }}</span>
                <van-icon name="sort" size=".32rem" color="grey" @click="sortActionShow = true">{{ sort }}</van-icon>
            </p>
        </div>
        <van-action-sheet v-model:show="sortActionShow" :actions="sortActions" @select="onSelect" cancel-text="取消"
            close-on-click-action />
        <div class="comments">
            <!-- 临时评论 -->
            <!-- <div class="commentItem tempComment" v-if="tempComment.userInfo">
                <div class="author" v-if="tempComment.userInfo">
                    <div class="avatarBox">
                        <van-image width="1rem" height="1rem" round fit="cover" lazy-load
                            :src="this.$store.state.URL + 'upload/avatar/' + tempComment.userInfo.avatar" />
                    </div>
                    <div class="info">
                        <p class="p5b">{{ tempComment.userInfo.nickname }}</p>
                        <p class="p6g postTime">{{ dayjs(tempComment.date) }}</p>
                    </div>
                    <div class="like">
                        <van-button size="small" color="transparent">
                            <template #default>
                                <span class="p5g">0</span>
                            </template>
                            <template #icon>
                                <van-icon name="good-job" color="grey" />
                            </template>
                        </van-button>
                    </div>
                </div>
                <div class="content p5 ">
                    <p class="message">{{ tempComment.message }}</p>
                    <div class="imageBox" v-if="tempImages.length !== 0">
                        <van-image width="1.7rem" height="1.7rem" fit=" cover" :src="img" radius=".08rem"
                            v-for=" (img) in tempImages" :key="img" lazy-load>
                        </van-image>
                    </div>
                </div>
            </div> -->
            <!-- 评论 -->
            <van-list v-model:loading="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                <div class="commentItem" v-for="el in comments" :key="el.commentId">
                    <div class="author" v-if="el.userInfo.avatar === '' || el.userInfo.avatar">
                        <div class="avatarBox">
                            <router-link to="">
                                <van-image width="1rem" height="1rem" round fit="cover" lazy-load
                                    :src="avatarGet(el.userInfo.avatar)" />
                            </router-link>
                        </div>
                        <div class="info">
                            <router-link :to="'/personal/' + el.userInfo.userId" class="alignCenter">
                                <span class="p5b">{{ el.userInfo.nickname }}</span>
                                <span class="isAuthor p8">{{
                                        el.userInfo.userId === authorId ? '作者' : null
                                }}</span>
                            </router-link>
                            <p class="p6g postTime">{{ before(+el.date) }}</p>
                        </div>
                        <div class="like">
                            <van-button size="small" color="transparent" @click="commentLike(el.commentId)">
                                <template #default>
                                    <span class="p5g">{{ el.like }}</span>
                                </template>
                                <template #icon>
                                    <van-icon name="good-job" :color="likeState(el.commentId)" />
                                </template>
                            </van-button>
                        </div>
                    </div>
                    <div class="content p5"
                        @click.stop="replying(el.commentId, el.userInfo.userId, el.userInfo.avatar, el.message, el.userInfo.nickname)">
                        <p class="message">{{ el.message }}</p>
                        <div class="imageBox" v-if="el.images">
                            <van-image width="1.7rem" height="1.7rem" v-for=" (img, index) in el.images" fit=" cover"
                                :key="img" lazy-load :src="img" radius=".08rem"
                                @click.stop="imgPreview(el.images, index)">
                            </van-image>
                        </div>
                        <div class="reply p5-5"
                            v-if="(el.reply && el.reply.length) || el.commentId === tempReply.commentId - 0">
                            <div class="replyBox" v-for="(item, index) in el.reply" :key="item"
                                :style="{ 'padding-bottom': index < 3 ? '0.2rem' : 0 }"
                                @click.stop="replying(el.commentId, item.speaker.id, item.speaker.avatar, item.replyMessage, item.speaker.nickname)">
                                <div class="replyContent"
                                    v-if="this.collapseId.indexOf(el.commentId) > -1 ? index >= 0 : index < 3">
                                    <p class="speaker">
                                        <router-link :to="'/personal/' + item.speaker.id" @click.stop>
                                            <van-image width=".6rem" height=".6rem" lazy-load round fit="cover"
                                                :src="avatarGet(item.speaker.avatar)" />
                                        </router-link>
                                        <span>
                                            <router-link :to="'/personal/' + item.speaker.id" class="p5-5g alignCenter"
                                                @click.stop>
                                                <span>
                                                    {{ item.speaker.nickname }}
                                                </span>
                                                <span class="isAuthor p8">{{ item.speaker.id === authorId ? '作者' : null
                                                }}</span>
                                            </router-link>
                                        </span>
                                    </p>
                                    <p class="replyMessage">
                                        <span class="huifu" v-if="item.listener.id !== el.userInfo.userId">回复</span>
                                        <span v-if="item.listener.id !== el.userInfo.userId">
                                            <router-link :to="'/personal/' + item.listener.id" class="p5-5a">
                                                @{{ item.listener.nickname }}
                                            </router-link>
                                        </span>
                                        <span>{{
                                                item.replyMessage
                                        }}</span>
                                        <span class="p6g">{{ dayjs(item.date) }}</span>
                                    </p>
                                </div>
                                <van-button type="primary" block
                                    v-show="this.collapseId.indexOf(el.commentId) > -1 ? false : index == 3"
                                    icon="arrow-down" size="mini" @click.stop="unfold(el.commentId)">全部回复{{
                                            el.reply.length
                                    }}
                                </van-button>
                            </div>
                            <!-- 临时回复 -->
                            <div class="replyBox tempReply" v-if="el.commentId === tempReply.commentId - 0">
                                <p class="speaker">
                                    <router-link :to="'/personal/' + tempReply.speaker.id" @click.stop>
                                        <van-image width=".6rem" height=".6rem" lazy-load round fit="cover"
                                            :src="avatarGet(tempReply.speaker.avatar)" />
                                    </router-link>
                                    <span class="alignCenter">
                                        <router-link :to="'/personal/' + tempReply.speaker.id" class="p5-5g"
                                            @click.stop>
                                            {{ tempReply.speaker.nickname }}
                                            <span class="isAuthor p9">{{ tempReply.speaker.id === authorId ? '作者' : null
                                            }}</span>
                                        </router-link>
                                    </span>
                                </p>
                                <p class="replyMessage">
                                    <span class="huifu" v-if="tempReply.listener.id !== el.userInfo.userId">回复</span>
                                    <span v-if="tempReply.listener.id !== el.userInfo.userId">
                                        <router-link :to="'/personal/' + tempReply.listener.id" class="p5-5a">
                                            @{{ tempReply.listener.nickname }}
                                        </router-link>
                                    </span>
                                    <span>{{
                                            tempReply.replyMessage
                                    }}</span>
                                    <span class="p6g">{{ dayjs(tempReply.date) }}</span>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </van-list>
        </div>
        <van-popup v-model:show="show"
            :position="(this.$store.state.setup && this.$store.state.setup.system) ? 'top' : 'bottom'">
            <div class="replyFrame">
                <van-cell center title-style="flex:1" :border="false">
                    <template #default>
                        <div class="replyObj">
                            <van-image width="0.8rem" height="0.8rem" round fit="cover" lazy-load
                                :src="avatarGet(replyObj.avatar)" />
                            <span class="p5g van-ellipsis">{{ replyObj.message }}</span>
                        </div>
                        <van-field v-model="replyMessage" rows="3" autosize type="textarea"
                            :placeholder="'回复 @' + replyObj.nickname" ref="input"></van-field>
                    </template>
                </van-cell>
                <van-cell center>
                    <template #title>
                    </template>
                    <template #value>
                        <van-grid :border="false" center column-num="3" icon-size="0.5rem">
                            <van-grid-item icon="arrow-down" @click="loseFocus" />
                            <van-grid-item icon="smile-o" @mousedown.prevent="developing" />
                            <van-grid-item>
                                <template #default>
                                    <van-button type="primary" size="small" color="#000" @click="replySend">发送
                                    </van-button>
                                </template>
                            </van-grid-item>
                        </van-grid>
                    </template>
                </van-cell>
            </div>
        </van-popup>
    </div>
</template>
<script>
import dayjs from '../assets/util/public'
import { ImagePreview } from 'vant';
import { Toast } from 'vant';
import { Dialog } from 'vant'
import qs from 'qs'
export default {
    name: 'CommentReply',
    inject: ['before', 'isExistChecked', 'developing', 'avatarGet'],
    props: {
        ArticleId: {
            type: Number,
        },
        TempComment: {
            type: Object
        },
        TempImages: {
            type: Array,
        },
    },
    data() {
        return {
            articleId: this.ArticleId,
            comments: [],
            show: false,
            replyObj: {},
            replyMessage: '',//回复得信息内容
            tempComment: this.TempComment,//临时评论
            comCount: 0,//评论数
            authorId: 0,//作者Id
            tempReply: {},//临时回复
            collapseId: [], //展开回复按钮所属评论的Id,
            content: [],//所有该用户所有文章评论状态,
            commentState: {},//该文章的评论状态,
            exist: true,

            //底部加载
            loading: false,
            finished: false,
            size: this.$store.state.size,
            skip: 0,
            sortActionShow: false,
            sortActions: [
                { name: '热门', color: 'grey', subname: '最热在前' },
                { name: '正序', color: 'grey', subname: '最早在前' },
                { name: '倒序', color: 'black', subname: '最新在前' },
            ],
            sort: '倒序',
        }
    },
    methods: {
        onSelect(item) {
            switch (item.name) {
                case '热门':
                    this.comments.sort((a, b) => {
                        return b.like - a.like
                    })
                    break;
                case '正序':
                    this.comments.sort((a, b) => {
                        return (+a.date) - (+b.date)
                    })
                    break;
                case '倒序':
                    this.comments.sort((a, b) => {
                        return (+b.date) - (+a.date)
                    })
                    break;
            }
            this.sortActions.forEach(el => {
                if (el.name === item.name) {
                    this.sort = item.name
                    el.color = 'black';
                }
                else
                    el.color = 'grey'

            })
        },
        likeState(commentId) {
            if (this.commentState.commentLikedId)//挂载完成再启用
                if (this.commentState.commentLikedId.indexOf(commentId) === -1)
                    return 'grey'
                else
                    return 'black'
        },
        async commentLike(commentId) {//文章的点赞状态
            const rs = await this.isExistChecked(this.articleId)
            if (!rs) {
                return
            }
            if (!this.$store.state.user)
                this.loginState()
            else {
                let option;
                if (this.commentState.commentLikedId.indexOf(commentId) === -1) {//没点赞
                    this.commentState.commentLikedId.push(commentId)
                    option = 'plus';
                }
                else {
                    this.commentState.commentLikedId = this.commentState.commentLikedId.filter(el => {
                        return el !== commentId
                    })
                    option = 'minus';
                }
                if (this.exist)
                    this.content = this.content.map(el => {
                        if (el.articleId === this.articleId)
                            el.commentLikedId = [...this.commentState.commentLikedId]
                        return el
                    })
                else {
                    this.content.push(this.commentState)
                    this.exist = true;
                }
                const params = { articleId: this.articleId, userId: this.$store.state.user.userId, content: this.content, option, commentId }
                await this.$axios.get('comeLikedIdUpdate', { params })
                if (option === 'plus') {
                    this.comments.forEach(el => {
                        if (el.commentId === commentId)
                            el.like++;
                    })
                }
                else {
                    this.comments.forEach(el => {
                        if (el.commentId === commentId)
                            el.like--;
                    })
                }
            }
        },
        loginState() {
            Dialog.confirm({
                title: '未登录',
                message:
                    '你必须要登录才能使用此功能，是否前去登录?',
            })
                .then(() => {
                    this.$router.replace('/login');
                })
                .catch(() => {
                    return
                });
        }, // 登录状态检测
        unfold(id) {
            this.collapseId.push(id);
        },
        dayjs(date) {
            date = date - 0;
            const createdAt = dayjs(date)
            const now = dayjs().valueOf()
            const diff = createdAt.diff(now, 'day', true)
            if (diff <= -7)
                return dayjs(date).format('YYYY/MM/DD')
            return dayjs(date).fromNow()
        },
        imgPreview(arr, n) {
            ImagePreview({ images: arr, startPosition: n });
        },
        replying(commentId, listener, avatar, message, nickname) {
            this.commentId = commentId
            this.listener = listener;
            this.replyObj.avatar = avatar;
            this.replyObj.message = message;
            this.replyObj.nickname = nickname;
            this.$nextTick(() => {
                this.show = true;
                this.$nextTick(() => {
                    this.$refs.input.focus();
                })
            })
        },
        loseFocus() {
            this.$refs.input.blur();
        },
        async replySend() {
            const rs = await this.isExistChecked(this.articleId)
            if (!rs) {
                return
            }
            if (!this.$store.state.user)
                this.loginState()
            else {
                if (this.replyMessage === '') {
                    Toast('回复内容不能为空！');
                    return
                }
                if (this.tempReply.commentId) {
                    Toast('你说话太快啦，休息一下吧！')
                    return
                }
                let params = {
                    articleId: this.articleId,
                    commentId: this.commentId,
                    speaker: this.$store.state.user.userId,
                    listener: this.listener,
                    replyMessage: this.replyMessage,
                    repliedMessage: this.replyObj.message,//被回复的对象的信息
                    date: Date.now(),
                }
                let data = qs.stringify(params)
                this.$axios.post('addReply', data).then(rs => {
                    this.tempReply = rs;
                    Toast.success('回复成功！');
                    console.log('回复的内容：', rs);
                    this.replyMessage = '';
                    this.show = false;
                    // this.$emit('reload')
                    this.unfold(this.commentId)
                })
            }
        },
        async onLoad() {
            const params = { articleId: this.articleId, skip: this.skip, size: this.size }
            await this.$axios.get('getCom', { params }).then(rs => {
                setTimeout(() => {
                    this.comCount = rs[2];
                    rs[0] = rs[0].map(el => {
                        if (el.images)
                            el.images = el.images.map(item => {
                                item = this.$store.state.URL + 'upload/commentImgs/' + item;
                                return item
                            })
                        return el
                    })
                    this.comments = [...this.comments, ...rs[0]];
                    this.loading = false;
                    this.skip = this.skip + this.size
                    if (rs[0].length < this.size) {
                        this.finished = true;
                    }
                }, 100);
                this.authorId = rs[1];
            })
        },
    },
    mounted() {
        // this.commentGet();
        if (this.$store.state.user) {//评论的点赞情况
            const params = { userId: this.$store.state.user.userId, articleId: this.articleId }
            this.$axios.get('articleState', { params }).then(rs => {
                this.content = rs[3].concat()
                let arr = [];
                rs[3].forEach(el => {
                    if (el.articleId === this.articleId)
                        arr.push(el)
                })
                if (arr.length) {
                    this.commentState = { ...arr[0] }
                    this.exist = true;
                }//如果有该文章的评论的点赞信息的话
                else {
                    this.exist = false;
                    this.commentState = { articleId: this.articleId, commentLikedId: [] }
                }
            })
        }
        else
            this.commentState = { articleId: this.articleId, commentLikedId: [] }
        // console.log('评论组件重渲染了，作者ID：', this.authorId);
    },
    watch: {
        TempComment(newVal) {
            this.tempComment = newVal;
            if (this.tempComment.usrInfo)
                this.comCount = this.comCount + 1;
        },
    },
    computed: {
        tempImages() {
            return this.TempImages
        }
    },
    updated() {
        // console.log("评论组件更新了");
        this.$emit('reload')
    }
}
</script>

<style scoped lang="less">
.CandR {
    :deep(.replyFrame>.van-cell--borderless) {
        padding-top: 0;
        padding-bottom: 0;
    }

    .top {
        p {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 10px;

            span {
                padding: 10px 0px;
                border-bottom: 2px rgb(0, 0, 0) solid;
            }

            border-bottom: thin rgb(231, 231, 231) solid;
        }
    }

    .replyFrame {
        width: 10rem;
        z-index: 3;

        .replyObj {
            display: flex;
            align-items: center;
            padding: 5px 0;

            span {
                padding-left: 5px;
            }
        }

        :deep(.van-field) {
            padding: 0;
        }
    }
}

.isAuthor {
    background: black;
    color: white;
    font-weight: normal;
    margin-left: 3px;
    padding: 0 !important;
}

.comments {
    padding: 10px;

    .commentItem {
        padding-bottom: 10px;

        .author {
            display: flex;

            div.info {
                padding-left: 10px;

                p {
                    padding-top: 2px;


                }
            }

            div.like {
                flex: 1;
                display: flex;
                justify-content: flex-end;
            }
        }

        .content {
            margin-left: 45px;

            .message {
                margin-bottom: 5px;
            }

            .imageBox {
                display: flex;
                width: 220px;
                flex-wrap: wrap;

                .van-image {
                    margin: 0 3px 3px 0;
                }
            }

            .reply {
                background: rgb(240, 239, 239);
                padding: 5px;

                .replyBox {
                    // padding-bottom: 7px;

                    .speaker {
                        display: flex;
                        align-items: center;

                        span {
                            padding-left: 5px;
                        }
                    }

                    .replyMessage {
                        padding-left: 27.5px;

                        .huifu {
                            padding: 0;
                        }

                        span {
                            padding-left: 5px;
                        }
                    }
                }
            }
        }
    }
}
</style>
