<template>
  <div class="communityItem pt wrapper">
    <div class="bulk communityItemBulk ">
      <van-nav-bar :title="'社区-'+communityItem.communityName" left-arrow @click-left="onClickLeft" fixed />
      <CommunityList :params="communityParams" v-if="communityParams.isSubmit!== undefined" :key="key"></CommunityList>
    </div>
  </div>
</template>

<script>
// import { Dialog } from 'vant'
import CommunityList from '@/components/CommunityList.vue'
export default {
  name: 'communityView',
  inject: ['getId', 'reload'],
  components: {
    CommunityList
  },
  data() {
    return {
      communityItem: {},
      communityParams: {},
      scrollTop: 0,
      key:0,
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
    async dataGet() {
      const communityId = this.getId(this.$route.path);
      await this.$axios.get('comGet', { params: { communityId } }).then(rs => {//所有社区
        this.communityItem = rs;
        console.log(this.communityItem);
      })
      this.communityParams = { isSubmit: false,communityId }
    },
  },
  mounted() {
    this.dataGet();
  },
  activated() {
    if (this.$route.meta.isBack !== true) {//回到你原来的模样
      // console.log('刷新');
      Object.assign(this.$data, this.$options.data())
      this.dataGet();
      this.key = this.reload()
    }
    else {
      // console.log('回滚');
      document.querySelector(".communityItemBulk").scrollTop = this.scrollTop;
    }
  },
  beforeRouteEnter(to, from, next) {
    if (from.path.indexOf('/article') !== -1) { to.meta.isBack = true; }
    next()
  },
  beforeRouteLeave() {
    this.scrollTop = document.querySelector(".communityItemBulk").scrollTop
  }
}
</script>

