<template>
  <div class="newSubscribe wrapper pt">
    <van-nav-bar title="新的关注" left-arrow @click-left="onClickLeft" fixed />
    <div class="bulk">
      <van-empty description="暂无新关注" v-show="!list.length" />
      <div class="top" v-for="el in list" :key="el.userId" @click="goPersonal(el.userId)">
        <van-image class="avatar" width="1rem" height="1rem" fit="cover" round
          :src="avatarGet(el.avatar)" />
        <div class="nameDateReport">
          <div class="nameDate">
            <div class="nickname p4b">{{el.nickname}}</div>
            <div class="time p5 van-ellipsis">关注了您！</div>
          </div>
          <div class="report">
            <div class="time p5">{{ before(+el.date) }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NewSubscribe',
  inject: ['getId', 'before','avatarGet'],
  data() {
    return {
      list: []
    }
  },
  methods: {
    goPersonal(id) {
      this.$router.push('/personal/' + id);
    },
    onClickLeft() {
      this.$router.back()
    }
  },
  mounted() {
    const userId = this.getId(this.$route.path);
    const params = { userId, type: 'subscribe' }
    this.$axios.get('getMessageNotice', { params }).then(rs => {
      console.log(rs);
      this.list = rs
    })
  },

}
</script>

<style scoped lang="less">
.top {
  padding: 10px;
  display: flex;
  align-items: center;
  border-bottom: 1px rgb(220, 220, 220) solid;
  background: white;

  div.nameDateReport {
    padding-left: .3rem;
    display: flex;
    justify-content: space-between;
    flex: auto;

    div.nameDate {
      display: flex;
      flex-flow: column;
      width: 6rem;

      .time {
        width: 100%;
      }
    }

    div.report {
      display: flex;
      align-items: center;
    }
  }
}
</style>