<template>
    <div class="subscribe wrapper pt">
        <van-nav-bar left-arrow @click-left="onClickLeft" fixed title='粉丝与关注' />
        <van-tabs v-model:active="active" swipeable>
            <van-tab :title="this.$store.state.user&&userId==this.$store.state.user.userId?'我的关注':'Ta的关注'">
                <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">加载中...</van-loading>
                <div v-show="!loading">
                    <div v-if="inited">
                        <div v-for="el in follows" :key="el.userId">
                            <PersonalInfo :userInfo="el"></PersonalInfo>
                        </div>
                    </div>
                    <van-empty description="暂无关注" v-if='inited&&!follows.length' />
                </div>
            </van-tab>
            <van-tab :title="this.$store.state.user&&userId==this.$store.state.user.userId?'我的粉丝':'Ta的粉丝'">
                <van-loading size="1rem" vertical v-if="loading" style="padding-top:1rem">加载中...</van-loading>
                <div v-show="!loading">
                    <div v-if="inited">
                        <div v-for="el in fans" :key="el.userId">
                            <PersonalInfo :userInfo="el"></PersonalInfo>
                        </div>
                    </div>
                    <van-empty description="暂无粉丝" v-if='inited&&!fans.length' />
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>
<script>
import PersonalInfo from '@/components/PersonalInfo.vue'
export default {
    name: 'SubscribeView',
    components: {
        PersonalInfo,
    },
    data() {
        return {
            followList: [],
            fanList: [],
            active: 0,
            userId: 0,
            follows: [],
            fans: [],
            inited: false,
            mounted: false,
            loading: true,
        }
    },
    methods: {
        onClickLeft() {
            this.$router.back()
        },
        async dataGet() {
            let arr = this.$route.path.split('/');
            this.userId = arr[arr.length - 2];
            this.active = arr[arr.length - 1] - 0
            await this.subscribeGet()
            this.detailGet()
        },
        async subscribeGet() {
            const params = { userId: this.userId }
            await this.$axios.get('subscribeListGet', { params }).then(rs => {//获取用户关注列表
                this.followList = rs[0];
                this.fanList = rs[1];
            })
        },
        loaded() {
            if (this.loading === true)
                setTimeout(() => {
                    this.loading = false;
                }, 100)
        },
        push() {
            if (!history.state.title) {//有了就不要再push了!
                let state1 = {
                    title: "title",
                    url: 'http://www.maomao365.com',
                }
                history.pushState(state1, 'title')
            }
        },
        detailGet() {
            const params = { followList: this.followList, fanList: this.fanList };
            this.$axios.get('subscribeDetailGet', { params }).then(rs => {
                this.follows = rs[0];
                this.fans = rs[1];
                this.inited = true;
                this.loaded()
            })
        }
    },
    mounted() {
        this.push();
        this.mounted = true;
        this.dataGet()
    },
    activated() {
        if (!this.mounted) {
            if (this.$store.state.isBack) {
                console.log('回滚！');
            }
            else {
                console.log('刷新！');
                this.push()
                Object.assign(this.$data, this.$options.data())
                this.dataGet();
            }
        }//没有执行挂载才执行
        else
            this.mounted = false;
    },
}
</script>

