<template>
    <div class="Personal wrapper pbt">
        <van-nav-bar title="动态" left-arrow @click-left="onClickLeft" fixed />
        <div class="logined bulk" id="personalBulk">
            <div>
                <div class="content">
                    <div class="imgbox">
                        <van-image round width="2rem" height="2rem" :src="avatarGet(user.avatar)" fit='cover' />
                    </div>
                    <div class="text">
                        <p class="p3">{{ user.nickname }}
                            <van-icon class-prefix="iconfont icon-nan" size="0.5rem" v-show="user.sex === '男'"
                                color="blue" />
                            <van-icon class-prefix="iconfont icon-nv" size="0.5rem" v-show="user.sex === '女'"
                                color="pink" />
                        </p>
                        <p class="p6g">{{ user.signature }}</p>
                    </div>
                </div>
            </div>
            <div class="">
                <van-grid clickable :column-num="3" :border="false">
                    <van-grid-item :to="'/subscribe/' + userId + '/' + '0'">
                        <p class="p2">{{ follow.length }}</p>
                        <p class="p5g">关注</p>
                    </van-grid-item>
                    <van-grid-item :to="'/subscribe/' + userId + '/' + '1'">
                        <p class="p2">{{ fan.length }}</p>
                        <p class="p5g">粉丝</p>
                    </van-grid-item>
                    <van-grid-item>
                        <p class="p2">{{ user.likeCount }}</p>
                        <p class="p5g">获赞</p>
                    </van-grid-item>
                </van-grid>
            </div>
            <ListCell :submitIdArr="submit" :postIdArr="post" :commentArr="comment" :userId="userId - 0" v-if="inited"
                :key="key" @refresh="refresh">
            </ListCell>
            
            <div class="bottomBtn" >
                <van-row gutter="40" justify="center">
                    <van-col span="12">
                        <van-button type="default" block :to="/directMessage/ + userId" :disabled="!this.$store.state.user || this.$store.state.user.userId == userId">私信Ta</van-button>
                    </van-col>
                    <van-col span="12">
                        <van-button block :color="subscribeState ? 'grey' : '#000'"
                            :icon="subscribeState ? 'success' : 'plus'" @click="subscribe(user.userId)" :disabled="!this.$store.state.user || this.$store.state.user.userId == userId">
                            {{ subscribeState ? '已关注' : '关注Ta' }}</van-button>
                    </van-col>
                </van-row>
            </div>
        </div>
    </div>
</template>
<script>
import ListCell from '@/components/ListCell.vue'
import { Dialog, } from 'vant'
export default {
    name: 'PersonalView',
    inject: ['reload','avatarGet'],
    components: {
        ListCell
    },
    data() {
        return {
            active: 0,
            user: 0,
            fan: [],
            follow: [],
            submit: [],
            post: [],
            comment: [],
            userId: 0,
            inited: false,//初始化状态
            subscribeState: false,//对作者关注状态
            subscribeList: [],
            fanList: [],
            mounted: false,
            key: 1,
            scrollTop: 0,
        }
    },
    mounted() {
        this.push()
        this.mounted = true;
        this.dataGet();//初始数据
    },
    methods: {
        async refresh(data,callback) {
            await this.dataShow()
            callback(data)
        },
        push() {
            if (!history.state.title) {//有了就不要再push了!
                let state1 = {
                    title: "title",
                    url: 'http://www.maomao365.com',
                }
                history.pushState(state1, 'title')
            }
        },
        async dataShow() {//数据加载
            let arr = this.$route.path.split('/');
            this.userId = arr[arr.length - 1];
            const params = { userId: this.userId }
            await this.$axios.get('userDataShow', { params }).then(rs => {
                this.user = rs;
                this.fan = rs.fans.fan;
                this.follow = rs.follows.follow;
                this.submit = rs.submits.submit.reverse();
                this.post = rs.posts.post.reverse();
                this.comment = rs.comments.comment.reverse();
                this.inited = true;
            })
        },
        async targetFansGet(userId) {//目标的粉丝获取
            const params = { userId }
            await this.$axios.get('subscribeListGet', { params }).then(rs => {
                this.fanList = rs[1];
            })
        },
        async subscribeGet() {//订阅状态获取
            const params = { userId: this.$store.state.user.userId }
            await this.$axios.get('subscribeListGet', { params }).then(rs => {//获取用户关注列表
                this.subscribeList = rs[0];
            })
            await this.targetFansGet(this.userId)
            if (this.fanList.indexOf(this.$store.state.user.userId + '') > -1)
                this.subscribeState = true
        },
        async dataGet() {//数据获取
            await this.dataShow();

            if (this.$store.state.user)
                this.subscribeGet();
        },
        subscribeUpdate() {//订阅更新
            const params = { fan: this.fanList, follow: this.subscribeList, userId: this.$store.state.user.userId, authorId: this.userId }
            this.$axios.get('subscribeUpdate', { params })
        },//关注与粉丝更新
        subscribe(id) {
            if (this.$store.state.user) {
                if (this.subscribeState) {//取消关注
                    Dialog.confirm({
                        title: '取消关注',
                    })
                        .then(() => {
                            this.subscribeList = this.subscribeList.filter(el => {
                                return el - 0 !== id
                            })
                            this.subscribeState = false;
                            this.fanList = this.fanList.filter(el => {
                                return el - 0 != this.$store.state.user.userId;
                            })
                            this.subscribeUpdate()
                        })
                        .catch(() => {
                            return
                        });
                }
                else {//关注
                    this.subscribeList.push(id);
                    this.fanList.push(this.$store.state.user.userId)
                    this.subscribeState = true;
                    this.subscribeUpdate()
                }
            }
            else
                this.loginState()
        },
        loginState() {
            Dialog.confirm({
                title: '未登录',
                message:
                    '你必须要登录才能使用此功能，是否前去登录?',
            })
                .then(() => {
                    this.$router.replace('/login');
                })
                .catch(() => {
                    return
                });
        }, // 登录状态检测
        onClickLeft() {
            this.$router.back()
        },
    },
    activated() {
        if (!this.mounted) {
            if (this.$store.state.isBack) {
                console.log('回滚！');
                // this.key = this.reload()
                this.$nextTick(() => {
                    console.log(this.scrollTop);
                    document.querySelector("#personalBulk").scrollTop = this.scrollTop;
                })
            }
            else {
                console.log('刷新！');
                this.push()
                Object.assign(this.$data, this.$options.data())
                this.dataGet();
                this.key = this.reload()
            }
        }
        else this.mounted = false
    },
    deactivated() {
        this.scrollTop = document.querySelector("#personalBulk").scrollTop
    },
}
</script>

<style scoped lang="less">
.content {
    padding: 10px;
    display: flex;
    align-items: center;

    >div {
        height: 100%;

        &.text {
            padding-left: 15px;
        }
    }
}

.bulk {
    height: 100%;
    overflow-y: auto;
}

div.bottomBtn {
    position: fixed;
    height: 50px;
    width: 10rem;
    bottom: 0;
    background: rgb(232, 232, 232);
    padding: 5px 0;
}
</style>