<template>
  <div class="questionList wrapper pt">
    <van-nav-bar title="问题列表" left-arrow @click-left="onClickLeft" fixed />
    <van-cell  is-link v-for="el in list" :key="el.id" center :to="'/questionDetail/'+el.id" :label="dateShow(el.createdAt)" >
      <template #title>
        <van-icon :name="type==='FAQ'?'fire':'underway'" :color="type==='FAQ'?'red':'blue'" style="padding-right:10px"/>
        <span>{{el.question}}</span>
      </template>
      <template #value v-if="el.answer!=='暂无回答'&&type==='NAQ'">
        <van-icon name="checked" color="green"  />
        <span>已回答</span>
      </template>
    </van-cell>
  </div>
</template>
<script>
import dayjs from '@/assets/util/public'
export default {
  name: 'ContactView',
  data() {
    return {
      list: [],
      type: '',
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
    dateShow(date) {//时间显示
      return dayjs(date).format('YYYY[年]MM[月]DD[日] HH:mm')
    },

  },
  mounted() {
    const arr = this.$route.path.split('/');
    this.type = arr[arr.length - 1];
    const params = { type: this.type }
    this.$axios.get('questionListGet', { params }).then(rs => {
      this.list = rs;
    })
  },

}
</script>

<style scoped lang="less">
.btnBox {
  display: flex;
  justify-content: center;
}
</style>