<template>
    <div class="imageCut wrapper bt">
        <van-nav-bar title="裁剪" @click-right="confirm" fixed @click-left="cancel">
            <template #left>
                <van-icon name="cross" size="1rem" color="red" />
            </template>
            <template #right>
                <van-icon name="success" size="1rem" />
            </template>
        </van-nav-bar>
        <div class="container">
            <div class="img-container">
                <img src="" ref="image" alt="">
            </div>
        </div>
    </div>
</template>

<script>
import Cropper from 'cropperjs'
import 'cropperjs/dist/cropper.css'
export default {
    name: 'ImageCut',
    props: {
        src: {
            type: String,
        },
        type: {
            type: String,
        }
    },
    data() {
        return {
            myCropper: null,
            afterImg: '',
            imgType: '',
        }
    },
    mounted() {
        this.init();
        this.replace(this.src)
        this.imgType = this.type;
        this.$nextTick(() => {
            this.$emit('loaded')
        })
    },
    methods: {
        cancel() {
            this.$emit('cutCancel')
        },
        confirm() {
            this.sureSava()
            this.$emit('cutConfirm', this.afterImg)
            return true;
        },
        replace(url) {
            this.myCropper.replace(url)
        },
        init() {
            this.myCropper = new Cropper(this.$refs.image, {
                viewMode: 2,
                dragMode: 'move',
                aspectRatio: 16 / 9,
                background: true,
                autoCropArea: 0.8,
                zoomOnWheel: true,
                // autoCrop:false
            })
        },
        sureSava() {
            this.afterImg = this.myCropper.getCroppedCanvas({
                imageSmoothingQuality: 'high'
            }).toDataURL(this.imgType)
        },
    },
}
</script>

<style scoped lang="less">
.imageCut {
    div.container {
        height: 100vh;

        .img-container {
            height: 100vh;
            overflow: hidden;
        }
    }

}
</style>