<template>
    <div class="post submit pt wrapper">
        <van-nav-bar title="发帖" :right-text="this.$route.query.articleId?'确认编辑':'发送'" left-arrow @click-right="uploadSend" fixed @click-left="onClickLeft" />
        <div class="bulk">
            <van-cell-group>
                <van-cell title="选择社区" is-link @click="showPop">
                    <template #value>
                        <span v-if="!selectedCom.id">选择想要发布到的社区</span>
                        <div v-else>
                            <div class="outBox">
                                <div class="iconBox">
                                    <div class="foreground" :style="{ 'background': selectedCom.foreground }">
                                        <van-icon :class-prefix="selectedCom.icon + ' iconfont'"
                                            :color="selectedCom.color" size="0.6rem" />
                                    </div>
                                </div>
                                <p class="textBox">{{ selectedCom.communityName }}</p>
                            </div>
                        </div>
                    </template>
                </van-cell>
                <van-popup v-model:show="show" position="right" :style="{ width: '100%', height: '100%' }" class="pop">
                    <van-nav-bar title="选择社区" @click-left="hidPop">
                        <template #left>
                            <van-icon name="cross" size="18" />
                        </template>
                    </van-nav-bar>
                    <van-cell title="我的社区" />
                    <van-grid direction="horizontal" :column-num="2" clickable gutter="0.2rem">
                        <van-grid-item v-for='el in myCommunitys' :key="el" @click="comSelect(el)">
                            <div class="outBox">
                                <div class="iconBox">
                                    <div class="foreground" :style="{ 'background': el.foreground }">
                                        <van-icon :class-prefix="el.icon + ' iconfont'" :color="el.color"
                                            size="0.6rem" />
                                    </div>
                                </div>
                                <p class="textBox">{{ el.communityName }}</p>
                            </div>
                        </van-grid-item>
                    </van-grid>
                    <div v-for="el in otherCoummunity" :key="el">
                        <van-cell :title="el.communityClass" />
                        <van-grid direction="horizontal" :column-num="2" clickable gutter="0.2rem">
                            <van-grid-item v-for='item in el.content' :key="item" @click="comSelect(item)">
                                <div class="outBox">
                                    <div class="iconBox">
                                        <div class="foreground" :style="{ 'background': item.foreground }">
                                            <van-icon :class-prefix="item.icon + ' iconfont'" :color="item.color"
                                                size="0.6rem" />
                                        </div>
                                    </div>
                                    <p class="textBox">{{ item.communityName }}</p>
                                </div>
                            </van-grid-item>
                        </van-grid>
                    </div>
                </van-popup>
                <van-cell>
                    <template #value>
                        <van-field v-model="title" placeholder="标题" />
                    </template>
                </van-cell>
            </van-cell-group>
            <WangEditor ref="editor" submitOrPost="post"></WangEditor>
        </div>
    </div>
</template>
<script>
import { Toast ,Dialog} from 'vant';
import WangEditor from '@/components/WangEditor.vue';
import qs from 'qs';
export default {
    name: 'PostView',
    components: {
        WangEditor
    },
    data() {
        return {
            show: false,//弹出层的显示
            title: '',
            myCommunitys: [],
            allCommunitys: [],
            selectedComId: -1,//选择的社区Id
            selectedCom: {},//选择的社区对象
            communityClass: this.$store.state.communityClass,
            otherCoummunity: [],
            saved: false,
            draftCreatedAt:0,
        }
    },
    methods: {
        comSelect(obj) {
            this.selectedComId = obj.communityId
            this.selectedCom = obj
            this.hidPop();
        },
        hidPop() {
            this.show = false;
        },
        showPop() {
            this.show = true;
        },
        onClickLeft() {
            this.$router.back();
        },
        //获得所有社区和全部社区
        async communityGet() {
            const params = { userId: this.$store.state.user.userId }
            await this.$axios.get('comListShow', { params }).then(rs => {
                this.myCommunitys = rs;
            });
            await this.$axios.get('comGet', { params }).then(rs => {
                this.allCommunitys = rs;
            });
            this.otherCoummunity = this.communityClass.map(el => {//选择社区页面的数据
                let obj = {}
                obj.communityClass = el;
                obj.content = this.allCommunitys.filter(item => {
                    return item.communityClass.indexOf(el) > -1;
                })
                return obj
            })
        },
        async uploadSend() {
            if (this.selectedComId === -1 || this.title === '' || this.$refs.editor.html === '' || this.$refs.editor.html === `<p><br></p>`)
                Toast.fail('存在未填写字段！');
            else {
                Toast.loading({
                    message: '提交中...',
                    forbidClick: true,
                    duration: 0,
                    overlay: true,
                });
                const authorId = this.$store.state.user.userId;
                const channelId = this.selectedComId
                const title = this.title
                const content = this.$refs.editor.html
                let params = { authorId, channelId, title, content, isSubmit: false }
                let message = '发帖成功！';
                if (this.$route.query.articleId) {
                    message = '编辑成功！';
                    params = { ...params, articleId: this.$route.query.articleId }
                    params = qs.stringify(params);
                    await this.$axios.post('articleEdit', params)
                }
                else {
                    params = qs.stringify(params);
                    await this.$axios.post('articleSubmit', params)
                }
                this.saved = true;
                Toast.clear();
                Toast({
                    message,
                    forbidClick: true,
                    onClose: () => {
                        this.$router.back()
                    }
                })
            }
        }
    },
    mounted() {
        this.communityGet();
        if (this.$route.query.articleId) {
            Toast.loading({
                message: '数据获取中',
                overlay: true,
                duration: 0
            })
            const params = { articleId: this.$route.query.articleId, URL: this.$store.state.URL }
            this.$axios.get('articleItemGet', { params }).then(rs => {
                this.title = rs[0].title;
                this.selectedCom = rs[2];
                this.selectedComId = rs[2].channelId;
                this.$refs.editor.html = rs[0].content;
                Toast.clear()
            })
        }
        if (this.$route.query.createdAt) {//草稿箱
            Toast.loading({
                message: '数据获取中',
                overlay: true,
                duration: 0
            })
            const params = { createdAt: this.$route.query.createdAt, URL: this.$store.state.URL, userId: this.$store.state.user.userId }
            this.$axios.get('draftListGet', { params }).then(rs => {
                this.draftCreatedAt = rs.createdAt;
                this.title = rs.title;
                if (rs.channelId) {
                    this.selectedCom = rs.channelId;
                    this.selectedComId=rs.channelId.communityId
                }
                this.$refs.editor.html = rs.content;
                Toast.clear()
            })
        }
    },
    beforeRouteLeave(to, from, next) {
        if (!this.$route.query.articleId && this.$refs.editor.html !== '' && this.$refs.editor.html !== `<p><br></p>` && !this.saved) {
            setTimeout(() => {
                Dialog.confirm({
                    title: '是否保存到草稿箱',
                })
                    .then(async () => {
                        Toast.loading({
                            message: '请求中',
                            overlay: true,
                            duration: 0,
                        })
                        const authorId = this.$store.state.user.userId;
                        const channelId = this.selectedCom
                        const title = this.title
                        const content = this.$refs.editor.html
                        let params = { authorId, channelId, title, content,createdAt:this.draftCreatedAt }
                        params = qs.stringify(params);
                        await this.$axios.post('draftSave', params).then(res => {
                            console.log(res);
                            Toast.clear();
                            Toast.success({
                                message: '保存成功!',
                                onClose: next
                            })
                        })

                    })
                    .catch(() => {
                        // on cancel
                        next(true)
                    });

            }, 1)
        }
        else
            next(true)
    }
}
</script>

<style scoped lang="less">
:deep(.pop .van-grid-item__content) {
    padding: 5px;
}

.submit {
    padding-bottom: 40px;

    :deep(.van-cell__title, van-cell__value) {
        display: flex;
        align-items: center;
    }

    .van-cell {
        padding: 5px;
    }

    .outBox {
        width: 100%;
        display: flex;

        .iconBox {
            flex: 1;
            display: flex;
            justify-content: end;
            align-items: center;

            .foreground {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 0.7rem;
                height: 0.7rem;
            }
        }

        .textBox {
            padding-left: 10px;
            flex: 1;
            font-size: 14px;
            display: flex;
            justify-content: start;
            align-items: center;
        }
    }
}
</style>