<template>
  <div class="messageNotice wrapper pt">
    <van-tabs v-model:active="active" shrink class="out">
      <van-tab title="消息">
        <div v-if="!this.$store.state.user">
          <van-empty image="error" description="未登录，无法查看消息" />
          <div class="notLogin">
            <van-button color="#000" to="/login">去登录</van-button>
          </div>
        </div>
        <div v-else>
          <van-pull-refresh v-model="refreshing" :head-height="80" style="min-height: calc(100vh - 96px);"
            @refresh="onRefresh">
            <van-grid :column-num="3" clickable>
              <van-grid-item text="回复我的" :to="'/replyToMe/' + this.$store.state.user.userId">
                <template #icon>
                  <van-icon name="comment" :dot='notice.newComment' />
                </template>
              </van-grid-item>
              <van-grid-item icon="friends" text="关注" :to="'/newSubscribe/' + this.$store.state.user.userId">
                <template #icon>
                  <van-icon name="friends" :dot='notice.newSubscribe' />
                </template>
              </van-grid-item>
              <van-grid-item icon="smile-comment" text="我的问题" :to="'/answerList/' + this.$store.state.user.userId">
                <template #icon>
                  <van-icon name="smile-comment" :dot='notice.newAnswer' />
                </template>
              </van-grid-item>
            </van-grid>
            <div class="DM">
              <div class="top" v-for="el in chatList" :key="el.oppositeId" @click="goDM(el.oppositeId)">
                <van-image class="avatar" width="1rem" height="1rem" fit="cover" round
                  :src="avatarGet(el.avatar)" />
                <div class="nameDateReport">
                  <div class="nameDate">
                    <div class="nickname p4b">{{ el.nickname }}</div>
                    <div class="time p5 van-ellipsis">{{ el.message }}</div>
                  </div>
                  <div class="report">
                    <div class="time p5">{{ untilMin(+el.date) }}</div>
                    <div class="badge p5" v-show="el.unread">
                      {{ el.unread }}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </van-pull-refresh>
        </div>
      </van-tab>
    </van-tabs>
    <TabBar></TabBar>
  </div>
</template>
<script>
import { Toast } from 'vant'
import TabBar from '@/views/layout/TabBar.vue'
export default {
  name: 'MessageNotice',
  inject: ['before', 'untilMin','avatarGet'],
  components: {
     TabBar
  },
  data() {
    return {
      active: 0,
      notice: {},
      refreshing: false,
      chatList: [],
    }
  },
  methods: {
    onRefresh() {
      setTimeout(() => {
        this.refreshing = false;
        Toast('刷新成功');
      }, 500);
    },
    goDM(id) {
      this.$router.push('/directMessage/' + id)
    },
    dataGet() {
      if (this.$store.state.user) {
        this.$axios.get('getMessageNotice', { params: { userId: this.$store.state.user.userId, toNew: true, } }).then(rs => {
          this.notice = rs;
        })
        this.$axios.get('getDMList', { params: { userId: this.$store.state.user.userId } }).then(rs => {
          this.chatList = rs;
        })
      }
    },
  },
  mounted() {
    this.dataGet();
  },
  activated() {
    this.dataGet();
  },
}
</script>

<style scoped lang="less">
:deep(.out>.van-tabs__wrap) {
  position: fixed;
  z-index: 100;
  top: 0;
}

.DM {
  margin-top: 10px;

  .top {
    padding: 10px;
    display: flex;
    align-items: center;
    border-bottom: 1px rgb(220, 220, 220) solid;
    background: white;

    div.nameDateReport {
      padding-left: .3rem;
      display: flex;
      justify-content: space-between;
      flex: auto;

      div.nameDate {
        display: flex;
        flex-flow: column;
        width: 6rem;

        .time {
          width: 100%;
        }
      }

      div.report {
        display: flex;
        flex-direction: column;
        align-items: center;

        .badge {
          width: .4rem;
          height: .4rem;
          background: red;
          color: white;
          border-radius: 50%;
          text-align: center;
          line-height: .4rem;
        }
      }
    }
  }


}

.notLogin {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>