<template>
    <div class="commentList ">
        <div class="commentItem" v-for="(el, index) in ArticleList" :key="el.articleId">
            <router-link :to="'/articleHome/' + el.articleId">
                <div class="author">
                    <div style="display:flex;">
                        <van-image round width="0.8rem" height="0.8rem" fit="cover"
                            :src="avatarGet(el.speakerInfo.avatar)" lazy-load />
                        <div class="info">
                            <span class="p5">{{ el.speakerInfo.nickname }}</span>
                            <span class="p7g" v-if="notice && el.listenerName">{{ before(el.date -
                                    0)
                            }}&nbsp;回复了你的评论</span>
                            <span class="p7g" v-else-if="notice && !el.listenerName">{{ before(el.date -
                                    0)
                            }}&nbsp;评论了你的文章</span>
                            <span class="p7g" v-else>{{ before(el.date - 0) }}&nbsp;发布了评论</span>
                        </div>
                    </div>
                    <div style="display:flex;align-items:center"
                        v-show="this.$store.state.user && listCell && el.speakerInfo.userId === this.$store.state.user.userId">
                        <van-popover v-model:show="showPopover[index]" :actions="actions" @select="onSelect"
                            placement="bottom-end" theme="dark" trigger="manual">
                            <template #reference>
                                <van-button size="small" @click.prevent="delComment(index, el)" color="transparent">
                                    <template #icon>
                                        <van-icon name="ellipsis" color="#aaa" size="0.5rem"></van-icon>
                                    </template>
                                </van-button>
                            </template>
                        </van-popover>
                    </div>
                </div>
                <div class="p6 van-multi-ellipsis--l2">
                    {{ el.replyMessage }}
                </div>
                <div class="bottom" :style="{ 'padding': el.listenerName ? '5px' : '0' }">
                    <p v-if="el.listenerName" class="p5 repliedMessage van-ellipsis">{{ el.listenerName }}:<span
                            class="p5g">{{
                                    el.repliedMessage
                            }}</span></p>
                    <div class="article"
                        :style="{ 'background': el.listenerName ? 'rgb(253, 253, 253)' : 'rgb(243, 243, 243)' }">
                        <div style="display:flex;align-item:center;">
                            <van-image width="1rem" height="1rem" fit="cover"
                                :src="require('@/assets/images/dinoeye.png')" lazy-load
                                v-if="el.articleInfo.cover === ' '" style='background:rgb(234, 234, 234)' />
                            <van-image width="1rem" height="1rem" fit="cover"
                                :src="this.$store.state.URL + 'upload/cover/' + el.articleInfo.cover" lazy-load
                                v-else />
                        </div>
                        <div class="articleInfo van-ellipsis">
                            <span class="p6g">{{ el.articleInfo.authorName }}</span>
                            <span class="p6 van-ellipsis">{{ el.articleInfo.title }}</span>
                        </div>
                    </div>
                </div>
            </router-link>
        </div>
    </div>
</template>
<script>

import { Toast } from 'vant';
export default {
    name: 'commentList',
    inject: ['before', 'avatarGet'],
    props: {
        ArticleList: {
            type: Array,
        },
        notice: {
            type: Boolean,
            default: false,
        },
        listCell: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            showPopover: [],
            actions: [
                { text: '删除', icon: 'delete-o' },
                { text: '取消', icon: 'close' },
            ],
            target: null,
        }
    },
    methods: {
        delComment(index, el) {
            this.target = el
            this.showPopover[index] = true;
        },
        onSelect(select) {
            if (select.text === '删除')
                this.del(this.target)
        },
        del(el) {
            Toast.loading({
                message: '删除中...',
                duration: 0,
                overlay: true,
            })
            let params = { articleId: el.articleId, commentId: el.commentId, userId: this.$store.state.user.userId }
            const arr = this.ArticleList
            arr.splice(this.ArticleList.indexOf(el), 1)
            if (el.repliedMessage) {
                params = { ...params, date: el.date }
            }
            this.$axios.get('delComment', { params }).then(rs => {
                console.log(rs, '评论或回复删除成功！');
                Toast.clear();
                Toast.success('删除成功!');
            })
        },

    },
    mounted() {
    },
}
</script>

<style scoped lang="less">
div.commentItem {
    padding: 10px;
    background: white;
    margin-top: 10px;

    >a>div {
        padding: 5px 0;
    }

    div.author {
        display: flex;
        align-items: center;
        justify-content: space-between;

        div.info {
            display: flex;
            flex-direction: column;
            padding-left: 5px;
        }
    }

    div.bottom {
        display: flex;
        flex-direction: column;
        background: rgb(243, 243, 243);
        padding: 5px;

        .repliedMessage {
            padding: 5px 0;
        }

        div.article {
            background: rgb(29, 24, 24);
            display: flex;
            align-items: center;

            div.articleInfo {
                display: flex;
                flex-direction: column;
                padding-left: 5px;
            }
        }
    }
}
</style>