<template>
  <div class="directMessage wrapper pbt">
    <van-nav-bar :title="opposite ? opposite.nickname : undefined" left-arrow @click-left="onClickLeft" fixed />
    <div :class="flexEnd ? 'background bulk flexEnd' : 'background bulk '" ref="background" id="dmBulk">
      <div class="main" ref="main">
        <div :class="'messageItem ' + getClass(el.userId)" v-for="el in messageList" :key="el">
          <div  class="avatar">
            <van-image  width=".8rem" height=".8rem" fit="cover" round :src="avatarGet(getSrc(el.userId))"
              @click.stop="goPersonal(el.userId)" />
          </div>
          <div class="content">
            <div class="message p5-5">{{ el.message }}</div>
            <div class="date p6g">{{ untilMin(+el.date) }}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="send">
      <van-field placeholder="发送消息" @focus="focus"  v-model="message"/>
    </div>
    <van-popup v-model:show="show" :position="(this.$store.state.setup&&this.$store.state.setup.system) ? 'top' : 'bottom'" class="pop" >
      <van-field v-model="message" rows="2" autosize type="textarea" placeholder="发送消息" ref="input" />
      <div class="option">
        <van-button type="primary" size="small" color="#000" @click="sendMessage">发送</van-button>
        <van-icon name="photo-o" color="grey" size="0.5rem" style="padding:10px" @mousedown.prevent="developing"/>
        <van-icon name="smile-o" color="grey" size="0.5rem" style="padding:10px" @mousedown.prevent="developing"/>
        <van-icon name="arrow-down" color="grey" size="0.5rem" style="padding:10px" @click="loseFocus"/>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { useRect } from '@vant/use';
import {Toast} from 'vant'
import qs from 'qs';
export default {
  name: 'DirectMessage',
  components: {
  },
  inject: ['getId', 'before', 'untilMin', 'avatarGet','developing'],
  data() {
    return {
      message: '',
      show: false,
      messageList: [],
      oppositeId: -1,
      user: null,
      opposite: null,
      flexEnd: false,
      mounted: false,
      scrollTop: 0,
    }
  },
  methods: {
    goPersonal(id) {
      this.$router.push('/personal/' + id)
    },
    onClickLeft() {
      this.$router.back()
    },
    getClass(id) {
      if (id == this.$store.state.user.userId)
        return 'right'
    },
    getSrc(id) {
      if (id == this.$store.state.user.userId)
        return this.user.avatar
      else
        return this.opposite.avatar
    },
    sendMessage() {
      if (this.message.trim() === '') {
        Toast.fail('内容无意义！')
        return
      }
      const params = { userId: this.$store.state.user.userId, date: Date.now(), message: this.message, oppositeId: this.oppositeId }
      this.$axios.post('sendMessage', qs.stringify(params)).then(rs => {
        this.messageList = [...this.messageList, params]
        console.log(rs);
        this.message = '';
        this.show = false;
        this.heightGet();
        Toast.clear();
        Toast.success('发送成功！');
      })
    },
    focus() {
      this.show = true;
      this.$nextTick(() => {
        this.$refs.input.focus();
      })
    },
    loseFocus() {
      this.$refs.input.blur();
    },
    async dataGet() {
      this.oppositeId = this.getId(this.$route.path)
      const params = { userId: this.$store.state.user.userId, oppositeId: this.oppositeId }
      await this.$axios.get('getDMDetail', { params }).then(rs => {
        this.user = rs[1]
        this.opposite = rs[0]
        this.messageList = rs[2]
      })
      this.heightGet();
      Toast.clear();
    },
    heightGet() {
      this.$nextTick(() => {
        const background = useRect(this.$refs.background)
        const main = useRect(this.$refs.main);
        if (main.height <= background.height) {
          this.flexEnd = true;
        }
        else
          this.flexEnd = false;
      })
    },
    push() {
      if (!history.state.title) {//有了就不要再push了!
        let state1 = {
          title: "title",
          url: 'http://www.maomao365.com',
        }
        history.pushState(state1, 'title')
      }
    },
  },
  mounted() {
    Toast.loading({
      message: '数据获取中',
      overlay: true,
      duration:0,
    })
    this.push()
    this.mounted = true;
    this.dataGet()
  },
  activated() {
    if (!this.mounted) {
      if (this.$store.state.isBack) {
        this.$nextTick(() => {
          document.querySelector("#dmBulk").scrollTop = this.scrollTop;
        })
      }
      else {
        this.push()
        Object.assign(this.$data, this.$options.data())
        this.dataGet();
      }
    }
    else this.mounted = false
  },
  deactivated() {
    this.scrollTop = document.querySelector("#dmBulk").scrollTop
    console.log(this.scrollTop);
  },
}
</script>

<style scoped lang="less">
div.background {
  background: rgb(242, 242, 242);
  display: flex;
  flex-flow: column;
  transform: scaleY(-1);

  &.flexEnd {
    justify-content: flex-end;
  }

  .main {
    padding: 10px 0;
    box-sizing: border-box;
    transform: scaleY(-1);

    .messageItem {
      margin-top: 10px;
      display: flex;

      .avatar {
        padding: 0 10px
      }

      .content {
        display: flex;
        flex-flow: column;

        .message {
          background: white;
          padding: 8px;
          max-width: 6rem;
          border-radius: 0 10px 10px 10px;
          min-height: 0.8rem;
          box-sizing: border-box;
        }

        .date {
          padding-top: 2px;
        }
      }
    }

    .right {
      flex-direction: row-reverse;

      .message {
        background: rgb(206, 206, 206) !important;
        border-radius: 10px 0 10px 10px !important;
      }

      .date {
        text-align: right;
      }
    }
  }
}

.send {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  bottom: 0;
  width: 100%;
  box-shadow: 0 0 2px 1px grey;

  :deep(input) {
    background: #f8f8f8;
  }
}

:deep(.pop textarea) {
  background: #f8f8f8;
}

.pop {
  .option {
    padding: 10px;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
  }
}
</style>