import { createRouter, createWebHashHistory } from 'vue-router'

//管理员路由
import Admin from '../views/admin/Admin.vue'
import ComAdd from '../views/admin/comManage/ComAdd.vue'
import ChaAdd from '../views/admin/chaManage/ChaAdd.vue'
import ArticlesList from '../views/admin/auditing/ArticlesList.vue'
import Article from '../views/admin/auditing/Article.vue'
import AnnounceList from '../views/admin/announce/AnnounceList.vue'

//公用路由
import Home from '../views/home/Home.vue'
import Community from '../views/community/Community.vue'
import CommunityItem from '../views/community/CommunityItem/CommunityItem.vue'
// import Video from '../views/video/Video.vue'
import MessageNotice from '../views/messageNotice/messageNotice.vue'
import AnswerList from '../views/messageNotice/answerList.vue'
import ReplyList from '../views/messageNotice/replyList.vue'
import NewSubscribe from '../views/messageNotice/newSubscribe.vue'
import DirectMessage from '../views/messageNotice/directMessage.vue'
import My from '../views/my/My.vue'
import Submit from '../views/submit/Submit.vue'
import Post from '../views/submit/Post.vue'
// import TabBAr from '../views/layout/TabBar.vue'
import Login from '../views/my/login/Login.vue'
import Reg from '../views/my/login/Register.vue'
import ProfileEdit from '../views/my/serves/ProfileEdit.vue'
import Draft from '../views/my/serves/Draft.vue'
import article from '../views/home/article/ArticleHome.vue'
import Personal from '../views/my/Personal.vue'
import MyCollect from '../views/my/serves/MyCollect.vue'
import CollectDetail from '../views/my/serves/CollectDetail.vue'
import History from '../views/my/serves/History.vue'
import ChannelSelect from '../views/other/ChannelSelect.vue'//频道定制
import Subscribe from '../views/my/serves/Subscribe.vue'//粉丝与关注
import Search from '../views/other/Search.vue'//搜索
import Contact from '../views/my/serves/Contact.vue'
import Feedback from '../views/my/serves/Feedback.vue'
import BugDetail from '../views/my/serves/BugDetail.vue'
import QuestionList from '../views/my/serves/Question/QuestionList.vue'
import QuestionDetail from '../views/my/serves/Question/QuestionDetail.vue'
import Setup from '../views/my/serves/setup/SetUp.vue'

const routes = [

  { path: '/admin', name: 'admin', component: Admin, meta: { index: 2, } },
  { path: '/comAdd', name: 'comAdd', component: ComAdd, meta: { index: 3 } },
  { path: '/chaAdd', name: 'chaAdd', component: ChaAdd, meta: { index: 3 } },
  { path: '/articlesList', name: 'articlesList', component: ArticlesList, meta: { index: 3 } },
  { path: '/article/:id', name: 'article', component: Article, meta: { index: 4 } },
  { path: '/announceList', name: 'announceList', component: AnnounceList, meta: { index: 3 } },

  { path: '/login', name: 'login', component: Login, meta: { index: 2 } },
  { path: '/channelSelect', name: 'channelSelect', component: ChannelSelect, meta: { index: 2 } },
  { path: '/register', name: 'register', component: Reg, meta: { index: 3 } },
  { path: '/profileEdit', name: 'profileEdit', component: ProfileEdit, meta: { index: 2 } },
  { path: '/draftList', name: 'draftList', component: Draft, meta: { index: 2 } },
  { path: '/myCollect', name: 'myCollect', component: MyCollect, meta: { index: 2 } },
  { path: '/history', name: 'history', component: History, meta: { index: 2, keepAlive: true, spe: true } },//缓存路由 特殊情况，只返回一次
  { path: '/collectDetail', name: 'collectDetail', component: CollectDetail, meta: { index: 3, keepAlive: true, spe: true } },//缓存路由 特殊情况，只返回一次
  { path: '/submit', name: 'submit', component: Submit, meta: { index: 101, } },//缓存路由 特殊情况，只返回一次
  { path: '/post', name: 'post', component: Post, meta: { index: 101, } },
  { path: '/search', name: '/search', component: Search, meta: { index: 2, } },
  { path: '/articleHome/:id', name: 'articleHome', component: article, meta: { index: 100, keepAlive: true } },//缓存路由
  { path: '/personal/:id', name: 'personal', component: Personal, meta: { index: 100, keepAlive: true } },//缓存路由
  { path: '/subscribe/:id/:active', name: 'subscribe', component: Subscribe, meta: { index: 100, keepAlive: true } },//缓存路由
  { path: '/contact', name: 'contact', component: Contact, meta: { index: 2, } },
  { path: '/feedback', name: 'feedback', component: Feedback, meta: { index: 3, } },
  { path: '/bugDetail/:id', name: 'bugDetail', component: BugDetail, meta: { index: 4, } },
  { path: '/questionList/:type', name: 'questionList', component: QuestionList, meta: { index: 4, } },
  { path: '/questionDetail/:id', name: 'questionDetail', component: QuestionDetail, meta: { index: 5, } },
  { path: '/answerList/:id', name: 'answerList', component: AnswerList, meta: { index: 2, } },
  { path: '/replyToMe/:id', name: 'replyList', component: ReplyList, meta: { index: 2, } },
  { path: '/newSubscribe/:id', name: 'newSubscribe', component: NewSubscribe, meta: { index: 2, } },
  { path: '/directMessage/:id', name: 'directMessage', component: DirectMessage, meta: { index: 100, keepAlive: true } },
  { path: '/communityItem/:id', name: 'communityItem', component: CommunityItem, meta: { index: 2, keepAlive: true, spe: true } },
  { path: '/', name: 'home', component: Home, meta: { index: 1, keepAlive: true, needRefresh: false, dynamicScroll: 0, spe: true }, },
  { path: '/my', name: 'my', component: My, meta: { index: 1, keepAlive: true, spe: true }, },
  { path: '/messageNotice', name: 'messageNotice', component: MessageNotice, meta: { index: 1, keepAlive: true, spe: true }, },
  { path: '/community', name: 'community', component: Community, meta: { index: 1, keepAlive: true, spe: true }, },
  { path: '/setup', name: 'setup', component: Setup, meta: { index:2 }, },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})
router.beforeEach((to) => {
  if (to.path === "/" || to.path === '/community' || to.path === '/my' || to.path === '/messageNotice') {
    sessionStorage["quit"] = 1;
  } else {
    sessionStorage["quit"] = 0;
  }
});
export default router
