<template>
    <div class="draftList wrapper">
        <van-loading v-if="loading" vertical>加载中...</van-loading>
        <van-empty description="暂无草稿" v-else-if="!list.length" />
        <div v-else>
            <van-swipe-cell v-for="el in list" :key="el.createdAt">
                <div class="item" style="margin-top:5px;background:white" @click="draftEdit(el.cover,el.createdAt)">
                    <div class="top p6">
                        <span class="type ">{{ el.cover ? '投稿' : '帖子' }}</span>
                        <span class="time p6g">上次编辑 {{ before(+el.updatedAt) }}</span>
                    </div>
                    <div class="mid p5">
                        <div class="title">{{ el.title === '' ? '(无标题)' : el.title }}</div>
                    </div>
                    <div class="bottom p5g">
                        <div class="content van-multi-ellipsis--l3">{{ el.content }}</div>
                    </div>
                </div>
                <template #right>
                    <van-button square type="danger" text="删除" style="height:100%" @click="delDraft(el.createdAt)" />
                </template>
            </van-swipe-cell>
            <van-collapse v-model="activeNames" style="margin-top:.5rem">
                <van-collapse-item title="" icon="question" :is-link="false">
                    <template #title>
                        <u>我有问题?</u>
                    </template>
                    <p>问:如何删除草稿?</p>
                    <ol>
                        <li>答:右滑删除</li>
                    </ol>
                </van-collapse-item>
            </van-collapse>
        </div>
    </div>
</template>
<script>
import { Dialog, Toast } from 'vant'
import qs from 'qs'
export default {
    name: 'DraftList',
    inject: ['before'],
    props: {

    },
    data() {
        return {
            loading: false,
            list: [],
            activeNames: [],
        }
    },
    methods: {
        draftEdit(cover, createdAt) {
            cover?
                this.$router.push({ path: '/submit', query: { createdAt } }) :
                this.$router.push({ path: '/post', query: { createdAt } })
        },
        delDraft(createdAt) {
            Dialog.confirm({
                title: '确认删除?',
            })
                .then(async () => {
                    Toast.loading({
                        message: '删除中',
                        overlay: true,
                        duration:0,
                    })
                    for (let index in this.list) {
                        if (this.list[index].createdAt == createdAt) {
                            this.list.splice(index, 1);
                            break;
                        }
                    }
                    const params={list:this.list,userId:this.$store.state.user.userId}
                    await this.$axios.post('draftUpdate', qs.stringify(params)).then(res => {
                        console.log(res);
                        Toast.clear();
                        Toast.success('删除成功!')
                    })
                })
                .catch(() => {
                    // on cancel
                });
        }
    },
    mounted() {
        const params = {
            userId: this.$store.state.user.userId,
        }
        this.$axios.get('draftListGet', { params }).then(rs => {
            console.log(rs);
            this.list = rs;
        })
    },
}
</script>

<style scoped lang="less">
.item {
    padding: 10px;
    display: flex;
    flex-flow: column;

    .top {
        display: flex;
        justify-content: space-between;
    }

    .mid {
        padding: 10px 0;
    }

}
</style>