<template>
  <div class="feedback wrapper pt">
    <van-nav-bar title="问题广场" left-arrow @click-left="onClickLeft" fixed />
    <div class="bulk" id="bulk">
      <div class="opinion" style="padding:.3rem 0 ">
        <p class="p5"><i>期待您的反馈</i></p>
        <p class="p5"><i>Offer your great opinion</i></p>
      </div>
      <van-cell-group>
        <template #title>
          <div style="display:flex;justify-content:space-between">
            <span>常见问题</span>
            <span @click="goAllQ('FAQ')">全部</span>
          </div>
        </template>
        <van-cell is-link v-for="el in hotList" :key="el.id" center :to="'/questionDetail/'+el.id">
          <template #title>
            【热点】{{el.question}}
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
        <template #title>
          <div style="display:flex;justify-content:space-between">
            <span>最新问题</span>
            <span @click="goAllQ('NAQ')">全部</span>
          </div>
        </template>
        <van-cell is-link v-for="el in newList" :key="el.id" center :to="'/questionDetail/'+el.id">
          <template #title>
            【最新】{{el.question}}
          </template>
        </van-cell>
      </van-cell-group>
      <van-loading size="24px" vertical color="#0094ff" v-show="loading" style="margin-top: 1rem;">加载中...</van-loading>
      <van-skeleton title avatar :row="4" v-show="loading" v-for="el in 2 " :key="el" />
      <div class="bug p4" v-show="!loading">
        <div class="bugItem" v-for="(el,index) in bugList" :key="el.id">
          <p class="greyBackground" v-show="index!==0"></p>
          <div class="content">
            <p class="p2b" v-show="index===0">用户说</p>
            <div class="top">
              <div class="avatar">
                <van-image width="1rem" height="1rem" fit="cover" radius="5px"
                  :src="avatarGet(el.avatar)" />
              </div>
              <div class="nickname p3b ">{{el.nickname}}</div>
            </div>
            <div class="mid">
              <div class="text p3 van-multi-ellipsis--l3" @click="push('bugDetail/'+el.id)">
                {{el.text}}
              </div>
            </div>
            <div class="imgBox" v-if="el.imgs.length">
              <div class="square" v-for="(item,index) in el.imgs" :key="item">
                <van-image width="100%" height="100%" fit="cover" radius="5px" @click.stop='imgPreview(el.imgs,index)'
                  lazy-load :src="this.$store.state.URL+'upload/bugImgs/'+item" />
              </div>
            </div>
            <div class="bottom">
              <div class="time">{{before(el.date)}}</div>
              <div class="optionBox">
                <van-button size="mini" color="transparent" disabled>
                  <template #icon>
                    <van-icon name="comment-o" color="#000" size="0.4rem"><span
                        class="icon-span">{{el.commentLength}}</span></van-icon>
                  </template>
                </van-button>
                <!-- <van-button size="mini" color="transparent">
                  <template #icon>
                    <van-icon name="good-job" color="#aaa" size="0.4rem"><span class="icon-span">2</span></van-icon>
                  </template>
                </van-button> -->
                <van-button size="mini" color="transparent" @click="actionShow()">
                  <template #icon>
                    <van-icon name="ellipsis" color="#aaa" size="0.5rem"></van-icon>
                  </template>
                </van-button>
              </div>
            </div>
            <div class="commentBox" v-if="el.comment.length">
              <div class="comment">
                <p class="commentItem" v-for="item in el.comment" :key="item">
                  <span><b>{{item.nickname}}</b></span>
                  <span v-if="item.targetName">&nbsp;回复&nbsp;</span>
                  <span v-if="item.targetName"><b>{{item.targetName}}</b></span>
                  <span><b>&nbsp;:</b></span>
                  <span>{{item.message}}
                  </span>
                </p>
                <span style="color:blue" @click="push('bugDetail/'+el.id)" v-show="el.commentLength>3">查看所有评论&gt;&gt;</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="btnBox" :style="{right:scrollTop>200?'.5rem':'3.5rem',width:scrollTop>200?'1rem':'3rem' }">
      <van-button icon="edit" color="black" round block @click="popup" style="height:1rem">{{text}}
      </van-button>
    </div>
    <van-popup v-model:show="show" position="right" :style="{ width: '100%' ,height:'100%'}" class="popup">
      <van-nav-bar title="问题反馈" @click-left="popDown">
        <template #left>
          <van-icon name="cross" size="18" />
        </template>
      </van-nav-bar>
      <van-tabs v-model:active="active" animated>
        <van-tab title="提缺陷/建议">
          <van-form @submit="onSubmit">
            <van-cell-group inset class="Q1">
              <template #title>
                <p class="p4b black">问题描述</p>
              </template>
              <van-field v-model="message" rows="3" autosize type="textarea" maxlength="100"
                placeholder='请尽量描述清晰，以便您对于产品的建议或所提缺陷能更清晰地反馈至开发团队。(如果需要向管理员提问，请移步至上方的"我想问/想要")' show-word-limit
                style="background-color: #eee;" :rules="[{ required: true, message: '请填写问题描述' }]" />
            </van-cell-group>
            <van-cell-group inset class="Q1">
              <template #title>
                <p class="p4b black">问题截图(jpg/png/gif)</p>
              </template>
              <van-uploader v-model="fileList" multiple :max-count="3" :after-read="afterRead" :before-read="beforeRead"
                :before-delete="beforeDelete" />
            </van-cell-group>
            <van-cell-group inset class="Q1">
              <template #title>
                <p class="p4b black">联系方式(方便我们更好地跟踪问题)</p>
              </template>
              <van-field label="QQ" v-model="qq" style="  background-color: #eee;" placeholder="请输入您的QQ号(可选)" />
              <van-field label="邮箱" v-model="email" style="  background-color: #eee;" placeholder="请输入您的邮箱(可选)" />
            </van-cell-group>
            <van-button round block color="black" native-type="submit" style="margin-top:.5rem">
              提交反馈
            </van-button>
          </van-form>
        </van-tab>
        <van-tab title="我想问/想要">
          <van-form @submit="submitQue">
            <van-cell-group inset>
              <template #title>
                <p class="p4b black">我的问题</p>
              </template>
              <van-field placeholder="请输入您的问题或想要的功能(至多16字)" v-model="myQue"
                :rules="[{ required: true, message: '请填写您的问题' }]" style="  background-color: #eee;" clearable
                maxlength="16" show-word-limit />
            </van-cell-group>
            <van-cell-group inset class="Q1">
              <template #title>
                <p class="p4b black">问题详情(可选)</p>
              </template>
              <van-field v-model="queDetail" rows="3" autosize type="textarea" maxlength="100"
                placeholder="I had been waiting for so long ,let's talk about it" show-word-limit
                style="background-color: #eee;" />
            </van-cell-group>
            <van-cell-group inset class="Q1" v-if="this.$store.state.user.userId===1000">
              <template #title>
                <p class="p4b black">是否常见</p>
              </template>
              <van-switch v-model="isHot" size="20" />
            </van-cell-group>
            <van-button round block color="black" native-type="submit" style="margin-top:.5rem">
              提交问题
            </van-button>
          </van-form>
          <van-collapse v-model="activeNames" style="margin-top:.5rem">
            <van-collapse-item title="" icon="question" :is-link="false">
              <template #title>
                <u>我可以问什么?</u>
              </template>
              <p>问你想问的一切，包括但不限于以下内容:</p>
              <ol>
                <li>1.。。。是什么?</li>
                <li>2.为什么。。。</li>
                <li>3.。。。怎么样?</li>
              </ol>
            </van-collapse-item>
          </van-collapse>
        </van-tab>
      </van-tabs>
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'vant';
import qs from 'qs'
import { ImagePreview } from 'vant';
import dayjs from '@/assets/util/public'

export default {
  name: 'ContactView',
  inject: ['actionShow','avatarGet'],
  data() {
    return {
      activeNames: [],
      show: false,//弹出层,
      text: '问题反馈',
      active: 0,
      message: '',
      fileList: [],
      //我想问
      myQue: '',
      queDetail: '',
      scrollTop: 0,
      bugImgs: [],
      qq: '',
      email: '',
      bugList: [],
      loading: true,
      isHot: false,
      hotList: [],
      newList: [],
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
    popup() {
      this.show = true;
    },
    popDown() {
      this.show = false
    },
    reset() {//重置数据
      this.show = false;
      this.message = '';
      this.qq = '';
      this.email = '';
      this.fileList.length = 0;
      this.myQue = '';
      this.bugImgs.length = '';
    },
    //提问重置
    questionReset() {
      this.queDetail = '';
      this.myQue = '';
      this.show = false;
      this.isHot = false;
    },
    goAllQ(type) {
      if (type === 'FAQ')
        this.$router.push('questionList/FAQ')
      else
        this.$router.push('questionList/NAQ')
    },
    async onSubmit() {
      if (this.message.trim().length === 0) {
        Toast.fail('内容无意义！')
        return
      }
      let toast1 = Toast.loading({
        forbidClick: true,
        message: '提交中',
        overlay: true,
        duration: 0,
      });
      let imgsName = []//上传多个图片
      if (this.bugImgs.length !== 0) {
        const data = new FormData()
        this.bugImgs.map(el => {
          data.append('bugImgs', el);
        })
        await this.$axios.post('bugImgsUpload', data).then(rs => {
          console.log('返回的图片名为:', rs);
          imgsName = rs
        })
      }
      let params = {
        userId: this.$store.state.user.userId,
        text: this.message,
        imgs: imgsName,
      }
      this.$axios.post('bugSubmit', qs.stringify(params)).then(rs => {
        this.bugList = [rs, ...this.bugList]
        toast1.clear();
        Toast.success({
          message: '反馈成功！',
          overlay: true,
          onClose: this.reset,
        })
      })
    },
    submitQue() {
      if (this.myQue.trim().length === 0) {
        Toast.fail('内容无意义！')
        return
      }
      if (this.queDetail.trim().length === 0)
        this.queDetail = this.queDetail.trim()
      let toast1 = Toast.loading({
        forbidClick: true,
        message: '提交中',
        overlay: true,
        duration: 0,
      });
      let params = {
        question: this.myQue,
        questionMaker: this.$store.state.user.userId,
        desc: this.queDetail,
        isHot:this.isHot,
      }
      this.$axios.post('addQuestion', qs.stringify(params)).then(rs => {
        console.log(rs);
        toast1.clear();
        Toast.success({
          message: '提问成功！',
          overlay: true,
          onClose: this.questionReset,
        })
      })
    },
    afterRead(file) {
      this.bugImgs.push(file.file)
    },
    beforeDelete(file) {
      this.bugImgs.splice(this.bugImgs.indexOf(file), 1)
      return true;
    },
    beforeRead(file) {
      let flag = true;
      if (this.$store.state.imageType.indexOf( file.type)!==-1) {
        this.bugImgs.forEach(el => {
          if (el.name === file.name)
            flag = false;
        })
        if (flag)
          return true;
        else {
          Toast.fail('重复图片！')
          return false
        }
      }
      else {
        Toast('格式不支持');
        return false;
      }
    },
    scrollHandler() {
      this.scrollTop = document.querySelector('#bulk').scrollTop;
      this.$nextTick(() => {
        if (this.scrollTop > 200)
          this.text = '';
        else {
          this.text = '问题反馈';
        }
      })
    },
    imgPreview(arr, index) {//图片预览
      arr = arr.map(el => {
        return this.$store.state.URL + 'upload/bugImgs/' + el;
      })
      ImagePreview({
        images: arr,
        startPosition: index,
      });
    },
    before(date) {//时间显示
      return dayjs(date).format('YYYY[年]MM[月]DD[日] HH:mm')
    },
    push(path) {//跳转
      this.$router.push(path);
    },
    async dataGet() {
      await this.$axios.get('questionListGet', { params:{type:'FAQ',home:'home'} }).then(rs => {
        this.hotList = rs;
      })
      await this.$axios.get('questionListGet', { params: { type: 'NAQ', home: 'home' } }).then(rs => {
        this.newList = rs;
      })
      this.bugListGet();
    },
    async bugListGet() {
      this.$axios.get('bugListGet').then(rs => {
        this.bugList = rs;
        this.$nextTick(() => {
          setTimeout(() => {
            this.loading = false;
          }, 100)
        })
      })
    },
  },
  mounted() {
    document.getElementById('bulk').addEventListener('scroll', this.scrollHandler)
    this.dataGet();
  },
  beforeUnmount() {
    document.getElementById('bulk').removeEventListener('scroll', this.scrollHandler);
  }
}
</script>

<style scoped lang="less">
.feedback {
  background: #f8f8f8;
}

.popup {
  .black {
    color: black;
  }
}

.btnBox {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 10vh;
  transition: width, right .3s;
}

.opinion {
  display: flex;
  justify-content: center;
  flex-flow: column;
  align-items: center;
}

.bug {
  margin-top: 20px;
  background: rgb(255, 255, 255);

  .bugItem {
    display: flex;
    flex-direction: column;

    .p2b {
      padding-bottom: .5rem;
    }

    .greyBackground {
      background: #f8f8f8;
      height: 20px;
    }

    .content {
      padding: .5rem;

      >div {
        padding: 2px 0;

      }

      .top {
        display: flex;
        align-items: center;

        div.nickname {
          padding-left: .3rem;
        }
      }


      .mid {
        padding-left: 1.3rem;
      }

      .imgBox {
        padding-left: 1.3rem;
        display: flex;

        .square {
          width: 2rem;
          height: 2rem;
          margin-right: 2px;
        }
      }

      .bottom {
        padding-left: 1.3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .icon-span {
          padding-left: 5px;
          // font-size: 0.5rem;
        }
      }

      .commentBox {
        padding-left: 1rem;

        .comment {
          border-radius: 5px;
          background: #f6f6f6;
          padding: .3rem;

          .commentItem {
            padding: 2px 0;
          }
        }

      }
    }

  }
}
</style>